/* eslint-disable require-jsdoc */
import log from '../utils/log/logger';
import * as env from '../utils/environment';

const getDisplayMedia = async function(options) {
  if (!navigator.mediaDevices) {
    log.error('navigator.mediaDevices is undefined');
    return;
  }
  let audioStream = null;
  if (options.audio) {
    const constraints = buildConstraints(options);
    log.info('getUserMedia with constraints: ' + JSON.stringify(constraints));
    audioStream = await navigator.mediaDevices.getUserMedia(constraints);
  }
  const screenConstraints = buildScreenConstraints(options);
  log.info('getDisplayMedia with contraints: ' + JSON.stringify(screenConstraints));
  let captureStream = await navigator.mediaDevices.getDisplayMedia(screenConstraints);
  if (audioStream) {
    captureStream.addTrack(audioStream.getAudioTracks()[0]);
  }
  return captureStream;
};

function buildConstraints(options) {
  let audioConstraints = {};
  if (typeof options.microphoneId !== 'undefined') {
    audioConstraints = {
      deviceId: { exact: options.microphoneId },
      sampleRate: options.sampleRate,
      channelCount: options.channelCount
    };
  } else {
    audioConstraints = { sampleRate: options.sampleRate, channelCount: options.channelCount };
  }
  return { audio: audioConstraints, video: false };
}

function buildScreenConstraints(options) {
  let videoConstraints = {
    width: options.width,
    height: options.height,
    frameRate: options.frameRate
  };
  if (typeof options.screenSource !== 'undefined') {
    videoConstraints.displaySurface = options.screenSource;
  }
  return { video: videoConstraints };
}

export default getDisplayMedia;
