// eventId used to show the events on avmonitor sender/receiver details page
const EventId = {
  PUBLISH_VIDEO: 0x8000, // 打开视频
  PUBLISH_AUDIO: 0x8001, // 打开音频
  UNPUBLISH_VIDEO: 0x8002, // 关闭视频
  UNPUBLISH_AUDIO: 0x8003, // 关闭音频
  MUTE_AUDIO: 0x8004, // 关闭音频 (仍发送静音包)
  MUTE_VIDEO: 0x8005, // 关闭视频 (仍发送黑屏包)
  UNMUTE_AUDIO: 0x8006, // 打开音频
  UNMUTE_VIDEO: 0x8007, // 打开视频
  SUBSCRIBE_VIDEO: 0x8008, // 订阅视频
  SUBSCRIBE_AUDIO: 0x8009, // 订阅音频
  UNSUBSCRIBE_VIDEO: 0x800a, // 取消订阅视频
  UNSUBSCRIBE_AUDIO: 0x800b, // 取消订阅音频
  SWITCH_CAMERA: 0x800c, // 切换摄像头
  SWITCH_MICROPHONE: 0x800d, // 切换麦克风
  REPLACE_VIDEO: 0x800e, // 更新视频
  REPLACE_AUDIO: 0x800f, // 更新音频
  MUTE_REMOTE_VIDEO: 0x8010, // 关闭远端视频
  MUTE_REMOTE_AUDIO: 0x8011, // 关闭远端音频
  UNMUTE_REMOTE_VIDEO: 0x8012, // 打开远端视频
  UNMUTE_REMOTE_AUDIO: 0x8013, // 打开远端音频
  JOIN: 0x8014, // 进房
  LEAVE: 0x8015, // 退房
  SIGNAL_DISCONNECTED: 0x8016, // 信令通道断开
  SIGNAL_CONNECTED: 0x8017, // 信令通道连接成功
  TRANSPORT_DISCONNECTED: 0x8018, // 媒体传输通道断开
  TRANSPORT_CONNECTED: 0x8019 // 媒体传输通道连接成功
};

const eventHub = new Map();

// add events to the hub
const addEvent = function(userId, event) {
  let list = eventHub.get(userId);
  if (!list) {
    eventHub.set(userId, []);
    list = eventHub.get(userId);
  }
  list.push(event);
};

// get the events and clear them in the hub
const getEvents = function(userId) {
  let list = eventHub.get(userId);
  if (!list) {
    list = [];
  } else {
    eventHub.delete(userId);
  }
  return list;
};

export { addEvent, getEvents, EventId };
