import generateUUID from '../utils/uuid';
import RtcError from '../common/rtc-error';
import ErrorCode from '../common/error-code';
import AudioPlayer from './audio-player';
import VideoPlayer from './video-player';
import EventEmitter from 'eventemitter3';
import { StreamEvent, PlayerEvent } from './event';
import IDLogger from '../utils/log/id-logger';
import { addEvent, EventId } from '../utils/event-hub';

/**
 * Stream 音视频流，一个 Stream 中最多只能同时包含一个音频 track 和一个视频 track。<br>
 * Stream 类是本地流 LocalStream 和远端流 RemoteStream 的基类，它包含本地流和远端流通用的方法。
 *
 * @class Stream
 */
class Stream {
  constructor(options) {
    this.userId_ = options.userId;
    this.isRemote_ = options.isRemote;
    this.type_ = options.type;
    this.log_ = new IDLogger({
      id: 's|' + this.userId_,
      direction: this.isRemote_ ? 'remote' : 'local',
      type: this.isRemote_ ? this.type_ : ''
    });
    this.mirror_ = false;
    if (!this.isRemote_) {
      this.mirror_ = true;
    }
    if (typeof options.mirror !== 'undefined') {
      this.mirror_ = options.mirror;
    }

    this.client_ = null;
    if (typeof options.client !== 'undefined') {
      this.client_ = options.client;
    }

    // the MediaStream associated with this Stream
    /* @type {MediaStream} */
    this.mediaStream_ = null;
    // container for appending <audio>/<video> elements
    this.div_ = null;
    this.isPlaying_ = false;
    /*
     * the Connection (RTCPeerConnection) to which this Stream attached
     * for local stream, it will be attached to Connection when it's published.
     * for remote stream, there's always a Connection attached when it's created.
     */
    this.connection_ = null;

    this.hasAudio_ = false;
    this.hasVideo_ = false;
    this.audioPlayer_ = null;
    this.videoPlayer_ = null;
    this.muted_ = false;
    this.objectFit_ = 'cover';

    this.id_ = generateUUID();
    this.audioOutputDeviceId_ = 0;
    this.audioVolume_ = 1.0;
    this.emitter_ = new EventEmitter();
  }

  getType() {
    return this.type_;
  }

  getIDLogger() {
    return this.log_;
  }

  /**
   *
   * @package
   * @param {Connection} connection
   * @memberof Stream
   */
  setConnection(connection) {
    if (this.connection_ !== connection) {
      this.connection_ = connection;
    }
  }

  /**
   *
   * @package
   * @returns {Connection}
   * @memberof BaseStream
   */
  getConnection() {
    return this.connection_;
  }

  /**
   * 播放该音视频流<br>
   * 该方法会自动创建 \<audio\> 和 \<video\> 标签并在指定的标签上播放音频和视频，同时该标签会被添加到页面中名为 `elementId` 的 div 容器。<br>
   * 换句说，Stream 内部会自动创建相应的音频播放器和视频播放器来播放相应的音频和视频。
   *
   * **NOTE**
   *
   * - 由于浏览器[自动播放策略](https://developers.google.com/web/updates/2017/09/autoplay-policy-changes)的影响，调用该接口可能会返回 `PLAY_NOT_ALLOWED` 错误，
   * 此时需要引导用户通过手势操作调用 {@link Stream#resume resume()} 恢复音视频播放。
   * @example
   * stream.play('remote').then(() => {
   *   // autoplay success
   * }).catch((e) => {
   *   const errorCode = e.getCode();
   *   if (errorCode === 0x4043) {
   *     // PLAY_NOT_ALLOWED,引导用户手势操作恢复音视频播放
   *     // stream.resume()
   *   }
   * });
   *
   * @param {string|HTMLDivElement} elementId HTML \<div\> 标签 ID 或者 HTMLDivElement 对象，该方法内部自动创建的音视频标签将被添加到该容器中。
   * @param {object=} options 播放选项
   * @param {string} options.objectFit 视频画面显示模式，参考 {@link https://developer.mozilla.org/zh-CN/docs/Web/CSS/object-fit| CSS object-fit} 属性
   * - 'contain' 优先保证视频内容全部显示。视频尺寸等比缩放，直至视频窗口的一边与视窗边框对齐。如果视频尺寸与显示视窗尺寸不一致，在保持长宽比的前提下，将视频进行缩放后填满视窗，缩放后的视频四周会有一圈黑边。
   * - 'cover' 优先保证视窗被填满。视频尺寸等比缩放，直至整个视窗被视频填满。如果视频长宽与显示窗口不同，则视频流会按照显示视窗的比例进行周边裁剪或图像拉伸后填满视窗。
   * <br>
   * <br>
   * 对于本地流，播放视频流默认使用 cover 模式，屏幕共享默认使用 contain 模式。
   * @param {boolean} options.muted 是否需要 mute 声音，对于本地流通常需要 mute 声音以防止播放从麦克风采集回来的声音。
   * @throws {RtcError}
   * @returns {Promise}
   * @memberof Stream
   */
  async play(elementId, options) {
    if (this.isPlaying_) {
      throw new RtcError({
        code: ErrorCode.INVALID_OPERATION,
        message: 'duplicated play() call observed, please stop() firstly'
      });
    }

    this.isPlaying_ = true;

    this.log_.info(`stream start to play with options: ${JSON.stringify(options)}`);

    // container
    const div = document.createElement('div');
    div.setAttribute('id', `player_${this.id_}`);
    div.setAttribute(
      'style',
      'width: 100%; height: 100%; position: relative; background-color: black; overflow: hidden;'
    );

    let container = elementId;
    if (typeof elementId !== 'object') {
      container = document.getElementById(elementId);
    }
    container.appendChild(div);
    this.div_ = div;

    if (!this.isRemote_) {
      // ensure audio is muted for local stream to avoid playing back the sound from microphone
      this.muted_ = true;
    }
    if (options && typeof options.muted !== 'undefined') {
      this.muted_ = options.muted;
    }

    // auxiliary stream is usually a screen share, we use 'contain' object-fit for that by default.
    if (this.isRemote_ && this.getType() === 'auxiliary') {
      this.objectFit_ = 'contain';
    }

    if (options && typeof options.objectFit !== 'undefined') {
      this.objectFit_ = options.objectFit;
    }

    if (this.hasVideo_) {
      await this.playVideo();
    }

    if (this.hasAudio_) {
      await this.playAudio();
    }
  }

  async playAudio() {
    const audioTrack = this.getAudioTrack();
    if (!this.audioPlayer_ && audioTrack) {
      this.log_.info('stream - create AudioPlayer and play');
      this.audioPlayer_ = new AudioPlayer({
        stream: this,
        track: audioTrack,
        div: this.div_,
        muted: this.muted_,
        outputDeviceId: this.audioOutputDeviceId_,
        volume: this.audioVolume_
      });
      this.audioPlayer_.on(PlayerEvent.PLAYER_STATE_CHANGED, event => {
        this.emitter_.emit(StreamEvent.PLAYER_STATE_CHANGED, {
          type: 'audio',
          state: event.state,
          reason: event.reason
        });
      });
      await this.audioPlayer_.play();
    }
  }

  async playVideo() {
    const videoTrack = this.getVideoTrack();
    if (!this.videoPlayer_ && videoTrack) {
      this.log_.info('stream - create VideoPlayer and play');
      this.videoPlayer_ = new VideoPlayer({
        stream: this,
        track: videoTrack,
        div: this.div_,
        muted: this.muted_,
        objectFit: this.objectFit_,
        mirror: this.mirror_
      });
      this.videoPlayer_.on(PlayerEvent.PLAYER_STATE_CHANGED, event => {
        this.emitter_.emit(StreamEvent.PLAYER_STATE_CHANGED, {
          type: 'video',
          state: event.state,
          reason: event.reason
        });
      });
      await this.videoPlayer_.play();
    }
  }

  stopAudio() {
    if (this.audioPlayer_) {
      this.log_.info('stream - stop AudioPlayer');
      this.audioPlayer_.stop();
      this.audioPlayer_ = null;
    }
  }

  stopVideo() {
    if (this.videoPlayer_) {
      this.log_.info('stream - stop VideoPlayer');
      this.videoPlayer_.stop();
      this.videoPlayer_ = null;
    }
  }

  restartAudio() {
    if (!this.isPlaying_) return;
    this.stopAudio();
    this.playAudio();
  }

  restartVideo() {
    if (!this.isPlaying_) return;
    this.stopVideo();
    this.playVideo();
  }

  /**
   * 停止播放音视频流
   * <br>
   * <br>
   * 该方法还会将由 {@link Stream#play play()} 创建的音视频标签从 HTML 页面中删除。
   */
  stop() {
    if (!this.isPlaying_) return;
    this.isPlaying_ = false;
    this.stopAudio();
    this.stopVideo();
    this.div_.parentNode.removeChild(this.div_);
  }

  /**
   * 恢复播放音视频
   * <br>
   * **NOTE**
   * - 在某些版本浏览器上移动传入 {@link Stream#play play()} 的 div 容器可能会导致音视频播放器进入 ‘PAUSED’ 状态，此时
   * 需要调用该接口恢复播放。
   * - 由于浏览器自动播放策略的限制，在 {@link Stream#play play()} 返回 `PLAY_NOT_ALLOWED` 错误后需要引导用户通过手势
   * 调用该接口恢复播放。
   * @memberof Stream
   * @throws {RtcError}
   * @returns {Promise}
   * @example
   * stream.on('player-state-changed', event => {
   *   if (event.state === 'PAUSED') {
   *     // resume audio/video playback
   *     stream.resume();
   *   }
   * });
   */
  async resume() {
    if (!this.isPlaying_) return;
    this.log_.info('stream - resume');

    // audio should come first before video otherwise there will be NotAllowedError exception
    // in WebChat browser on iOS.
    if (this.audioPlayer_) {
      await this.audioPlayer_.resume();
    }

    if (this.videoPlayer_) {
      await this.videoPlayer_.resume();
    }
  }

  /**
   * 关闭音视频流<br>
   * <br>
   * 对于本地流，该方法会关闭摄像头并释放摄像头和麦克风访问权限。
   */
  close() {
    // sanity check
    if (this.isPlaying_) {
      this.stop();
    }
    if (this.mediaStream_) {
      this.mediaStream_.preventEvent = 1;
      this.mediaStream_.getTracks().forEach(track => {
        track.stop();
      });
      this.mediaStream_ = null;
    }
  }

  /**
   * 禁用音频轨道<br>
   * <br>
   * - 对于本地流，调用该方法会停止发送音频，远端会触发 `Client.on('mute-audio')` 事件。
   * - 对于远端流，调用该方法会停止播放音频，但是仍然接收音频数据。
   *
   * @returns {boolean}
   * - true 禁用音频轨道成功。
   * - false 禁用音频轨道失败，因为没有音频轨道。
   * @memberof Stream
   */
  muteAudio() {
    this.addRemoteEvent(true, 'audio');
    return this.doEnableTrack('audio', false);
  }

  /**
   * 禁用视频轨道<br>
   * <br>
   * - 对于本地流，调用该方法会停止发送视频，远端会触发 `Client.on('mute-video')` 事件。<br>
   * 如果视频是从摄像头采集，此时摄像头灯仍然是亮着的。若想完全禁用视频轨道(即关闭摄像头)，<br>
   * 可以使用 {@link LocalStream#removeTrack removeTrack()} 删除视频轨道然后调用
   * {@link https://developer.mozilla.org/en-US/docs/Web/API/MediaStreamTrack/stop|MediaStreamTrack.stop()}
   * 关闭视频轨道（关闭摄像头）。
   * - 对于远端流，调用该方法会停止播放视频，但是仍然接收视频数据.
   *
   * @returns {boolean}
   * - true 禁用视频轨道成功
   * - false 禁用视频轨道失败，因为没有视频轨道。
   * @memberof Stream
   */
  muteVideo() {
    this.addRemoteEvent(true, 'video');
    return this.doEnableTrack('video', false);
  }

  /**
   * 启用音频轨道<br>
   * <br>
   * 对于本地流，调用该方法会触发远端 `Client.on('unmute-audio')` 事件。<br>
   * <br>
   * 音频轨道默认是开启的，若你调用 {@link Stream#muteAudio muteAudio()} 后可用该方法重新启用音频。
   *
   * @returns {boolean}
   * - true 启用音频轨道成功。
   * - false 没有音频轨道，启用失败。
   * @memberof Stream
   */
  unmuteAudio() {
    this.addRemoteEvent(false, 'audio');
    return this.doEnableTrack('audio', true);
  }

  /**
   * 启用视频轨道<br>
   * <br>
   * 对于本地流，调用该方法会触发远端 `Client.on('unmute-video')` 事件。<br>
   * <br>
   * 视频轨道默认是开启的，若你调用 {@link Stream#muteVideo muteVideo()} 后可用该方法重新启用视频。
   *
   * @return {boolean}
   * - true 启用视频轨道成功。
   * - false 没有视频轨道，启用失败。
   * @memberof Stream
   */
  unmuteVideo() {
    this.addRemoteEvent(false, 'video');
    return this.doEnableTrack('video', true);
  }

  addRemoteEvent(mute, kind) {
    if (this.isRemote_ && this.client_) {
      const uid = this.client_.getUserId();
      let eventId;
      let eventDesc = `${mute ? 'mute' : 'unmute'} remote ${kind}`;
      if (kind === 'audio') {
        eventId = mute ? EventId.MUTE_REMOTE_AUDIO : EventId.UNMUTE_REMOTE_AUDIO;
      } else {
        eventId = mute ? EventId.MUTE_REMOTE_VIDEO : EventId.UNMUTE_REMOTE_VIDEO;
      }

      addEvent(uid, {
        eventId,
        eventDesc,
        timestamp: new Date().getTime(),
        // the stream owner - the client's IDs
        userId: uid,
        tinyId: this.client_.getTinyId(),
        // remote peer's IDs
        remoteUserId: this.userId_,
        remoteTinyId: this.connection_.getTinyId()
      });
    }
  }

  doEnableTrack(kind, enable) {
    let has = false;
    if (kind === 'audio') {
      this.mediaStream_.getAudioTracks().forEach(track => {
        has = true;
        track.enabled = enable;
      });
    } else {
      this.mediaStream_.getVideoTracks().forEach(track => {
        has = true;
        track.enabled = enable;
      });
    }
    return has;
  }

  /**
   * 获取 Stream 唯一标识ID
   *
   * @returns {string} Id 流唯一标识
   * @memberof Stream
   */
  getId() {
    return this.id_;
  }

  /**
   * 获取该流所属的用户ID
   *
   * @returns {string} userId 用户ID
   * @memberof Stream
   */
  getUserId() {
    return this.userId_;
  }

  isPlaying() {
    return this.isPlaying_;
  }

  /**
   * 设置声音输出设备
   *
   * @param {string} deviceId 设备标识，通过 {@link TRTC.getSpeakers getSpeakers()} 获取。
   * @memberof Stream
   */
  async setAudioOutput(deviceId) {
    this.audioOutputDeviceId_ = deviceId;
    if (this.audioPlayer_) {
      await this.audioPlayer_.setSinkId(deviceId);
    }
  }

  /**
   * 设置播放音量大小<br>
   *
   * @param {double} volume 音量大小，取值在 0.0 (静音) 到 1.0 (最大音量) 之间。
   * @memberof Stream
   */
  setAudioVolume(volume) {
    this.audioVolume_ = volume;
    this.log_.info(`setAudioVolume to ${volume}`);
    if (this.audioPlayer_) {
      this.audioPlayer_.setVolume(volume);
    }
  }

  /**
   * 获取当前音量大小<br>
   * 只有当本地流或远端流中有音频数据才有效。
   *
   * @returns {number} audioLevel 音量大小
   * - 返回值在(0.0, 1.0)之间，通常认为值大于0.1为用户正在说话。
   * @example
   * setInterval(() => {
   *   const level = stream.getAudioLevel();
   *   if (level >= 0.1) {
   *     console.log(`user ${stream.getUserId()} is speaking`);
   *   }
   * }, 200);
   */
  getAudioLevel() {
    let vol = 0.0;
    if (this.audioPlayer_) {
      vol = this.audioPlayer_.getAudioLevel();
    }
    return vol;
  }

  /**
   * 是否包含音频轨道
   *
   * @returns {boolean}
   * @memberof Stream
   */
  hasAudio() {
    return this.hasAudio_;
  }

  /**
   * 是否包含视频轨道
   *
   * @returns {boolean}
   * @memberof Stream
   */
  hasVideo() {
    return this.hasVideo_;
  }

  /**
   * 获取音频轨道
   *
   * @returns {MediaStreamTrack?} 音频轨道
   * @memberof Stream
   */
  getAudioTrack() {
    let track = null;
    if (this.mediaStream_) {
      const audioTracks = this.mediaStream_.getAudioTracks();
      if (audioTracks.length > 0) {
        track = audioTracks[0];
      }
    }
    return track;
  }

  /**
   * 获取视频轨道
   *
   * @returns {MediaStreamTrack?} 视频轨道
   * @memberof Stream
   */
  getVideoTrack() {
    let track = null;
    if (this.mediaStream_) {
      const videoTracks = this.mediaStream_.getVideoTracks();
      if (videoTracks.length > 0) {
        track = videoTracks[0];
      }
    }
    return track;
  }

  /**
   * 获取当前视频帧
   * <br>
   * **NOTE**
   * - 该方法需要在 {@link Stream#play play()} 后调用，并且 Stream 中有视频流才有效
   *
   * @returns {String?} 'image/png' 类型的 {@link https://developer.mozilla.org/en-US/docs/Web/HTTP/Basics_of_HTTP/Data_URIs dataURL}
   * @memberof Stream
   * @example
   * // 截取当前视频帧
   * const frame = stream.getVideoFrame();
   * if (frame) {
   *   const img = document.createElement('img');
   *   img.src = frame;
   * }
   */
  getVideoFrame() {
    if (!this.videoPlayer_) return null;
    return this.videoPlayer_.getVideoFrame();
  }

  // package

  getMediaStream() {
    return this.mediaStream_;
  }

  setMediaStream(stream) {
    if (stream !== this.mediaStream_) {
      // release unused media resources
      if (this.mediaStream_) {
        this.mediaStream_.getTracks().forEach(track => track.stop());
      }
      this.mediaStream_ = stream;
    }
  }

  /**
   *
   * @package
   * @param {boolean} has
   * @memberof Stream
   */
  setHasVideo(has) {
    this.hasVideo_ = has;
    // ensure video is played/stopped after stream is updated
    if (this.isPlaying_) {
      if (has) {
        this.log_.info('stream updated, play video');
        this.playVideo();
      } else {
        this.log_.info('stream updated, stop video');
        this.stopVideo();
      }
    }
  }

  /**
   *
   * @package
   * @param {boolean} has
   * @memberof Stream
   */
  setHasAudio(has) {
    this.hasAudio_ = has;
    // ensure audio is played/stopped after stream is updated
    if (this.isPlaying_) {
      if (has) {
        this.log_.info('stream updated, play audio');
        this.playAudio();
      } else {
        this.log_.info('stream updated, stop audio');
        this.stopAudio();
      }
    }
  }

  /**
   * 监听Stream事件<br>
   * 详细事件列表请参见：{@link module:Event.StreamEvent StreamEvent }
   *
   * @param {string} eventName 事件名称
   * @param {function} handler 事件处理方法
   * @memberof Stream
   * @example
   * stream.on('player-state-changed', event => {
   *   console.log(`${event.type} player is ${event.state}`);
   * });
   */
  on(event, handler) {
    this.emitter_.on(event, handler);
  }

  isRemote() {
    return this.isRemote_;
  }
}

export default Stream;
