import transform from 'sdp-transform';
import { MAIN_STREAM_MSID, AUX_STREAM_MSID, StreamType } from '../common/constants';
import log from '../utils/log/logger';

export const checkTrack = function(prevSdp, curSdp) {
  const prevMedia = transform.parse(prevSdp).media;
  const curMedia = transform.parse(curSdp).media;
  const added = curMedia.filter(item => {
    const p = prevMedia.find(pi => pi.mid === item.mid);
    return (p === undefined || p.ssrcs === undefined) && item.ssrcs;
  });
  const removed = prevMedia.filter(item => {
    const c = curMedia.find(ci => ci.mid === item.mid);
    return (c === undefined || c.ssrcs === undefined) && item.ssrcs;
  });

  return { added, removed };
};

/**
 * validate if a track is associated with a valid ssrc.
 * assuming that audio track is packaged together with main video track in the same stream
 * and auxiliary video track is in separate stream
 */
export const validateStreams = function(sdp) {
  let parsed = transform.parse(sdp);
  let validated = {
    audio: [],
    video: []
  };
  parsed.media.forEach(item => {
    if (item.ssrcs) {
      let id = item.ssrcs[0].id;
      let type = (id >> 16) & 0xff;
      if (item.type === 'audio') {
        // audio is always in main stream.
        validated.audio.push(MAIN_STREAM_MSID);
      } else if (item.type == 'video') {
        const msid = type === StreamType.MAIN ? MAIN_STREAM_MSID : AUX_STREAM_MSID;
        validated.video.push(msid);
      }
    }
  });
  return validated;
};

export const parse = function(sdp) {
  return transform.parse(sdp);
};

export const write = function(parsed) {
  return transform.write(parsed);
};

export const getStreamTypeFromSsrc = function(ssrcId) {
  return (ssrcId >> 16) & 0xff;
};
