import EventEmitter from 'eventemitter3';
import RemoteStream from './remote-stream';
import RtcError from '../common/rtc-error';
import ErrorCode from '../common/error-code';
import { SignalCommand, SignalEvent } from '../signal/signal-event';
import { ConnectionEvent } from './event';
import {
  AUDIO_MUTED_MASK,
  VIDEO_MUTED_MASK,
  MAIN_STREAM_MSID,
  StreamType,
  AUX_STREAM_MSID,
  AUX_VIDEO_MUTED_MASK
} from '../common/constants';
import * as SdpParser from './sdp';
import * as Utils from '../utils/utils';
import isEmpty from '../utils/is-empty';
import { logEvent } from '../utils/event-log';
import IDLogger from '../utils/log/id-logger';
import { addEvent, getEvents, EventId } from '../utils/event-hub';
import { getCurrentTime } from '../utils/time';
import Stats from './stats';

// default sdp offer options
const offerOptions = {
  // offerToReceiveAudio: true,
  // offerToReceiveVideo: true,
  voiceActivityDetection: false
};

/**
 * Connection represents a peer connection between the local and remote
 * It handles the events received from RTCPeerConnection and events
 * received from SignalChannel which is dedicated to the connection.
 *
 * For uplink connection, it's also responsible for publishing local stream.
 * For downlink connection, it's responsible for constructing remote stream.
 * There could be two remote streams in one downlink connection, one of the two
 * is the remote stream containing the audio track and the main video track,
 * another one is the remote stream containing only the auxiliary video stream,
 * e.g, the screen sharing stream
 *
 * @export
 * @class Connection
 */
export default class Connection {
  constructor(options) {
    /*
     * the userId who initiates the connection, for uplink connection it's the local userId
     * for downlink connection, it's the remote userId.
     */
    this.userId_ = options.userId;
    // tinyId is the userId representation in remote server.
    this.tinyId_ = options.tinyId;
    this.client_ = options.client;
    this.sdpSemantics_ = options.client.getSdpSemantics();
    this.isUplink_ = options.isUplink;
    this.log_ = new IDLogger({
      id: 'n|' + this.userId_,
      direction: this.isUplink_ ? 'local' : 'remote',
      type: ''
    });
    this.signalChannel_ = options.signalChannel;
    this.peerConnection_ = null;
    this.connectTimer_ = -1;
    /** @type {Map<number:Stream>} streamId -> Stream */
    this.remoteStreams_ = new Map();

    this.localStream_ = null;

    this.waitForUpdatedAnswer_ = false;

    // used to prevent multiple Error Report.
    this.isErrorObserved_ = false;

    // uplink's muted state
    this.mutedState_ = 0;

    // downlink's subscribing state
    this.subscribeState_ = {
      audio: true, // audio track
      video: true, // main video track
      auxiliary: true // auxiliary video track
    };
    this.pendingSubscription_ = [];
    this.pendingStreams_ = [];
    this.subscriptionTimeout_ = -1;
    this.subscriptionRetryCount_ = 0;
    this.isSubscriptionPending_ = false;
    this.sentSubscriptionAfterConnected_ = false;

    this.emitter_ = new EventEmitter();

    // 视频通话健康度统计

    // 视频生效时间
    this.startTime_ = new Date();
    // 视频结束时间
    this.endTime_ = this.startTime_;
    this.hasValidEndTime_ = false;
    this.hasVideo_ = false;
  }

  /**
   * Intialize the connection, which install the permenant handlers for RTCPeerConnection and SignalChannel events
   *
   * @memberof Connection
   */
  initialize() {
    const iceServers = this.client_.getIceServers();

    // set to |relay| if user set a TURN server to force it go through TURN,
    // otherwise use |all| for normal connection.
    // This is used only for TEST!
    // const iceTransportPolicy = isEmpty(iceServers) ? 'all' : 'relay';

    // set to |all| to allow ICE select the best candidate to connect
    // it may use host/srflx to connect if it's reachable, otherwise it
    // may use relay to connect if there's a TURN available.
    const iceTransportPolicy = 'all';

    const configuration = {
      iceServers,
      iceTransportPolicy,
      sdpSemantics: this.sdpSemantics_,
      bundlePolicy: 'max-bundle',
      rtcpMuxPolicy: 'require',
      // the two items below are non-standard & obsolete
      tcpCandidatePolicy: 'disable',
      IceTransportsType: 'nohost'
    };
    this.peerConnection_ = new RTCPeerConnection(configuration);
    this.peerConnection_.onconnectionstatechange = this.onConnectionStateChange.bind(this);
    // uplink RTCPeerConnection doesn't need to listen on ontrack event
    if (!this.isUplink_) {
      this.peerConnection_.ontrack = this.onTrack.bind(this);
    }

    /*
     * Uplink RTCPeerConnection flow:
     * 1. addTrack() of local stream
     * 2. createOffer/setLocalDescription and sending offer to remote server.
     * 3. remote server sends back answer over SignalChannel and setRemoteDescription
     */

    this.installSignalChannelEvents();
  }

  /**
   * Publish a local stream via this uplink connection
   *
   * @param {Stream} stream Local stream created by TRTC.createStream()
   * @param {function} failureCallback callback to be called on publishing failure
   * @memberof Connection
   */
  publish(stream, failureCallback) {
    this.localStream_ = stream;
    const mediaStream = stream.getMediaStream();
    this.log_.info(`is publishing stream: ${stream.getId()}`);

    mediaStream.getTracks().forEach(track => {
      if (track.kind === 'video') {
        this.setVideoStats('start');
      }

      // The adapter.js addTrack polyfill expects a MediaStream parameter
      // verified with Chrome M59.
      this.peerConnection_.addTrack(track, mediaStream);
    });
    // update media setting before exchanging offer with remote server.
    this.updateMediaSettings(mediaStream);

    this.exchangeOffer(failureCallback);
  }

  updateMediaSettings(mediaStream) {
    // data structure defined by backend team, please bear with the ugly style.
    let mediaSettings = {
      EncVideoCodec: 'H264',
      EncVideoWidth: 0,
      EncVideoHeight: 0,
      EncVideoBr: '0', // bps
      EncVideoFps: 0,

      EncAudioCodec: 'opus',
      EncAudioFS: 0, // 采样率
      EncAudioCh: 0, // 声道数
      EncAudioBr: '0' // 单位bps
    };
    if ('getSettings' in MediaStreamTrack.prototype) {
      mediaStream.getTracks().forEach(track => {
        const settings = track.getSettings();
        if (track.kind === 'audio') {
          let channelCount = 1;
          // no channelCount in Safari 12.1.1
          if (settings.channelCount) channelCount = settings.channelCount;
          mediaSettings.EncAudioCh = channelCount;
          mediaSettings.EncAudioBr = `${this.localStream_.getAudioBitrate() * 1000}`; // bps
          mediaSettings.EncAudioFS = settings.sampleRate;
        } else if (track.kind === 'video') {
          mediaSettings.EncVideoWidth = settings.width;
          mediaSettings.EncVideoHeight = settings.height;
          mediaSettings.EncVideoFps = settings.frameRate;
          mediaSettings.EncVideoBr = `${this.localStream_.getVideoBitrate() * 1000}`; // bps
        }
      });
    } else {
      mediaSettings = this.getMediaSettingsFromProfile(mediaSettings);
    }
    this.log_.info('updateMediaSettings: ' + JSON.stringify(mediaSettings));
    this.signalChannel_.send(SignalCommand.UPDATE_MEDIA_SETTINGS, mediaSettings);
  }

  getMediaSettingsFromProfile(settings) {
    const stream = this.localStream_;
    if (stream) {
      if (stream.getAudioTrack()) {
        const profile = stream.getAudioProfile();
        settings.EncAudioCh = profile.channelCount;
        settings.EncAudioBr = `${profile.bitrate * 1000}`; // bps
        settings.EncAudioFS = profile.sampleRate;
      }

      if (stream.getVideoTrack()) {
        const profile = stream.getVideoProfile();
        settings.EncVideoWidth = profile.width;
        settings.EncVideoHeight = profile.height;
        settings.EncVideoFps = profile.frameRate;
        settings.EncVideoBr = `${profile.bitrate * 1000}`; // bps
      }
    }
    return settings;
  }

  /**
   * replace the tracks in the on-going RTCPeerConnection.
   * this is only valid for uplink connection
   *
   * @param {MediaStream} stream
   */
  async replaceStream(stream) {
    if (this.peerConnection_) {
      const videoTracks = stream.getVideoTracks();
      const audioTracks = stream.getAudioTracks();
      const senders = this.peerConnection_.getSenders() || [];
      senders.forEach(sender => {
        if (sender.track) {
          if (sender.track.kind === 'video') {
            if (sender.replaceTrack && videoTracks.length > 0) {
              sender.replaceTrack(videoTracks[0]);
            } else {
              this.peerConnection_.removeTrack(sender);
              if (videoTracks.length > 0) {
                this.peerConnection_.addTrack(videoTracks[0], stream);
              }
            }
          } else if (sender.track.kind === 'audio') {
            if (sender.replaceTrack && audioTracks.length > 0) {
              sender.replaceTrack(audioTracks[0]);
            } else {
              this.peerConnection_.removeTrack(sender);
              if (audioTracks.length > 0) {
                this.peerConnection_.addTrack(audioTracks[0], stream);
              }
            }
          }
        }
      });

      // update media setting before exchanging offer with remote server.
      this.updateMediaSettings(stream);

      await this.updateOffer();
    }
  }

  async addTrack(track) {
    if (this.peerConnection_) {
      this.log_.info(`is adding ${track.kind} track to current published local stream`);
      // The adapter.js addTrack polyfill expects a MediaStream parameter
      const mediaStream = this.localStream_.getMediaStream();
      this.peerConnection_.addTrack(track, mediaStream);
      if (track.kind === 'video') {
        this.setVideoStats('start');
      }

      // update media setting before exchanging offer with remote server.
      this.updateMediaSettings(mediaStream);

      await this.updateOffer();
    }
  }

  async removeTrack(track) {
    if (this.peerConnection_) {
      this.log_.info(`is removing ${track.kind} track from current published local stream`);
      const sender = this.peerConnection_.getSenders().find(sender => {
        return sender.track === track;
      });
      if (sender) {
        this.peerConnection_.removeTrack(sender);
        if (track.kind === 'video') {
          this.setVideoStats('end');
        }

        // update media setting before exchanging offer with remote server.
        this.updateMediaSettings(this.localStream_.getMediaStream());

        await this.updateOffer();
      }
    }
  }

  isReplaceTrackAvailable() {
    return 'RTCRtpSender' in window && 'replaceTrack' in window.RTCRtpSender.prototype;
  }

  async replaceTrack(track) {
    if (!this.isReplaceTrackAvailable()) {
      throw new RtcError({
        code: ErrorCode.INVALID_OPERATION,
        message:
          'replaceTrack is not supported in this browser, please use switchDevice or addTrack instead'
      });
    }
    if (!this.peerConnection_) {
      throw new RtcError({
        code: ErrorCode.INVALID_OPERATION,
        message: 'replaceTrack() is only valid after the LocalStream has been published'
      });
    }
    // getSenders is available since of Chrome M64
    const senders = this.peerConnection_.getSenders();
    if (senders.length === 0) {
      throw new RtcError({
        code: ErrorCode.INVALID_OPERATION,
        message: 'replaceTrack() is only valid after the LocalStream has been published'
      });
    }
    senders.forEach(sender => {
      if (sender.track && sender.track.kind === track.kind) {
        // replaceTrack() is available since of Chrome M65
        this.log_.info(`is replacing ${track.kind} track to current published local stream`);
        sender.replaceTrack(track);
      }
    });
  }

  /**
   * Close the connection and release all of its resources
   * For downlink connection, it also cleanup the remote stream
   *
   * @memberof Connection
   */
  close() {
    this.log_.info('closing connection');

    if (this.connectTimer_ !== -1) {
      clearTimeout(this.connectTimer_);
      this.connectTimer_ = -1;
    }

    if (this.subscriptionTimeout_ !== -1) {
      clearTimeout(this.subscriptionTimeout_);
      this.subscriptionTimeout_ = -1;
    }

    if (this.peerConnection_) {
      // reset onconnectionstatechange to avoid handling |closed| event
      this.peerConnection_.onconnectionstatechange = event => {};
      this.peerConnection_.close();
      this.peerConnection_ = null;
    }
    if (!this.isUplink_) {
      this.remoteStreams_.forEach((value, key, map) => {
        const stream = value;
        stream.setConnection(null);
        this.emitter_.emit(ConnectionEvent.STREAM_REMOVED, { stream });
      });
      this.remoteStreams_.clear();
    }
    // cleanup the listeners on SignalChennel to avoid being called after closed.
    this.uninstallSignalChannelEvents();
  }

  /**
   *
   * @private
   * @memberof Connection
   */
  installSignalChannelEvents() {
    if (this.isUplink_) {
      this.signalChannel_.on(SignalEvent.NEW_REMOTE_SDP, this.onNewRemoteSdp, this);
    }

    if (!this.isUplink_) {
      this.signalChannel_.on(SignalEvent.UPDATE_REMOTE_SDP, this.onUpdateRemoteSdp, this);
      this.signalChannel_.on(SignalEvent.UPDATE_AUDIO_SSRC, this.onUpdateAudioSSRC, this);
      this.signalChannel_.on(SignalEvent.UPDATE_VIDEO_SSRC, this.onUpdateVideoSSRC, this);
      this.signalChannel_.on(SignalEvent.SUBSCRIBE_ACK, this.onSubscription, this);
    }
  }

  uninstallSignalChannelEvents() {
    if (this.isUplink_) {
      this.signalChannel_.removeListener(SignalEvent.NEW_REMOTE_SDP, this.onNewRemoteSdp, this);
    }

    if (!this.isUplink_) {
      this.signalChannel_.removeListener(
        SignalEvent.UPDATE_REMOTE_SDP,
        this.onUpdateRemoteSdp,
        this
      );
      this.signalChannel_.removeListener(
        SignalEvent.UPDATE_AUDIO_SSRC,
        this.onUpdateAudioSSRC,
        this
      );
      this.signalChannel_.removeListener(
        SignalEvent.UPDATE_VIDEO_SSRC,
        this.onUpdateVideoSSRC,
        this
      );
      this.signalChannel_.removeListener(SignalEvent.SUBSCRIBE_ACK, this.onSubscription, this);
    }
  }

  onNewRemoteSdp(event) {
    if (this.hitTest(event.data.srctinyid)) {
      const answer = event.data.content;
      this.acceptAnswer(answer);
    }
  }

  onUpdateRemoteSdp(event) {
    const content = event.data.content;
    if (this.hitTest(content.srctinyid)) {
      this.log_.info('is updating remote sdp offer');
      const sdp = content.newSdp;
      this.updateRemoteDescription(sdp);
    }
  }

  onUpdateAudioSSRC(event) {
    const content = event.data.content;
    if (this.hitTest(content.srctinyid)) {
      this.log_.info('is updating audio ssrc');
      const sdp = content.newSdp;
      this.updateRemoteDescription(sdp);
    }
  }

  onUpdateVideoSSRC(event) {
    const content = event.data.content;
    if (this.hitTest(content.srctinyid)) {
      this.log_.info('is updating video ssrc');
      const sdp = content.newSdp;
      this.updateRemoteDescription(sdp);
    }
  }

  /**
   * RTCPeerConnection.ontrack handler
   *
   * @private
   * @param {*} event
   * @memberof Connection
   */
  onTrack(/** @type {RTCTrackEvent} */ event) {
    const stream = event.streams[0];
    const track = event.track;
    this.log_.info(`ontrack() kind: ${track.kind} id: ${track.id} streamId: ${stream.id}`);

    /**
     * validate streams in unified-plan as the remote backend adds invalid m= line to sdp offer
     * in plan-b the m= line is always valid.
     */
    if (this.sdpSemantics_ === 'unified-plan') {
      const sdpOffer = this.peerConnection_.remoteDescription.sdp;
      const validated = SdpParser.validateStreams(sdpOffer);
      if (track.kind === 'audio') {
        // audio is always packed into main stream
        // if remote peer unpublish and then publish immediately, backend will send
        // two update-audio-ssrc sequentially and this will cause ontrack with an invalid
        // audio track but the stream.id is not equal to MAIN_STREAM_MSID
        if (validated.audio.length === 0 || stream.id !== MAIN_STREAM_MSID) {
          this.log_.debug('skip this invalid audio track');
          return;
        }
      } else {
        if (validated.video.indexOf(stream.id) === -1) {
          this.log_.debug(`skip this invalid video track: ${track.id}  msid: ${stream.id}`);
          return;
        }
      }
    }

    logEvent({ eventType: 'ontrack', kind: track.kind });

    let added = false;
    let remoteStream = this.remoteStreams_.get(stream.id);
    if (remoteStream === undefined) {
      const type = stream.id === MAIN_STREAM_MSID ? 'main' : 'auxiliary';
      remoteStream = new RemoteStream({
        type,
        userId: this.userId_,
        client: this.client_
      });
      remoteStream.setConnection(this);
      this.remoteStreams_.set(stream.id, remoteStream);
      added = true;
    }

    // always update the MediaStream in case of track is added/removed
    remoteStream.setMediaStream(stream);
    if (track.kind === 'audio') {
      // the previous audio track may be in muted/ended state after remote updating
      // audio ssrc so we have to restart audio player with the fresh track
      if (remoteStream.hasAudio() && remoteStream.isPlaying()) {
        this.log_.debug('already has an audio track, restart audio player with the new track');
        remoteStream.restartAudio();
      }
      remoteStream.setHasAudio(true);
    } else {
      // 视频状态统计
      this.setVideoStats('start');

      if (remoteStream.hasVideo() && remoteStream.isPlaying()) {
        this.log_.debug('already has an video track, restart video player with the new track');
        remoteStream.restartVideo();
      }
      remoteStream.setHasVideo(true);
    }

    if (added) {
      // fire 'stream-added' event
      this.emitter_.emit(ConnectionEvent.STREAM_ADDED, {
        stream: remoteStream
      });
    } else {
      // fire 'stream-updated' event to let custom renderer update audio/video track
      this.emitter_.emit(ConnectionEvent.STREAM_UPDATED, {
        stream: remoteStream
      });
    }
  }

  isRtpSenderAvailable() {
    return 'RTCRtpSender' in window && 'setParameters' in window.RTCRtpSender.prototype;
  }

  async setBandwidth(bandwidth, type) {
    if (!this.isUplink_) return;

    if (type === undefined) {
      type = 'video';
    }

    // In Chrome, use RTCRtpSender.setParameters to change bandwidth without
    // (local) renegotiation. Note that this will be within the envelope of
    // the initial maximum bandwidth negotiated via SDP.
    if (this.isRtpSenderAvailable()) {
      const sender = this.peerConnection_
        .getSenders()
        .find(sender => sender.track && sender.track.kind === type);
      if (sender) {
        const parameters = sender.getParameters();
        if (!parameters.encodings) {
          parameters.encodings = [{}];
        }
        if (bandwidth === 'unlimited') {
          delete parameters.encodings[0].maxBitrate;
        } else {
          parameters.encodings[0].maxBitrate = bandwidth * 1000;
        }
        sender
          .setParameters(parameters)
          .then(() => {
            this.log_.debug(type + ' bandwidth was set to ' + bandwidth + ' kbps');
          })
          .catch(e => this.log_.error('failed to set bandwidth: ' + e));
      }
    }
  }

  // bandwidth setting via munging sdp
  // refer to: http://www.rtcbits.com/2016/11/controlling-bandwidth-usage-in-webrtc.html
  updateVideoBandwidthRestriction(sdp, bandwidth) {
    let modifier = 'AS';
    // if (adapter.browserDetails.browser === 'firefox') {
    //   bandwidth = (bandwidth >>> 0) * 1000;
    //   modifier = 'TIAS';
    // }
    if (sdp.indexOf('b=' + modifier + ':') === -1) {
      // insert b= after c= line of m=video
      sdp = sdp.replace(
        /m=video (.*)\r\nc=IN (.*)\r\n/,
        'm=video $1\r\nc=IN $2\r\nb=' + modifier + ':' + bandwidth + '\r\n'
      );
    } else {
      sdp = sdp.replace(
        new RegExp('b=' + modifier + ':.*\r\n'),
        'b=' + modifier + ':' + bandwidth + '\r\n'
      );
    }
    return sdp;
  }

  updateAudioBandwidthRestriction(sdp, bandwidth) {
    let modifier = 'AS';
    // insert b= after c= line of m=audio
    sdp = sdp.replace(
      /m=audio (.*)\r\nc=IN (.*)\r\n/,
      'm=audio $1\r\nc=IN $2\r\nb=' + modifier + ':' + bandwidth + '\r\n'
    );
    return sdp;
  }

  removeBandwidthRestriction(sdp) {
    return sdp.replace(/b=AS:.*\r\n/, '').replace(/b=TIAS:.*\r\n/, '');
  }

  /**
   *
   * @private
   * @param {*} failureCallback
   * @memberof Connection
   */
  exchangeOffer(failureCallback) {
    this.peerConnection_
      .createOffer(offerOptions)
      .then(offer => {
        return this.peerConnection_.setLocalDescription(offer);
      })
      .then(() => {
        this.log_.info('createOffer success, sending offer to remote server');
        const desc = this.peerConnection_.localDescription;
        this.log_.debug('sending sdp offer: ' + desc.sdp);
        // FIXME: target is supposed to be tinyId, but if we send this.tinyId_ remote server return nothing.
        this.signalChannel_.send(SignalCommand.NEW_LOCAL_SDP, desc, /** myself */ 0);

        logEvent({
          eventType: 'setLocalDescription',
          kind: 'offer',
          result: 'success'
        });
      })
      .catch(err => {
        logEvent({
          eventType: 'setLocalDescription',
          kind: 'offer',
          result: 'failed'
        });
        this.log_.error('failed to create offer');
        failureCallback(
          new RtcError({
            code: ErrorCode.CREATE_OFFER_FAILED,
            message: 'failed to create offer'
          })
        );
      });
  }

  async updateOffer() {
    let offer = '';
    try {
      offer = await this.peerConnection_.createOffer(offerOptions);
      await this.peerConnection_.setLocalDescription(offer);
      const desc = this.peerConnection_.localDescription;
      this.log_.info('createOffer success, sending updated offer to remote server');
      this.log_.debug('updatedOffer: ' + desc.sdp);

      this.signalChannel_.send(SignalCommand.UPDATE_LOCAL_SDP, desc, 0);
      this.waitForUpdatedAnswer_ = true;
      logEvent({
        eventType: 'setLocalDescription',
        kind: 'offer',
        result: 'success'
      });
    } catch (e) {
      logEvent({
        eventType: 'setLocalDescription',
        kind: 'offer',
        result: 'failed'
      });
      this.log_.error('failed to create updated sdp offer');
      throw new RtcError({
        code: ErrorCode.CREATE_OFFER_FAILED,
        message: 'failed to create updated sdp offer'
      });
    }
  }

  /**
   *
   * @private
   * @param {*} answer
   * @memberof Connection
   * @fires 'stream-published'
   */
  acceptAnswer(answer) {
    // set video bitrate
    let videoBitrate = this.localStream_.getVideoBitrate();
    let sdp = this.updateVideoBandwidthRestriction(answer.sdp, videoBitrate);

    // set audio bitrate
    const audioBitrate = this.localStream_.getAudioBitrate();
    sdp = this.updateAudioBandwidthRestriction(sdp, audioBitrate);

    const desc = { type: answer.type, sdp: sdp };
    this.peerConnection_.setRemoteDescription(desc).then(
      () => {
        this.log_.debug('accepted anwswer: ' + sdp);

        // ensure the maxBitrate is set via RTCRtpSender.setParameter
        this.setBandwidth(audioBitrate, 'audio');
        this.setBandwidth(videoBitrate, 'video');

        if (!this.waitForUpdatedAnswer_) {
          this.log_.info('accepted remote answer, fired stream-published event');
          this.emitter_.emit(ConnectionEvent.STREAM_PUBLISHED, {
            stream: this.localStream_
          });
        }
        if (this.waitForUpdatedAnswer_) {
          this.waitForUpdatedAnswer_ = false;
        }

        logEvent({
          eventType: 'setRemoteDescription',
          kind: 'answer',
          result: 'success'
        });
      },
      error => {
        logEvent({
          eventType: 'setRemoteDescription',
          kind: 'answer',
          result: 'failed'
        });
        this.log_.error('failed to accept remote answer ' + error);
      }
    );
  }

  // fire a ICE failure event for test
  failIceConnectionForTest() {
    this.log_.warn('failIceConnectionForTest()');
    this.emitter_.emit(
      ConnectionEvent.ERROR,
      new RtcError({
        message: 'fake ICE failure fired',
        code: ErrorCode.ICE_TRANSPORT_ERROR
      })
    );
  }

  onConnectionStateChange(event) {
    this.log_.warn('onConnectionStateChange() connectionState: ' + event.target.connectionState);

    if (event.target.connectionState === 'connecting') {
      // assume that ICE/DTLS Transport connection shall be established successfully in 10s.
      // otherwise throw error to let app refresh the page.
      const TIMEOUT = 10000;
      this.connectTimer_ = setTimeout(() => {
        const message = `DTLS Transport connection timeout (${TIMEOUT / 1000}s)`;
        logEvent({ eventType: 'iceConnectionState', result: 'failed' });
        this.log_.error(message);

        this.addTransportEvent('disconnected');

        this.isErrorObserved_ = true;
        this.emitter_.emit(
          ConnectionEvent.ERROR,
          new RtcError({ message, code: ErrorCode.ICE_TRANSPORT_ERROR })
        );
      }, TIMEOUT);

      this.emitter_.emit(ConnectionEvent.CONNECTION_STATE_CHANGED, {
        state: 'connecting'
      });
    } else {
      clearTimeout(this.connectTimer_);
      this.connectTimer_ = -1;
    }

    // |closed| should only be fired by RTCPeerConnection.close(). In case, |closed| is fired
    // by browser internal error, we reset onconnectionstatechange callback before RTCPeerConnection.close
    // and then take both |failed| and |closed| as error
    // seiyabai
    if (event.target.connectionState === 'failed' || event.target.connectionState === 'closed') {
      const message = `ICE/DTLS Transport connection ${event.target.connectionState}`;
      logEvent({ eventType: 'iceConnectionState', result: 'failed' });
      this.log_.error(message);
      if (!this.isErrorObserved_) {
        this.addTransportEvent('disconnected');
        // only fire the ERROR event if error hasn't been fired before.
        this.emitter_.emit(
          ConnectionEvent.ERROR,
          new RtcError({ message, code: ErrorCode.ICE_TRANSPORT_ERROR })
        );
      }
    }

    if (
      event.target.connectionState === 'connected' ||
      event.target.connectionState === 'completed'
    ) {
      logEvent({ eventType: 'iceConnectionState', result: 'success' });
      this.addTransportEvent('connected');

      this.emitter_.emit(ConnectionEvent.CONNECTION_STATE_CHANGED, {
        state: 'connected'
      });

      // send pending subscription
      if (!this.sentSubscriptionAfterConnected_ && this.pendingSubscription_.length > 0) {
        this.log_.info('send pending subscription after RTCPeerConnection is connected');
        const sub = this.pendingSubscription_[0];
        this.doSendSubscription(sub.data, sub.stream);
        // sent it once, don't send after RTCPeerConnection auto reconnected
        this.sentSubscriptionAfterConnected_ = true;
      }
    } else if (event.target.connectionState === 'disconnected') {
      this.close();
      // this.addTransportEvent('disconnected');
      const message = `ICE/DTLS Transport connection ${event.target.connectionState}`;
      this.emitter_.emit(
        ConnectionEvent.ERROR,
        new RtcError({ message, code: ErrorCode.ICE_TRANSPORT_BROKE })
      );
    }
  }

  // check if the srctinyid is associated with me.
  hitTest(srctinyid) {
    // for uplink connection, sometimes the srctinyid sent from remote is 0
    return ((srctinyid === 0 || srctinyid === '0') && this.isUplink_) || srctinyid === this.tinyId_;
  }

  updateRemoteDescription(sdp) {
    this.log_.debug(`updateRemoteDescription() ${sdp.sdp}`);
    const prevSdp = this.peerConnection_.remoteDescription.sdp;
    this.peerConnection_
      .setRemoteDescription(sdp)
      .then(() => {
        logEvent({
          eventType: 'setRemoteDescription',
          kind: 'offer',
          result: 'success'
        });
        this.log_.info('updateRemoteDescription success');
        if (this.sdpSemantics_ === 'unified-plan') {
          const changed = SdpParser.checkTrack(prevSdp, sdp.sdp);
          /**
           * addTrack based on sdp m=line diff for unified-plan only.
           * in plan-b, ontrack is guaranteed to be fired to add track.
           */
          this.addRemoteTrack(changed.added);
          this.removeRemoteTrack(changed.removed);
        } else {
          // plan-b: remove track based on ssrcs/ssrcGroups existence
          this.removeRemoteTrackLegacy(prevSdp, sdp.sdp);
        }

        const stream = this.pendingStreams_.shift();
        if (stream) {
          this.log_.debug(`mark ${stream.getType()} stream exit subscription cycle`);
          stream.setInSubscriptionCycle(false);
        }
      })
      .catch(error => {
        logEvent({
          eventType: 'setRemoteDescription',
          kind: 'offer',
          result: 'failed'
        });
        this.log_.error(`updateRemoteDescription failed ${error}`);
      });
  }

  /**
   *
   * @private
   * @param {*} added
   * @memberof Connection
   */
  addRemoteTrack(added) {
    added.forEach(item => {
      const mid = item.mid;
      const type = SdpParser.getStreamTypeFromSsrc(item.ssrcs[0].id);
      const msid = type === StreamType.AUX ? AUX_STREAM_MSID : MAIN_STREAM_MSID;
      const receiver = this.peerConnection_.getReceivers()[mid];
      if (receiver && receiver.track) {
        this.log_.info(`add ${receiver.track.kind} track ${receiver.track.id}`);
        let remoteStream = this.remoteStreams_.get(msid);
        if (remoteStream) {
          remoteStream.getMediaStream().addTrack(receiver.track);
          if (receiver.track.kind === 'audio') {
            remoteStream.setHasAudio(true);
          } else {
            remoteStream.setHasVideo(true);

            // 视频状态统计
            this.setVideoStats('start');
          }

          this.emitter_.emit(ConnectionEvent.STREAM_UPDATED, {
            stream: remoteStream
          });
        } else {
          this.log_.debug(`remoteStream for msid:${msid} not exist`);
          const stream = new MediaStream();
          stream.addTrack(receiver.track);
          const st = msid === MAIN_STREAM_MSID ? 'main' : 'auxiliary';
          remoteStream = new RemoteStream({
            type: st,
            userId: this.userId_,
            client: this.client_
          });
          remoteStream.setMediaStream(stream);
          remoteStream.setConnection(this);
          this.remoteStreams_.set(msid, remoteStream);
          if (receiver.track.kind === 'audio') {
            remoteStream.setHasAudio(true);
          } else {
            remoteStream.setHasVideo(true);
          }

          // fire 'stream-added' event
          this.emitter_.emit('stream-added', { stream: remoteStream });
        }
      }
    });
  }

  /**
   *
   * @private
   * @param {*} removed
   * @memberof Connection
   */
  removeRemoteTrack(removed) {
    removed.forEach(item => {
      const type = SdpParser.getStreamTypeFromSsrc(item.ssrcs[0].id);
      const kind =
        type === StreamType.MAIN ? 'video' : type === StreamType.AUX ? 'auxVideo' : 'audio';
      this.log_.info(`[${kind}] track was removed`);
      const msid = kind === 'auxVideo' ? AUX_STREAM_MSID : MAIN_STREAM_MSID;
      const remoteStream = this.remoteStreams_.get(msid);
      if (remoteStream) {
        if (
          (msid === AUX_STREAM_MSID || remoteStream.getMediaStream().getTracks().length === 0) &&
          !remoteStream.isInSubscriptionCycle()
        ) {
          // delete the stream if and only if it's not in subscription cycle
          this.log_.info(`remote stream ${msid} removed`);
          this.remoteStreams_.delete(msid);
          this.emitter_.emit(ConnectionEvent.STREAM_REMOVED, {
            stream: remoteStream
          });
        } else {
          if (kind === 'audio') {
            remoteStream.setHasAudio(false);
          } else {
            remoteStream.setHasVideo(false);

            // 视频状态统计
            this.setVideoStats('end');
          }
          this.emitter_.emit(ConnectionEvent.STREAM_UPDATED, {
            stream: remoteStream
          });
        }
      }
    });
  }

  /**
   * plan-b legacy API
   * remove track based on the existence of ssrcs/ssrcGroups
   * audio m= line comes with/without ssrcs, video m= line comes
   * with/without ssrcGroups/ssrcs. for m= line with auxiliary video
   * track, ssrcGroups.length > 1
   *
   * @private
   * @param {string} prevSdp
   * @param {string} curSdp
   * @memberof Connection
   */
  removeRemoteTrackLegacy(prevSdp, curSdp) {
    const prevMedia = SdpParser.parse(prevSdp).media;
    const curMedia = SdpParser.parse(curSdp).media;
    prevMedia.filter(item => {
      const c = curMedia.find(ci => ci.type === item.type);
      let msid = StreamType.MAIN;
      let remoteStream;
      if (item.type === 'audio') {
        if ((c === undefined || c.ssrcs === undefined) && item.ssrcs) {
          this.log_.info('[audio] track was removed');
          remoteStream = this.remoteStreams_.get(msid);
          if (remoteStream) {
            if (remoteStream.getMediaStream().getTracks().length === 0) {
              // delete the stream if and only if it's not in subscription cycle
              if (!remoteStream.isInSubscriptionCycle()) {
                this.log_.info(`remote stream ${msid} removed`);
                this.remoteStreams_.delete(msid);
                this.emitter_.emit(ConnectionEvent.STREAM_REMOVED, {
                  stream: remoteStream
                });
              }
            } else {
              remoteStream.setHasAudio(false);
              this.emitter_.emit(ConnectionEvent.STREAM_UPDATED, {
                stream: remoteStream
              });
            }
          }
        }
      } else {
        if (
          ((c === undefined || c.ssrcs === undefined) && item.ssrcs) ||
          (c.ssrcGroups && item.ssrcGroups && c.ssrcGroups.length < item.ssrcGroups.length)
        ) {
          let mainRemoved = false;
          let auxRemoved = false;
          if (c.ssrcs === undefined && item.ssrcGroups.length === 2) {
            // both main and aux video tracks were removed simultaneously
            this.log_.info('[main & aux video] tracks were removed');
            mainRemoved = true;
            auxRemoved = true;
          } else if (c.ssrcs === undefined && item.ssrcGroups.length === 1) {
            // main video track was removed.
            this.log_.info('[main video] track was removed');
            mainRemoved = true;
          } else if (item.ssrcGroups.length > c.ssrcGroups.length) {
            // auxiliary video was removed.
            this.log_.info('[auxiliary video] track was removed');
            auxRemoved = true;
          }

          // remove aux's RemoteStream
          if (auxRemoved) {
            msid = AUX_STREAM_MSID;
            this.log_.info(`remote stream ${msid} removed`);
            remoteStream = this.remoteStreams_.get(msid);
            if (remoteStream && !remoteStream.isInSubscriptionCycle()) {
              this.remoteStreams_.delete(msid);
              this.emitter_.emit(ConnectionEvent.STREAM_REMOVED, {
                stream: remoteStream
              });
            }
          }
          if (mainRemoved) {
            msid = MAIN_STREAM_MSID;
            remoteStream = this.remoteStreams_.get(msid);
            if (remoteStream) {
              if (remoteStream.getMediaStream().getTracks().length === 0) {
                if (!remoteStream.isInSubscriptionCycle()) {
                  this.log_.info(`remote stream ${msid} removed`);
                  this.remoteStreams_.delete(msid);
                  this.emitter_.emit(ConnectionEvent.STREAM_REMOVED, {
                    stream: remoteStream
                  });
                }
              } else {
                // 视频状态统计
                this.setVideoStats('end');

                remoteStream.setHasVideo(false);
                this.emitter_.emit(ConnectionEvent.STREAM_UPDATED, {
                  stream: remoteStream
                });
              }
            }
          }
        }
      }
    });
  }

  // set the remote offer received from remote server. this is only valid for downlink connection
  setRemoteOffer(sdp) {
    this.log_.info(`[${this.userId_}] setRemoteOffer sdp: ${sdp.sdp}`);
    this.peerConnection_
      .setRemoteDescription(sdp)
      .then(() => {
        logEvent({
          eventType: 'setRemoteDescription',
          kind: 'offer',
          result: 'success'
        });
        return this.peerConnection_.createAnswer();
      })
      .then(answer => {
        return this.peerConnection_.setLocalDescription(answer);
      })
      .then(() => {
        const desc = this.peerConnection_.localDescription;
        this.signalChannel_.send(SignalCommand.NEW_LOCAL_SDP, desc, this.tinyId_);
        this.log_.info('accepted remote offer and acknowledged answer');
      })
      .catch(error => {
        logEvent({
          eventType: 'setRemoteDescription',
          kind: 'offer',
          result: 'failed'
        });
        this.log_.error('failed to accept remote offer ' + error);
      });
  }

  setMutedState(kind, muted) {
    if (!this.isUplink_) return;

    if (kind === 'audio') {
      if (muted) {
        this.mutedState_ |= AUDIO_MUTED_MASK;
      } else {
        this.mutedState_ &= ~AUDIO_MUTED_MASK;
      }
    } else {
      if (muted) {
        this.mutedState_ |= VIDEO_MUTED_MASK;
      } else {
        this.mutedState_ &= ~VIDEO_MUTED_MASK;
      }
    }
    const data = {
      srctinyid: 0,
      userid: this.userId_,
      flag: this.mutedState_
    };
    this.log_.info(`set ${kind} muted state: [${muted ? 'mute' : 'unmute'}]`);
    this.signalChannel_.send(SignalCommand.UPDATE_MUTE_STAT, data);
  }

  onSubscription(event) {
    const content = event.data.content;
    const tinyId = content.srctinyid;
    if (this.hitTest(tinyId)) {
      const result = content.errCode === 0 ? true : false;
      let subscription = this.pendingSubscription_.shift();
      if (subscription !== undefined) {
        // clear retry stuffs
        this.subscriptionRetryCount_ = 0;
        this.isSubscriptionPending_ = false;
        if (this.subscriptionTimeout_ !== -1) {
          clearTimeout(this.subscriptionTimeout_);
          this.subscriptionTimeout_ = -1;
        }

        const stream = subscription.stream;
        this.log_.info(
          `${stream.getType()} stream ${subscription.type} result: ${
            result ? 'success' : 'failure'
          } errCode: ${content.errCode}`
        );

        subscription.callback(result);

        logEvent({
          eventType: subscription.type,
          result: content.errCode === 0 ? 'success' : 'failed'
        });

        if (result && subscription.type === 'subscribe') {
          // fire 'stream-subscribed' once the subscription is done ok
          this.emitter_.emit(ConnectionEvent.STREAM_SUBSCRIBED, {
            stream
          });
        }
      }

      if (this.pendingSubscription_.length > 0) {
        const sub = this.pendingSubscription_[0];
        this.log_.info('schedule a pending subscription');
        this.doSendSubscription(sub.data, sub.stream);
      }
    }
  }

  // subscribe/unsubscribe must be done in sequence otherwise the state on remote side
  // will be in chaos
  subscribe(stream, options) {
    return new Promise((resolve, reject) => {
      if (
        // default subscription
        typeof options === 'undefined' ||
        // main stream - audio & video
        (stream.getType() === 'main' &&
          typeof options.audio !== 'undefined' &&
          typeof options.video !== 'undefined' &&
          options.audio === this.subscribeState_.audio &&
          options.video === this.subscribeState_.video) ||
        // aux stream
        (stream.getType() === 'auxiliary' &&
          typeof options.video !== 'undefined' &&
          this.subscribeState_.auxiliary === options.video)
      ) {
        // audio & video stream has been already subscribed by default and received from remote server
        // immediately after the transport channel has been established successfully.
        // so we don't need to go through the round-trip and instread firing 'stream-subscribed'
        // event immediately.
        this.emitter_.emit(ConnectionEvent.STREAM_SUBSCRIBED, {
          stream,
          result: true
        });
        return resolve();
      }

      if (stream.getType() === 'main') {
        if (typeof options.audio !== 'undefined') {
          this.subscribeState_.audio = options.audio;
        }
        if (typeof options.video !== 'undefined') {
          this.subscribeState_.video = options.video;
        }

        let eventId;
        let eventDesc;
        if (this.subscribeState_.audio) {
          eventId = EventId.SUBSCRIBE_AUDIO;
          eventDesc = 'subscribe audio';
        } else {
          eventId = EventId.UNSUBSCRIBE_AUDIO;
          eventDesc = 'unsubscribe audio';
        }
        this.addEventInternal(eventId, eventDesc);

        if (this.subscribeState_.video) {
          eventId = EventId.SUBSCRIBE_VIDEO;
          eventDesc = 'subscribe video';
        } else {
          eventId = EventId.UNSUBSCRIBE_VIDEO;
          eventDesc = 'unsubscribe video';
        }
        this.addEventInternal(eventId, eventDesc);
      } else {
        if (typeof options.video !== 'undefined') {
          this.subscribeState_.auxiliary = options.video;
        }
      }
      this.log_.info(
        `subscribe ${stream.getType()} stream with options ${JSON.stringify(
          options
        )} current state: ${JSON.stringify(this.subscribeState_)}`
      );

      this.sendSubscription(stream, 'subscribe', result => {
        if (result) {
          resolve();
        } else {
          const message = 'failed to subscribe remote stream';
          this.log_.error(message);
          reject(new RtcError({ message }));
        }
      });
    });
  }

  unsubscribe(stream) {
    return new Promise((resolve, reject) => {
      if (stream.getType() === 'main') {
        this.subscribeState_.audio = false;
        this.subscribeState_.video = false;
      } else {
        this.subscribeState_.auxiliary = false;
      }
      this.log_.info(
        `unsubscribe ${stream.getType()} stream with ${JSON.stringify(this.subscribeState_)}`
      );
      this.sendSubscription(stream, 'unsubscribe', result => {
        if (result) {
          resolve();
        } else {
          const message = 'failed to unsubscribe remote stream';
          this.log_.error(message);
          reject(new RtcError({ message }));
        }
      });

      this.addEventInternal(EventId.UNSUBSCRIBE_AUDIO, 'unsubscribe audio');
      this.addEventInternal(EventId.UNSUBSCRIBE_VIDEO, 'unsubscribe video');
    });
  }

  addEventInternal(eventId, eventDesc) {
    const uid = this.client_.getUserId();
    let event = {
      eventId,
      eventDesc,
      timestamp: getCurrentTime(),
      // the stream owner - the client's IDs
      userId: uid,
      tinyId: this.client_.getTinyId()
    };
    if (!this.isUplink_) {
      // remote peer's IDs
      event.remoteUserId = this.userId_;
      event.remoteTinyId = this.tinyId_;
    }
    addEvent(uid, event);
  }

  addTransportEvent(state) {
    if (state === 'connected') {
      this.addEventInternal(EventId.TRANSPORT_CONNECTED, 'ice transport is connected');
    } else {
      this.addEventInternal(EventId.TRANSPORT_DISCONNECTED, 'ice transport is disconnected');
    }
  }

  sendSubscription(stream, type, callback) {
    const data = {
      srctinyid: this.tinyId_,
      userid: this.userId_,
      audio: this.subscribeState_.audio,
      bigVideo: this.subscribeState_.video,
      auxVideo: this.subscribeState_.auxiliary
    };

    // subscription order is enforced by SDK
    if (this.pendingSubscription_.length > 0) {
      this.log_.debug('queue the subscription for later handling');
    } else {
      this.doSendSubscription(data, stream);
    }
    this.pendingSubscription_.push({ stream, type, data, callback });

    // mark the stream as is in subscription cycle, stream in subscription cycle
    // shall not be removed otherwise user will be unable to resubscribe it again.
    stream.setInSubscriptionCycle(true);
  }

  doSendSubscription(data, stream) {
    if (
      this.peerConnection_.connectionState !== 'connected' &&
      this.peerConnection_.connectionState !== 'completed'
    ) {
      this.log_.debug(
        `try to send subscription when connectionState ${this.peerConnection_.connectionState}`
      );
      // if we send subscription request to remote server before connectionState is connected
      // remote server will be in a mess state. so we have to postpone the request until the
      // RTCPeerConnection is connected successfully.
      return;
    }

    if (stream) {
      this.pendingStreams_.push(stream);
    }
    this.log_.debug(
      `doSendSubscription() send SUBSCRIBE command with data: ${JSON.stringify(data)}`
    );
    this.signalChannel_.send(SignalCommand.SUBSCRIBE, data);
    this.isSubscriptionPending_ = true;
    this.subscriptionTimeout_ = setTimeout(() => {
      if (this.isSubscriptionPending_) {
        this.log_.debug('subscription timeout');
        this.subscriptionRetryCount_ += 1;
        if (this.subscriptionRetryCount_ <= 3) {
          this.log_.debug('resend subscription');
          const subData = this.pendingSubscription_[0].data;
          this.doSendSubscription(subData);
        } else {
          this.log_.error('remote server not response to subscription');
          // remove the failed one
          this.pendingSubscription_.shift();
          this.pendingStreams_.shift();
          this.isSubscriptionPending_ = false;
          this.subscriptionRetryCount_ = 0;
          this.emitter_.emit(
            ConnectionEvent.ERROR,
            new RtcError({
              code: ErrorCode.SUBSCRIPTION_TIMEOUT,
              message: 'remote server not response to subscription'
            })
          );
        }
      }
    }, 5000);
  }

  /**
   *
   * @package
   * @returns {RTCPeerConnection}
   * @memberof Connection
   */
  getPeerConnection() {
    return this.peerConnection_;
  }

  /**
   *
   * @package
   * @returns
   * @memberof Connection
   */
  getUserId() {
    return this.userId_;
  }

  /**
   *
   * @package
   */
  getTinyId() {
    return this.tinyId_;
  }

  setVideoStats(stage) {
    if (stage === 'start') {
      this.hasVideo_ = true;
      this.startTime_ = new Date();
      this.hasValidEndTime_ = false;
    } else {
      // 远端用户停止推流之后，浏览器 |Chrome| 将接收状态清零导致无法统计
      this.hasVideo_ = false;
      this.endTime_ = new Date();
      this.hasValidEndTime_ = true;
    }
  }

  // 在 Close 之前调用，获取视频通话健康度统计
  async getVideoHealthStats() {
    if (!this.hasValidEndTime_) {
      // 视频状态没有变更过，则当前时间为截止时间
      this.endTime_ = new Date();
    }

    const duration = (this.endTime_ - this.startTime_) / 1000;
    let valid = false;
    let framerate = 0;
    let width = 0;
    let height = 0;
    if (this.hasVideo_) {
      // 有效状态统计
      valid = true;
      let stats;
      if (this.isUplink_) {
        stats = await new Stats().getSenderStats(this);
        framerate = stats.video.framesSent / duration;
      } else {
        stats = await new Stats().getReceiverStats(this);
        framerate = stats.video.framesDecoded / duration;
      }
      width = stats.video.frameWidth;
      height = stats.video.frameHeight;
    }

    return { valid, framerate, duration, width, height };
  }

  /**
   * add listeners to Connection
   *
   * @param {*} event
   * @param {*} handler
   * @memberof Connection
   */
  on(event, handler) {
    this.emitter_.on(event, handler);
  }
}
