/*
 * Connection's Event
 */
const ConnectionEvent = {
  STREAM_ADDED: 'stream-added',
  STREAM_REMOVED: 'stream-removed',
  STREAM_UPDATED: 'stream-updated',
  STREAM_PUBLISHED: 'stream-published',
  STREAM_SUBSCRIBED: 'stream-subscribed',
  STREAM_UNSUBSCRIBED: 'stream-unsubscribed',
  STATE_CHANGED: 'state-changed',
  ERROR: 'error',
  CONNECTION_STATE_CHANGED: 'connection-state-changed'
};

/**
 * **客户端事件列表**<br>
 * <br>
 * `Client.on('eventName')` 事件监听中的事件名称。
 *
 * @module Event
 */
const ClientEvent = {
  /**
   * @description 远端流添加事件，当远端用户发布流后会收到该通知。
   * @default 'stream-added'
   * @memberOf module:Event
   * @example
   * client.on('stream-added', event => {
   *   const remoteStream = event.stream;
   * });
   */
  STREAM_ADDED: 'stream-added',
  /**
   * @description 远端流移除事件，当远端用户取消发布流后会收到该通知。
   * @default 'stream-removed'
   * @memberOf module:Event
   * @example
   * client.on('stream-removed', event => {
   *   const remoteStream = event.stream;
   * });
   */
  STREAM_REMOVED: 'stream-removed',
  /**
   * @description 远端流更新事件，当远端用户添加、移除或更换音视频轨道后会收到该通知。
   * @default 'stream-updated'
   * @memberOf module:Event
   * @example
   * client.on('stream-updated', event => {
   *   const remoteStream = event.stream;
   * });
   */
  STREAM_UPDATED: 'stream-updated',
  /**
   * @description 远端流订阅成功事件，调用 {@link Client#subscribe subscribe()} 成功后会触发该事件。
   * @default 'stream-subscribed'
   * @memberOf module:Event
   * @example
   * client.on('stream-subscribed', event => {
   *   const remoteStream = event.stream;
   * });
   */
  STREAM_SUBSCRIBED: 'stream-subscribed',
  /**
   * @description WebSocket 信令通道连接状态变化事件
   * - ‘DISCONNECTED’：连接断开
   * - ‘CONNECTING’：正在连接中
   * - ‘RECONNECTING’：自动重连中
   * - ‘CONNECTED’：已连接
   * @default 'connection-state-changed'
   * @memberof module:Event
   * @example
   * client.on('connection-state-changed', event => {
   *   const prevState = event.prevState;
   *   const curState = event.state;
   * });
   */
  CONNECTION_STATE_CHANGED: 'connection-state-changed',
  /**
   * @description 远端用户进房通知，只有主动推流的远端用户进房才会收到该通知。
   *
   * **注意：**
   * - 目前 `peer-join/peer-leave` 事件尚不是严格意义上的用户进退房通知，房间内成员状态管理请使用腾讯云 {@link https://cloud.tencent.com/product/im|WebIM SDK} 或其他信令机制。
   * @default 'peer-join'
   * @memberof module:Event
   * @example
   * client.on('peer-join', event => {
   *   const userId = event.userId;
   * });
   */
  PEER_JOIN: 'peer-join',
  /**
   * @description 远端用户退房通知，只有主动推流的远端用户退房才会收到该通知。
   * @default 'peer-leave'
   * @memberof module:Event
   * @example
   * client.on('peer-leave', event => {
   *   const userId = event.userId;
   * });
   */
  PEER_LEAVE: 'peer-leave',
  /**
   * @description 远端用户禁用音频通知。
   * @default 'mute-audio'
   * @memberof module:Event
   * @example
   * client.on('mute-audio', event => {
   *   const userId = event.userId;
   * });
   */
  MUTE_AUDIO: 'mute-audio',
  /**
   * @description 远端用户禁用视频通知。
   * @default 'mute-video'
   * @memberof module:Event
   * @example
   * client.on('mute-video', event => {
   *   const userId = event.userId;
   * });
   */
  MUTE_VIDEO: 'mute-video',
  /**
   * @description 远端用户启用音频通知。
   * @default 'unmute-audio'
   * @memberof module:Event
   * @example
   * client.on('unmute-audio', event => {
   *   const userId = event.userId;
   * });
   */
  UNMUTE_AUDIO: 'unmute-audio',
  /**
   * @description 远端用户启用视频通知。
   * @default 'unmute-video'
   * @memberof module:Event
   * @example
   * client.on('unmute-video', event => {
   *   const userId = event.userId;
   * });
   */
  UNMUTE_VIDEO: 'unmute-video',
  /**
   * @description 用户被踢出房间通知，被踢原因有：
   * - 同名用户登录，**注意：同名用户同时登陆是不允许的行为，可能会导致双方音视频通话异常，此乃应用业务逻辑错误！**
   * - 被账户管理员主动踢出房间
   *
   * **建议：**
   * - 房间内成员状态管理请使用腾讯云 {@link https://cloud.tencent.com/product/im|WebIM SDK} 或其他信令机制。
   * @default 'client-banned'
   * @memberof module:Event
   * @example
   * client.on('client-banned', error => {
   *   console.error('client-banned observed: ' + error);
   *   // 退出刷新页面
   * });
   */
  CLIENT_BANNED: 'client-banned',
  /**
   * @description 客户端错误事件，当出现不可恢复错误后 Client 会通过该事件上报。
   * @default 'error'
   * @memberof module:Event
   * @example
   * client.on('error', error => {
   *   console.error('client error observed: ' + error);
   *   const errorCode = error.getCode();
   *   // 请根据错误码列表(https://cloud.tencent.com/document/product/647/34342)查看错误类型。
   *   // 当出现客户端错误后，请调用 Client.leave() 退房并尝试通过 Client.join() 重新进房恢复通话。
   * });
   */
  ERROR: 'error'
};

/**
 * **Stream 事件列表**
 * Stream.on() 监听的事件。
 *
 * @name module:Event.StreamEvent
 */
const StreamEvent = {
  /**
   * @description Audio/Video Player 状态变化事件
   * App 可根据状态变化来更新 UI，比如，通过监听 video player 状态变化来关闭或打开遮板。
   * - 'PLAYING'：开始播放
   * - 'PAUSED'：暂停播放
   *   - event.reason 为 ‘pause’ 时，由 \<audio\>/\<video\> element 的 pause 事件触发，如果在 Stream.play() 后
   * App 移动相关 div 容器，在 Chrome M70 版本以下的浏览器上可能会触发该事件，此时您需要调用 Stream.resume() 继续播放。
   *   - event.reason 为 ‘mute’ 时，表明音视频轨道暂时未能提供数据
   *
   * - 'STOPPED'：停止播放
   *
   * reason 状态变化原因：
   * - ‘playing’：开始播放，详见事件 {@link https://developer.mozilla.org/en-US/docs/Web/API/HTMLMediaElement/playing_event | HTMLMediaElement.playing_event}
   * - ‘mute’：音视频轨道暂时未能提供数据，详见事件 {@link https://developer.mozilla.org/en-US/docs/Web/API/MediaStreamTrack/mute_event | MediaStreamTrack.mute_event}
   * - ‘unmute’：音视频轨道恢复提供数据，详见事件 {@link https://developer.mozilla.org/en-US/docs/Web/API/MediaStreamTrack/unmute_event | MediaStreamTrack.unmute_event}
   * - ‘ended’：音视频轨道已被关闭
   * @default 'player-state-changed'
   * @memberof module:Event
   * @example
   * stream.on('player-state-changed', event => {
   *   console.log(`${event.type} player is ${event.state} because of ${event.reason}`);
   * });
   */
  PLAYER_STATE_CHANGED: 'player-state-changed',
  /**
   * @description 本地屏幕分享停止事件通知，仅对本地屏幕分享流有效。
   * @default 'screen-sharing-stopped'
   * @memberof module:Event
   * @example
   * stream.on('screen-sharing-stopped', () => {
   *   console.log('screen sharing was stopped');
   * });
   */
  SCREEN_SHARING_STOPPED: 'screen-sharing-stopped'
};

const PlayerEvent = {
  PLAYER_STATE_CHANGED: 'player-state-changed'
};

export { ConnectionEvent, ClientEvent, StreamEvent, PlayerEvent };
