/* eslint-disable require-jsdoc */
import log from '../utils/log/logger';
import * as env from '../utils/environment';

const getUserMedia = async function(options) {
  if (!navigator.mediaDevices) {
    log.error('navigator.mediaDevices is undefined');
    return;
  }
  const constraints = buildConstraints(options);
  log.info('getUserMedia with contraints: ' + JSON.stringify(constraints));
  return await navigator.mediaDevices.getUserMedia(constraints);
};

function buildConstraints(options) {
  let audioConstraints = {};
  if (!options.audio) {
    audioConstraints = false;
  } else if (typeof options.microphoneId !== 'undefined') {
    audioConstraints = {
      deviceId: { exact: options.microphoneId },
      sampleRate: options.sampleRate,
      channelCount: options.channelCount
    };
  } else {
    audioConstraints = { sampleRate: options.sampleRate, channelCount: options.channelCount };
  }

  let videoConstraints = {};
  if (typeof options.facingMode !== 'undefined' && options.video) {
    videoConstraints = {
      facingMode: options.facingMode,
      width: options.width,
      height: options.height,
      frameRate: options.frameRate
    };
  } else if (typeof options.cameraId !== 'undefined' && options.video) {
    videoConstraints = {
      deviceId: { exact: options.cameraId },
      width: options.width,
      height: options.height,
      frameRate: options.frameRate
    };
  } else {
    if (!options.video) {
      videoConstraints = false;
    } else {
      if (typeof options.width !== 'undefined') {
        videoConstraints = {
          width: options.width,
          height: options.height,
          frameRate: options.frameRate
        };
      } else {
        videoConstraints = true;
      }
    }
  }
  return { audio: audioConstraints, video: videoConstraints };
}

export default getUserMedia;
