const videoProfileMap = new Map();
/*
 * | 视频Profile | 分辨率（宽 x 高）| 帧率（fps）| 码率（kbps）|
 * | :---       | :---           | :---      | :---      |
 * | 120p       | 160 x 120      | 15        | 200        |
 * | 180p       | 320 x 180      | 15        | 350        |
 * | 240p       | 320 x 240      | 15        | 400       |
 * | 360p       | 640 x 360      | 15        | 800       |
 * | 480p       | 640 x 480      | 15        | 900       |
 * | 720p       | 1280 x 720     | 15        | 1500      |
 * | 1080p      | 1920 x 1080    | 15        | 2000      |
 * | 1440p      | 2560 x 1440    | 30        | 4860      |
 * | 4K         | 3840 x 2160    | 30        | 9000      |
 */
videoProfileMap.set('120p', { width: 160, height: 120, frameRate: 15, bitrate: 200 });
videoProfileMap.set('180p', { width: 320, height: 180, frameRate: 15, bitrate: 350 });
videoProfileMap.set('240p', { width: 320, height: 240, frameRate: 15, bitrate: 400 });
videoProfileMap.set('360p', { width: 640, height: 360, frameRate: 15, bitrate: 800 });
videoProfileMap.set('480p', { width: 640, height: 480, frameRate: 15, bitrate: 900 });
videoProfileMap.set('720p', { width: 1280, height: 720, frameRate: 15, bitrate: 1500 });
videoProfileMap.set('1080p', { width: 1920, height: 1080, frameRate: 15, bitrate: 2000 });
videoProfileMap.set('1440p', { width: 2560, height: 1440, frameRate: 30, bitrate: 4860 });
videoProfileMap.set('4K', { width: 3840, height: 2160, frameRate: 30, bitrate: 9000 });

const screenProfileMap = new Map();
screenProfileMap.set('480p', { width: 640, height: 480, frameRate: 5, bitrate: 900 });
