import { version } from '../../package.json';
import * as env from '../utils/environment';

// use Tencent's specific env name to avoid naming collisions.
const envName = 'trtc_env';

const query = function(name) {
  const match = window.location.search.match(new RegExp('(\\?|&)' + name + '=([^&]*)(&|$)'));
  return !match ? '' : decodeURIComponent(match[2]);
};

export const getEnv = function() {
  return query(envName);
};

export const getUrl = function(url) {
  let finalUrl = url;
  const env = query(envName);
  if (env) {
    finalUrl = 'wss://' + env + '.rtc.qq.com:8687';
  }
  return finalUrl;
};

export const getNumOfPattern = function(string, pattern) {
  let count = 0;
  let pos = string.indexOf(pattern);
  while (pos > -1) {
    count += 1;
    pos = string.indexOf(pattern, pos + 1);
  }
  return count;
};

