function getTerminalType() {
  if (env.IS_ANDROID) {
    return 4;
  } else if (env.IS_IPHONE) {
    return 2;
  } else if (env.IS_IPAD) {
    return 3;
  } else if (env.IS_MAC) {
    return 12;
  } else if (env.IS_WIN) {
    return 5;
  } else if (env.IS_LINUX) {
    return 13;
  }
  // unknown
  return 1;
}

function getOSType() {
  if (env.IS_ANDROID) {
    return 'Android';
  } else if (env.IS_IPHONE) {
    return 'iPhone';
  } else if (env.IS_IPAD) {
    return 'iPad';
  } else if (env.IS_MAC) {
    return 'Mac';
  } else if (env.IS_WIN) {
    return 'Windows';
  } else if (env.IS_LINUX) {
    return 'Linux';
  }
  return 'unknown';
}

export const getSysInfo = function() {
  // **NOTE** below code are copied from old sdk with minimal changes.
  let connectionType = 0;
  // effectiveType is slow-2g/2g/3g/4g
  if (navigator && navigator.connection && navigator.connection.effectiveType) {
    if (navigator.connection.effectiveType === '4g') {
      connectionType = 1; // assume it's wifi
    } else {
      connectionType = 4; // otherwise cellular
    }
  }
  // type is almost not implemented in browser
  if (navigator && navigator.connection && navigator.connection.type) {
    if (navigator.connection.type === 'wifi') {
      connectionType = 1;
    } else if (navigator.connection.type === 'cellular') {
      connectionType = 4;
    }
  }

  return {
    AbilityOption: {
      GeneralLimit: {
        CPULimit: {
          uint32_CPU_num: String(navigator.hardwareConcurrency || 0), //CPU核心数   ok
          str_CPU_name: String(navigator.platform),
          uint32_CPU_maxfreq: String(0),
          model: '',
          uint32_total_memory: String(0)
        },
        uint32_terminal_type: String(getTerminalType()), //终端类型，使用enum TERMINAL_TYPE
        uint32_device_type: String(0), //机型、设备类型(比如iphone4、iphone5s)，只针对移动终端，pc不用填写
        str_os_verion: getOSType(), //操作系统类型、版本
        uint32_link_type: String(1), //客户端接口机连接类型，1表示UDP，2表示TCP，0未知（旧版本没有上报此字段）
        str_client_version: version,
        uint32_net_type: String(connectionType),
        ua: navigator.userAgent,
        version: ''
      }
      // below fileds are not used at all
      //   VideoLimit: {
      //     CameraLimit: {
      //       str_camera_auth_name: '',
      //       uint32_video_src_type: 1
      //     },
      //     uint32_screen_width: String(window.screen.width), //屏幕宽
      //     uint32_screen_height: String(window.screen.height) //屏幕高
      //   },
      //   SpeciParam: {
      //     SpeciVidParam: {
      //       uint32_spcivid_proto: String(3), //角色类型
      //       uint32_spcivid_width: String(640),
      //       uint32_spcivid_height: String(368),
      //       uint32_spcivid_fps: String(20)
      //     },
      //     SpeciAudParam: {
      //       uint32_spciaud_fs: String(48000),
      //       uint32_spciaud_ch: String(2)
      //     }
      //   }
    }
  };
};

export const isPlainObject = function(value) {
  // 判断类型不是 Object 的
  if (
    !value ||
    typeof value !== 'object' ||
    Object.prototype.toString.call(value) != '[object Object]'
  ) {
    return false;
  }
  // 返回指定对象的原型 判断对象有没有原型，没有原型的对象算纯粹对象
  var proto = Object.getPrototypeOf(value);
  if (proto === null) {
    // Object.create(null) 的情况
    return true;
  }
  // 最后判断是不是通过 "{}" 或 "new Object" 方式创建的对象
  // 如果 proto 有 constructor属性，Ctor 的值就为 proto.constructor，
  // 原型的 constructor 属性指向关联的构造函数
  var Ctor = Object.prototype.hasOwnProperty.call(proto, 'constructor') && proto.constructor;
  return (
    typeof Ctor == 'function' &&
    // 用于检测构造函数Ctor的 prototype 属性是否出现在某个实例对象(Ctor)的原型链上。
    Ctor instanceof Ctor &&
    // 如果 Ctor 类型是  "function" ，并且调用Function.prototype.toString 方法后得到的字符串 与 "function Object() { [native code] }" 这样的字符串相等就返回true
    // 用来区分 自定义构造函数和 Object 构造函数
    Function.prototype.toString.call(Ctor) === Function.prototype.toString.call(Object)
  );
};
