import getUserMedia from './get-user-media';
import getDisplayMedia from './get-display-media';
import RtcError from '../common/rtc-error';
import ErrorCode from '../common/error-code';
import Stream from './stream';
import { StreamEvent } from './event';
import * as env from '../utils/environment';
import { videoProfileMap, screenProfileMap } from './video-profile';
import { logEvent } from '../utils/event-log';
import audioProfileMap from './audio-profile';
import { addEvent, getEvents, EventId } from '../utils/event-hub';
import { getCurrentTime } from '../utils/time';

/**
 * LocalStream 本地音视频流，通过 {@link TRTC.createStream createStream()} 创建。
 *
 * @class LocalStream
 * @extends {Stream}
 */
class LocalStream extends Stream {
  constructor(options) {
    const extra = { isRemote: false, type: 'local' };
    let opt = { ...options, ...extra };
    if (options.screen) {
      // mirror is invalid in screenshare
      opt.mirror = false;
    }
    super(opt);

    // local stream creation properties
    this.video_ = options.video;
    this.audio_ = options.audio;
    // optional parameters for specified deviceId
    this.cameraId_ = options.cameraId;
    this.facingMode_ = options.facingMode;
    this.microphoneId_ = options.microphoneId;
    // Audio/Video tracks (MediaStreamTrack) are provided by app
    this.videoSource_ = options.videoSource;
    this.audioSource_ = options.audioSource;
    // whether want screen share or not
    this.screen_ = options.screen;
    this.screenSource_ = options.screenSource;

    this.audioProfile_ = {
      sampleRate: 48000,
      channelCount: 1,
      bitrate: 40 // kbps
    };

    // video profile for local stream
    this.videoProfile_ = {
      // parameters for getUserMedia
      width: 640,
      height: 480,
      frameRate: 15,
      bitrate: 900
    };

    // video profile for screen sharing
    this.screenProfile_ = {
      width: 1920,
      height: 1080,
      frameRate: 5,
      bitrate: 1600
    };

    this.videoBitrate_ = this.screen_ ? 1600 : 900; // kbps
  }

  /**
   * 初始化本地音视频流对象<br>
   * <br>
   * 错误信息：
   * 
   * | 错误名                   | 描述                                                         | 处理建议                                                     |
   * | ------------------------ | ------------------------------------------------------------ | ------------------------------------------------------------ |
   * | **NotFoundError**        | 找不到满足请求参数的媒体类型（包括：音频、视频、屏幕分享）。例如：PC 没有摄像头，但是请求浏览器获取视频流，则会报此错误。 | 建议在通话开始前引导用户检查通话所需的摄像头或麦克风等外设，若没有摄像头且需要进行语音通话，可在 `TRTC.createStream({ audio: true, video: false })` 指明仅采集麦克风。   |
   * | **NotAllowedError**      | 用户拒绝了当前的浏览器实例的访问音频、视频、屏幕分享请求。   | 提示用户不授权摄像头/麦克风访问无法进行音视频通话                                           |
   * | **NotReadableError**     | 尽管用户已经授权使用相应的设备，但是由于操作系统上某个硬件、浏览器或者网页层面发生的错误导致设备无法被访问。 | 根据浏览器的报错信息处理，并提示用户：<br />**“暂时无法访问摄像头/麦克风，请确保当前没有其他应用请求访问摄像头/麦克风，并重试”** |
   * | **OverConstrainedError** | cameraId/microphoneId 参数的值无效                           | 确保 cameraId/microphoneId 传值正确且有效                    |
   * | **AbortError**           | 由于某些未知原因导致设备无法被使用                           |                                                              |
   * 
   * 参考：[getUserMedia 异常](<https://developer.mozilla.org/zh-CN/docs/Web/API/MediaDevices/getUserMedia#异常>) 和 [getDisplayMedia 异常](<https://developer.mozilla.org/zh-CN/docs/Web/API/MediaDevices/getDisplayMedia#异常>)
   * @example
   * // 例如：处理 NotReadableError 错误
   * localStream.initialize().then(() => {
   *   // 本地流初始化成功，发布本地流
   *   client.publish(localStream).then(() => {
   *   // 本地流发布成功
   *   })
   * }).catch((error) => {
   *   // 本地流初始化失败
   *    switch (error.name) {
   *      case 'NotReadableError':
   *        // 提示用户：暂时无法访问摄像头/麦克风，请确保当前没有其他应用请求访问摄像头/麦克风，并重试。
   *        break;
   *      default:
   *        console.error(error);
   *        break;
   *    }
   * })
   * @returns {Promise}
   * @memberof LocalStream
   */
  initialize() {
    return new Promise((resolve, reject) => {
      if (typeof this.audio_ !== 'undefined') {
        if (this.screen_) {
          this.log_.info('initialize stream audio: ' + this.audio_ + ' screen: ' + this.screen_);
          getDisplayMedia({
            audio: this.audio_,
            microphoneId: this.microphoneId_,
            screenSource: this.screenSource_,
            width: this.screenProfile_.width,
            height: this.screenProfile_.height,
            frameRate: this.screenProfile_.frameRate,
            sampleRate: this.audioProfile_.sampleRate,
            channelCount: this.audioProfile_.channelCount
          })
            .catch(error => {
              logEvent({
                eventType: 'stream-initialize',
                kind: 'getDisplayMedia',
                result: 'failed'
              });
              this.log_.error('getDisplayMedia error observed ' + error);
              reject(error);
            })
            .then(stream => {
              this.setMediaStream(stream);
              this.setHasAudio(this.audio_);
              this.setHasVideo(true);
              this.listenForScreenSharingStopped(this.getVideoTrack());
              // set default content hint for screencast
              this.setVideoContentHint('detail');

              logEvent({
                eventType: 'stream-initialize',
                kind: 'getDisplayMedia',
                result: 'success'
              });
              return resolve();
            });
        } else {
          this.log_.info('initialize stream audio: ' + this.audio_ + ' video: ' + this.video_);
          getUserMedia({
            audio: this.audio_,
            video: this.video_,
            facingMode: this.facingMode_,
            cameraId: this.cameraId_,
            microphoneId: this.microphoneId_,
            width: this.videoProfile_.width,
            height: this.videoProfile_.height,
            frameRate: this.videoProfile_.frameRate,
            sampleRate: this.audioProfile_.sampleRate,
            channelCount: this.audioProfile_.channelCount
          })
            .catch(error => {
              logEvent({
                eventType: 'stream-initialize',
                kind: 'getUserMedia',
                result: 'failed'
              });
              this.log_.error('getUserMedia error observed ' + error);
              // FIXME: detailed error report?
              reject(error);
            })
            .then(stream => {
              this.setMediaStream(stream);
              this.setHasAudio(this.audio_);
              this.setHasVideo(this.video_);
              this.log_.debug(
                'gotStream hasAudio: ' + this.hasAudio_ + ' hasVideo: ' + this.hasVideo_
              );

              logEvent({
                eventType: 'stream-initialize',
                kind: 'getUserMedia',
                result: 'success'
              });
              return resolve();
            });
        }
      } else {
        const stream = new MediaStream();
        if (typeof this.audioSource_ !== 'undefined') {
          stream.addTrack(this.audioSource_);
          this.setHasAudio(true);
        }
        if (typeof this.videoSource_ !== 'undefined') {
          stream.addTrack(this.videoSource_);
          this.setHasVideo(true);
        }
        this.setMediaStream(stream);
        logEvent({
          eventType: 'stream-initialize',
          kind: 'custom',
          result: 'success'
        });
        return resolve();
      }
    });
  }

  listenForScreenSharingStopped(track) {
    // listen for track's ended event
    track.addEventListener('ended', event => {
      this.log_.info('screensharing was stopped because the video track is ended');
      this.emitter_.emit(StreamEvent.SCREEN_SHARING_STOPPED);
    });
  }

  /**
   * @override
   * @memberof LocalStream
   */
  muteAudio() {
    const result = super.muteAudio();
    if (result) {
      this.log_.info('localStream mute audio');
      const connection = this.getConnection();
      if (connection) {
        connection.setMutedState('audio', true);

        const uid = connection.getUserId();
        const tid = connection.getTinyId();
        addEvent(uid, {
          eventId: EventId.MUTE_AUDIO,
          eventDesc: 'mute local audio track',
          timestamp: getCurrentTime(),
          userId: uid,
          tinyId: tid
        });
      }
    }

    return result;
  }

  /**
   * @override
   * @memberof LocalStream
   */
  muteVideo() {
    this.log_.info('localStream mute video');
    const result = super.muteVideo();
    // always set muted state for video even if there's no video track in place
    // this is used to simulate a mute
    if (result) {
      const connection = this.getConnection();
      if (connection) {
        connection.setMutedState('video', true);

        const uid = connection.getUserId();
        const tid = connection.getTinyId();
        addEvent(uid, {
          eventId: EventId.MUTE_VIDEO,
          eventDesc: 'mute local video track',
          timestamp: getCurrentTime(),
          userId: uid,
          tinyId: tid
        });
      }
    }
    return result;
  }

  /**
   * @override
   * @memberof LocalStream
   */
  unmuteAudio() {
    const result = super.unmuteAudio();
    if (result) {
      this.log_.info('localStream unmute audio');
      const connection = this.getConnection();
      if (connection) {
        connection.setMutedState('audio', false);

        const uid = connection.getUserId();
        const tid = connection.getTinyId();
        addEvent(uid, {
          eventId: EventId.UNMUTE_AUDIO,
          eventDesc: 'unmute local audio track',
          timestamp: getCurrentTime(),
          userId: uid,
          tinyId: tid
        });
      }
    }
    return result;
  }

  /**
   * @override
   * @memberof LocalStream
   */
  unmuteVideo() {
    this.log_.info('localStream unmute video');
    const result = super.unmuteVideo();
    if (result) {
      const connection = this.getConnection();
      if (connection) {
        connection.setMutedState('video', false);

        const uid = connection.getUserId();
        const tid = connection.getTinyId();
        addEvent(uid, {
          eventId: EventId.UNMUTE_VIDEO,
          eventDesc: 'unmute local video track',
          timestamp: getCurrentTime(),
          userId: uid,
          tinyId: tid
        });
      }
    }
    return result;
  }

  /**
   * 设置音频 Profile
   * <br>
   * 该方法需要在调用 {@link LocalStream#initialize initialize()} 之前调用。
   *
   * @param {string} profile 音频 Profile
   * | 音频 Profile | 采样率 | 声道 | 码率 (kbps) |
   * | :---        | :---  | :--- | :---       |
   * | standard    | 48000 | 单声道| 40         |
   * | high        | 48000 | 单声道| 128        |
   * 默认使用 `standard`。
   *
   * @memberof LocalStream
   * @example
   * localStream.setAudioProfile('high');
   * localStream.initialize().then(() => {
   *   // localStream was initialized success
   * });
   */
  setAudioProfile(profile) {
    let p;
    if (typeof profile === 'object') {
      p = profile;
    } else {
      p = audioProfileMap.get(profile);
      if (p === undefined) {
        p = audioProfileMap.get('standard');
      }
    }
    this.log_.info('setAudioProfile: ' + JSON.stringify(p));
    this.audioProfile_ = p;
  }

  /**
   * 设置视频 Profile<br>
   * <br>
   * 该方法需要在调用 {@link LocalStream#initialize initialize()} 之前调用。
   *
   * @param {string|object} profile 视频 Profile
   * | 视频 Profile | 分辨率（宽 x 高）| 帧率（fps）| 码率（kbps）|
   * | :---       | :---           | :---      | :---      |
   * | 120p       | 160 x 120      | 15        | 200        |
   * | 180p       | 320 x 180      | 15        | 350       |
   * | 240p       | 320 x 240      | 15        | 400       |
   * | 360p       | 640 x 360      | 15        | 800       |
   * | 480p       | 640 x 480      | 15        | 900       |
   * | 720p       | 1280 x 720     | 15        | 1500      |
   * | 1080p      | 1920 x 1080    | 15        | 2000      |
   * | 1440p      | 2560 x 1440    | 30        | 4860      |
   * | 4K         | 3840 x 2160    | 30        | 9000      |
   * - 由于设备和浏览器的限制，视频分辨率不一定能够完全匹配，在这种情况下，浏览器会自动调整分辨率使其接近 Profile 对应的分辨率。
   * - 如果以上视频 Profile 不符合您的要求，您可以设置自定义 Profile。
   * - 默认使用 `480p`。
   * @memberof LocalStream
   * @example
   * // 使用预定义Profile设置
   * localStream.setVideoProfile('480p');
   * localStream.initialize().then(() => {
   *   // local stream was initialized successfully.
   * });
   *
   * // 使用自定义视频Profile设置
   * localStream.setVideoProfile({
   *   width: 360, // 视频宽度
   *   height: 360, // 视频高度
   *   frameRate: 10, // 帧率
   *   bitrate: 400 // 比特率 kbps
   * });
   * localStream.initialize().then(() => {
   *   // local stream was initialized successfully.
   * });
   */
  setVideoProfile(profile) {
    let p;
    if (typeof profile === 'object') {
      p = profile;
    } else {
      p = videoProfileMap.get(profile);
      if (p === undefined) {
        p = videoProfileMap.get('480p');
      }
    }
    this.log_.info('setVideoProfile ' + JSON.stringify(p));
    this.videoProfile_ = p;
    this.videoBitrate_ = p.bitrate;
  }

  getVideoBitrate() {
    return this.videoBitrate_;
  }

  getAudioBitrate() {
    return this.audioProfile_.bitrate;
  }

  /**
   * 设置屏幕分享 Profile<br>
   * <br>
   * 该方法需要在调用 {@link LocalStream#initialize initialize()} 之前调用。
   *
   * @param {string} profile screen profile
   * | 屏幕 Profile | 分辨率（宽 x 高）| 帧率（fps）| 码率 (kbps) |
   * | :---       | :---           | :---      | :---        |
   * | 480p       | 640 x 480      | 5         | 900         |
   * | 480p_2     | 640 x 480      | 30        | 1000        |
   * | 720p       | 1280 x 720     | 5         | 1200        |
   * | 720p_2     | 1280 x 720     | 30        | 3000        |
   * | 1080p      | 1920 x 1080    | 5         | 1600        |
   * | 1080p_2    | 1920 x 1080    | 30        | 4000        |
   * - 屏幕分享默认使用 `1080p`。
   * - 若以上 Profile 不能满足您的业务需求，您也可以指定自定义的分辨率、帧率和码率。
   * @memberof LocalStream
   * @example
   * // 指定 Profile
   * localStream.setScreenProfile('1080p');
   * localStream.initialize().then(() => {
   *   // local stream was initialized successfully
   * });
   *
   * // 指定自定义分辨率、帧率和码率
   * localStream.setScreenProfile({ width: 1920, height: 1080, frameRate: 5, bitrate: 1600 });
   * localStream.initialize().then(() => {
   *   // local stream was initialized successfully
   * });
   */
  setScreenProfile(profile) {
    let p = profile;
    if (typeof profile !== 'object') {
      p = screenProfileMap.get(profile);
      if (p === undefined) {
        p = screenProfileMap.get('1080p');
      }
    }
    this.log_.info('setScreenProfile ' + JSON.stringify(profile));
    this.screenProfile_ = p;
    this.videoBitrate_ = p.bitrate;
  }

  // package
  getVideoProfile() {
    return this.screen_ ? this.screenProfile_ : this.videoProfile_;
  }

  getAudioProfile() {
    return this.audioProfile_;
  }

  /**
   * 设置视频内容提示，主要用于提升在不同场景下的视频编码质量。
   * <br>
   * 该方法需要在调用 {@link LocalStream#initialize initialize()} 成功之后调用。
   * @param {string} hint 内容提示，参考 {@link https://www.w3.org/TR/mst-content-hint/ MediaStreamTrack.contentHint}
   * - 'motion'：本地流视频内容为从摄像头采集的内容、电影或者视频游戏等。
   * - 'detail'：本地视频内容为 ppt、带有文本内容、绘画或艺术线条的网页。一般屏幕分享默认使用这个提示。
   * - 'text'：本地视频内容主要是含有文本的 ppt 或网页等。
   * @memberof LocalStream
   * @example
   * const localStream = TRTC.createStream({ audio: false, screen: true });
   * localStream.initialize().then(() => {
   *   // 若当前屏幕分享的主要内容是一般网页
   *   localStream.setVideoContentHint('detail');
   *
   *   // 若当前屏幕分享的主要内容中含有大量的文字
   *   // localStream.setVideoContentHint('text');
   *
   *   client.publish(localStream);
   * });
   */
  setVideoContentHint(hint) {
    const videoTrack = this.getVideoTrack();
    if (videoTrack && 'contentHint' in videoTrack) {
      this.log_.info('set video track contentHint to: ' + hint);
      videoTrack.contentHint = hint;
      if (videoTrack.contentHint !== hint) {
        this.log_.warn('Invalid video track contentHint: ' + hint);
      }
    }
  }

  /**
   * 切换媒体输入设备<br>
   * <br>
   * 调用该方法可更换本地流的媒体输入设备：
   * - 音频输入设备，如麦克风。
   * - 视频输入设备，如摄像头。
   *
   * <br>
   * 注意：该方法仅适用于从摄像头和麦克风采集音视频的本地流。即：屏幕分享流不支持调用该接口<br>
   * <br>
   * 若该本地流已经被发布，该方法会自动更新发往远端的音视频流，此时，远端会收到 `Client.on('stream-updated')` 事件通知。
   *
   * @param {string} type 媒体类型
   * - ‘audio’ 音频
   * - ‘video’ 视频
   * @param {string} deviceId 设备标识
   * - 摄像头设备标识通过 {@link TRTC.getCameras getCameras()} 获取。<br>
   * 在移动设备上，可以通过设置 deviceId 为 'user' 和 'environment' 来切换前置和后置摄像头。
   * - 麦克风设备标识通过 {@link TRTC.getMicrophones getMicrophones()} 获取。
   *
   * @returns {Promise}
   * @memberof LocalStream
   * @example
   * // ...
   * // 发布本地流
   * client.publish(localStream);
   *
   * // ...
   *
   * TRTC.getCameras().then(devices => {
   *   // 切换camera设备
   *   localStream.switchDevice('video', devices[0].deviceId).then(() => {
   *     // camera切换成功
   *   });
   * });
   *
   */
  async switchDevice(type, deviceId) {
    if (this.screen_) {
      throw new RtcError({
        code: ErrorCode.INVALID_OPERATION,
        message: 'switch device is not supported in screen sharing'
      })
    }

    if (
      (type === 'audio' && this.microphoneId_ === deviceId) ||
      (type === 'video' && this.cameraId_ === deviceId) ||
      (this.audioSource_ || this.videoSource_)
    ) {
      // same deviceId or audioSource/videoSource is used, bail out
      return;
    }

    if (type === 'audio') {
      this.microphoneId_ = deviceId;
      // ensure audio is being requested
      if (!this.audio_) {
        this.audio_ = true;
      }
    }
    if (type === 'video') {
      if (deviceId === 'user' || deviceId === 'environment') {
        this.facingMode_ = deviceId;
      } else {
        this.cameraId_ = deviceId;
      }
      // ensure video is being requested
      if (!this.video_) {
        this.video_ = true;
      }
    }
    if (!this.getMediaStream()) {
      // not initialized, so let initialize take it effect.
      return;
    }

    this.log_.info('switchDevice ' + type + ' to: ' + deviceId);

    if (type === 'video' && (env.IS_ANDROID || env.IS_IOS)) {
      // on mobile platform we have to release the camera before switching to another
      // camera otherwise it may get stuck.
      const videoTrack = this.getVideoTrack();
      if (videoTrack) {
        videoTrack.stop();
      }
    }

    const stream = await getUserMedia({
      audio: this.audio_,
      video: this.video_,
      facingMode: this.facingMode_,
      cameraId: this.cameraId_,
      microphoneId: this.microphoneId_,
      width: this.videoProfile_.width,
      height: this.videoProfile_.height,
      frameRate: this.videoProfile_.frameRate,
      sampleRate: this.audioProfile_.sampleRate,
      channelCount: this.audioProfile_.channelCount
    });
    this.setMediaStream(stream);

    const connection = this.getConnection();
    if (connection) {
      // this local stream has been published, so replace the tracks in RTCPeerConnection
      await connection.replaceStream(stream);

      const uid = connection.getUserId();
      const tid = connection.getTinyId();
      let eventId = EventId.SWITCH_CAMERA;
      let eventDesc = 'switch camera';
      if (type === 'audio') {
        eventId = EventId.SWITCH_MICROPHONE;
        eventDesc = 'switch microphone';
      }
      addEvent(uid, {
        eventId,
        eventDesc,
        timestamp: getCurrentTime(),
        userId: uid,
        tinyId: tid
      });
    }

    this.log_.info('swichDevice - restart audio/video player');
    super.restartAudio();
    super.restartVideo();

    return;
  }

  /**
   * 添加音频或视频轨道<br>
   * <br>
   * 调用该方法将音频或视频轨道添加到本地流，若该本地流已经被发布，则会自动更新发往远端的音视频流，此时远端会收到 `Client.on('stream-updated')` 事件通知。<br>
   * <br>
   * 请注意，一个 Stream 对象中最多只能同时包含一路音频轨道和一路视频轨道。如果你想要更换同类型的轨道，请使用 {@link LocalStream#replaceTrack replaceTrack()}。
   * <br>
   * <br>
   * 新的音视频轨道可以通过 {@link TRTC.createStream createStream()}/{@link Stream#getAudioTrack getAudioTrack()}|{@link Stream#getVideoTrack getVideoTrack()} 获取，
   * 或者直接通过 {@link https://developer.mozilla.org/en-US/docs/Web/API/MediaDevices/getUserMedia getUserMedia()}、{@link https://developer.mozilla.org/en-US/docs/Web/API/HTMLMediaElement/captureStream captureStream()} 获取。
   *
   * **Note**
   * - 在增加视频轨道时，要求视频分辨率跟 {@link LocalStream#setVideoProfile setVideoProfile()} 设置保持一致，否则会抛出异常。
   *
   * @throws RtcError
   * @param {MediaStreamTrack} track 音频或视频轨道
   * @memberof LocalStream
   * @example
   *
   * const localStream = TRTC.createStream({ userId, audio: true, video: false });
   * localStream.initialize().then(() => {
   *   // 分布本地流（只有从麦克风采集的音频流）
   *   client.publish(localStream);
   * });
   *
   * // ...
   *
   * // 开启视频通话
   * const videoStream = TRTC.createStream({ userId, audio: false, video: true });
   * videoStream.initialize().then(() => {
   *   const videoTrack = videoStream.getVideoTrack();
   *   // 将从摄像头采集的视频轨道插入当前已发布的本地流对象LocalStream
   *   localStream.addTrack(videoTrack).then(() => {
   *     // 视频通话开启成功，远端流将会收到‘stream-updated’通知
   *   });
   * });
   * //
   */
  async addTrack(track) {
    const mediaStream = this.getMediaStream();
    if (!mediaStream) {
      throw new RtcError({
        code: ErrorCode.INVALID_OPERATION,
        message: 'the local stream is not initialized yet'
      });
    }
    // A LocalStream has at most one audio track and one video track.
    if (
      (track.kind === 'audio' && mediaStream.getAudioTracks().length > 0) ||
      (track.kind === 'video' && mediaStream.getVideoTracks().length > 0)
    ) {
      throw new RtcError({
        code: ErrorCode.INVALID_OPERATION,
        message: 'A Stream has at most one audio track and one video track'
      });
    }

    if (track.kind === 'video') {
      if ('getSettings' in MediaStreamTrack.prototype) {
        const settings = track.getSettings();
        if (
          settings.width !== this.videoProfile_.width ||
          settings.height !== this.videoProfile_.height
        ) {
          throw new RtcError({
            code: ErrorCode.INVALID_PARAMETER,
            message: `video resolution of the track (${settings.width} x ${settings.height}) shall be kept the same as the one defined via setVideoProfile(): ${this.videoProfile_.width} x ${this.videoProfile_.height}`
          });
        }
      }
    }

    mediaStream.addTrack(track);
    if (track.kind === 'audio') {
      this.setHasAudio(true);
    } else {
      this.setHasVideo(true);
    }
    const connection = this.getConnection();
    if (connection) {
      // the LocalStream has been published
      await connection.addTrack(track);

      const uid = connection.getUserId();
      const tid = connection.getTinyId();
      let eventId = EventId.PUBLISH_VIDEO;
      let eventDesc = 'add video track to current published stream';
      if (track.kind === 'audio') {
        eventId = EventId.PUBLISH_AUDIO;
        eventDesc = 'add audio track to current published stream';
      }
      addEvent(uid, {
        eventId,
        eventDesc,
        timestamp: getCurrentTime(),
        userId: uid,
        tinyId: tid
      });
    }
  }

  /**
   * 移除视频轨道<br>
   * <br>
   * 调用该方法会移除本地流中的视频轨道，若本地流已经被发布，则会自动更新发往远端的视频流，此时远端会收到 `Client.on('stream-updated')` 事件通知。<br>
   * <br>
   * 请注意，一个已经发布的Stream对象中至少要有一个媒体轨道，如果你想完全删除本地流中的音视频轨道，请直接通过 {@link Client#unpublish unpublish()} 取消发布，
   * 然后再通过 {@link LocalStream#close close()} 关闭本地流。<br>
   * **NOTE**
   * - 目前尚不支持移除音频轨道，若想禁用音频，可通过调用 {@link LocalStream#muteAudio muteAudio()} 实现。
   *
   * @param {MediaStreamTrack} track 视频轨道，track 通过 {@link Stream#getVideoTrack getVideoTrack()} 获取。
   * @memberof LocalStream
   * @throws RtcError
   * @example
   * // 关闭视频通话示例，对应addTrack接口的开启视频通话示例
   * const videoTrack = localStream.getVideoTrack();
   * if (videoTrack) {
   *   localStream.removeTrack(videoTrack).then(() => {
   *     // 关闭视频通话成功，停止videoTrack并释放摄像头资源
   *     videoTrack.stop();
   *   });
   * }
   */
  async removeTrack(track) {
    if (track.kind === 'audio') {
      throw new RtcError({
        code: ErrorCode.INVALID_PARAMETER,
        message: 'remove audio track is not supported'
      });
    }
    const mediaStream = this.getMediaStream();
    if (!mediaStream) {
      throw new RtcError({
        code: ErrorCode.INVALID_OPERATION,
        message: 'the local stream is not initialized yet'
      });
    }
    if (mediaStream.getTracks().indexOf(track) === -1) {
      throw new RtcError({
        code: ErrorCode.INVALID_PARAMETER,
        message: 'the track to be removed is not being publishing'
      });
    }
    mediaStream.removeTrack(track);
    if (track.kind === 'audio') {
      this.setHasAudio(false);
    } else {
      this.setHasVideo(false);
    }
    const connection = this.getConnection();
    if (connection) {
      // the LocalStream has been published
      await connection.removeTrack(track);

      const uid = connection.getUserId();
      const tid = connection.getTinyId();
      let eventId = EventId.UNPUBLISH_VIDEO;
      let eventDesc = 'remove video track from current published stream';
      if (track.kind === 'audio') {
        eventId = EventId.UNPUBLISH_AUDIO;
        eventDesc = 'remove audio track from current published stream';
      }
      addEvent(uid, {
        eventId,
        eventDesc,
        timestamp: getCurrentTime(),
        userId: uid,
        tinyId: tid
      });
    }
  }

  /**
   * 更换音频或视频轨道<br>
   * <br>
   * 调用该方法更换本地流中的同类型轨道，若本地流已经被发布，该方法会自动更新发往远端的音视频流，此时远端会收到 `Client.on('stream-updated')` 事件通知。<br>
   * <br>
   * 新的音视频轨道可以通过 {@link TRTC.createStream createStream()}/{@link Stream#getAudioTrack getAudioTrack()}|{@link Stream#getVideoTrack getVideoTrack()} 获取，
   * 或者直接通过 {@link https://developer.mozilla.org/en-US/docs/Web/API/MediaDevices/getUserMedia getUserMedia()}、{@link https://developer.mozilla.org/en-US/docs/Web/API/HTMLMediaElement/captureStream captureStream()} 获取。
   * <br>
   * <br>
   * **Note**:
   * - 如果需要更换媒体输入设备，推荐使用 {@link LocalStream#switchDevice switchDevice()}。
   * - 在更换视频轨道时，要求视频分辨率跟 {@link LocalStream#setVideoProfile setVideoProfile()} 设置保持一致，否则会抛出异常。
   * - 支持 Chrome 65+、Safari 浏览器。
   *
   * @param {MediaStreamTrack} track 音频或视频轨道
   * @memberof LocalStream
   * @throws RtcError
   */
  async replaceTrack(track) {
    const mediaStream = this.getMediaStream();
    if (!mediaStream) {
      throw new RtcError({
        code: ErrorCode.INVALID_OPERATION,
        message: 'the local stream is not initialized yet'
      });
    }
    if (
      (track.kind === 'audio' && mediaStream.getAudioTracks().length <= 0) ||
      (track.kind === 'video' && mediaStream.getVideoTracks().length <= 0)
    ) {
      throw new RtcError({
        code: ErrorCode.INVALID_PARAMETER,
        message: `try to replace ${track.kind} track but there's no previous ${track.kind} being published`
      });
    }
    if (track.kind === 'video') {
      if ('getSettings' in MediaStreamTrack.prototype) {
        const settings = track.getSettings();
        if (
          settings.width !== this.videoProfile_.width ||
          settings.height !== this.videoProfile_.height
        ) {
          throw new RtcError({
            code: ErrorCode.INVALID_PARAMETER,
            message: `video resolution of the track (${settings.width} x ${settings.height}) shall be kept the same as the one defined via setVideoProfile(): ${this.videoProfile_.width} x ${this.videoProfile_.height}`
          });
        }
      }
    }

    if (track.kind === 'audio') {
      mediaStream.removeTrack(mediaStream.getAudioTracks()[0]);
      mediaStream.addTrack(track);
      // restart audio player as the track has already been changed.
      super.restartAudio();
    } else {
      mediaStream.removeTrack(mediaStream.getVideoTracks()[0]);
      mediaStream.addTrack(track);
      // restart video player as the track has already been changed
      super.restartVideo();
    }
    const connection = this.getConnection();
    if (connection) {
      // the LocalStream has been published
      await connection.replaceTrack(track);

      const uid = connection.getUserId();
      const tid = connection.getTinyId();
      let eventId = EventId.REPLACE_VIDEO;
      let eventDesc = 'replace video track from current published stream';
      if (track.kind === 'audio') {
        eventId = EventId.REPLACE_AUDIO;
        eventDesc = 'replace audio track from current published stream';
      }
      addEvent(uid, {
        eventId,
        eventDesc,
        timestamp: getCurrentTime(),
        userId: uid,
        tinyId: tid
      });
    }
  }
  /**
   * @private
   * @memberof LocalStream
   */
  setAudioVolume(volume) {
    super.setAudioVolume(volume);
  }
}

export default LocalStream;
