import EventEmitter from 'eventemitter3';
import { PlayerEvent } from './event';
import SoundMeter from './soundmeter';
import RtcError from '../common/rtc-error';
import ErrorCode from '../common/error-code';

class AudioPlayer {
  constructor(options) {
    // Stream this player belong to
    this.stream_ = options.stream;
    this.userId_ = options.stream.getUserId();
    this.log_ = this.stream_.getIDLogger();
    // audio track to be played
    this.track_ = options.track;
    // the div container for appending <audio> element into
    this.div_ = options.div;
    this.muted_ = options.muted;

    // speaker output deviceId
    this.outputDeviceId_ = options.outputDeviceId;
    this.volume_ = options.volume;

    this.element_ = null;
    this.emitter_ = new EventEmitter();
    this.state_ = 'NONE';

    this.soundMeter_ = null;
  }

  async play() {
    const mediaStream = new MediaStream();
    mediaStream.addTrack(this.track_);
    const element = document.createElement('audio');
    element.srcObject = mediaStream;
    element.muted = this.muted_;
    element.setAttribute('id', `audio_${this.stream_.getId()}`);
    element.setAttribute('autoplay', 'autoplay');
    element.setAttribute('playsinline', 'playsinline');

    // append <audio> to the div container
    this.div_.appendChild(element);

    if (this.outputDeviceId_) {
      await element.setSinkId(this.outputDeviceId_);
    }

    this.element_ = element;
    this.setVolume(this.volume_);
    this.handleEvents();

    try {
      await element.play();
    } catch (e) {
      this.log_.warn('<audio> play() error: ' + e);
      const errorMessage = e.toString() + ' <audio>';
      if (errorMessage.startsWith('NotAllowedError')) {
        throw new RtcError({ code: ErrorCode.PLAY_NOT_ALLOWED, message: errorMessage });
      }
    }
  }

  handleEvents() {
    this.element_.addEventListener('playing', event => {
      this.log_.info('stream - audio player is starting playing');
      this.state_ = 'PLAYING';
      this.emitter_.emit(PlayerEvent.PLAYER_STATE_CHANGED, {
        state: this.state_,
        reason: 'playing'
      });
    });
    this.element_.addEventListener('ended', event => {
      this.log_.info('stream - audio player is ended');
      if (this.state_ !== 'STOPPED') {
        this.state_ = 'STOPPED';
        this.emitter_.emit(PlayerEvent.PLAYER_STATE_CHANGED, {
          state: this.state_,
          reason: 'ended'
        });
      }
    });
    this.element_.addEventListener('pause', event => {
      this.log_.info('stream - audio player is paused');
      this.state_ = 'PAUSED';
      this.emitter_.emit(PlayerEvent.PLAYER_STATE_CHANGED, { state: this.state_, reason: 'pause' });
    });
    // listen for track's ended event
    this.track_.addEventListener('ended', event => {
      this.log_.info('stream - audio player track is ended');
      if (this.state_ !== 'STOPPED') {
        this.state_ = 'STOPPED';
        this.emitter_.emit(PlayerEvent.PLAYER_STATE_CHANGED, {
          state: this.state_,
          reason: 'ended'
        });
      }
    });

    this.track_.addEventListener('mute', event => {
      this.log_.info('stream - audio track is muted');
      if (this.state_ !== 'PAUSED') {
        this.state_ = 'PAUSED';
        this.emitter_.emit(PlayerEvent.PLAYER_STATE_CHANGED, {
          state: this.state_,
          reason: 'mute'
        });
      }
    });
    this.track_.addEventListener('unmute', event => {
      this.log_.info('stream - audio track is unmuted');
      if (this.state_ === 'PAUSED') {
        this.state_ = 'PLAYING';
        this.emitter_.emit(PlayerEvent.PLAYER_STATE_CHANGED, {
          state: this.state_,
          reason: 'unmute'
        });
      }
    });
  }

  async setSinkId(deviceId) {
    if (this.outputDeviceId_ !== deviceId) {
      await this.element_.setSinkId(deviceId);
      this.outputDeviceId_ = deviceId;
    }
  }

  setVolume(volume) {
    this.log_.info(`stream - audioElement setVolume to : ${volume}`);
    this.element_.volume = volume;
  }

  getAudioLevel() {
    if (!this.soundMeter_) {
      // lazy init to preserve resource
      this.soundMeter_ = new SoundMeter();
      this.soundMeter_.connectToSource(this.track_);
    }
    return this.soundMeter_.getVolume();
  }

  stop() {
    this.div_.removeChild(this.element_);
    this.element_.srcObject = null;
    if (this.soundMeter_) {
      this.soundMeter_.stop();
      this.soundMeter_ = null;
    }
  }

  async resume() {
    try {
      await this.element_.play();
    } catch (e) {
      this.log_.warn('<audio> play() error: ' + e);
      const errorMessage = e.toString() + ' <audio>';
      if (errorMessage.startsWith('NotAllowedError')) {
        throw new RtcError({ code: ErrorCode.PLAY_NOT_ALLOWED, message: errorMessage });
      }
    }
  }

  on(event, handler) {
    this.emitter_.on(event, handler);
  }
}

export default AudioPlayer;
