/* eslint-disable require-jsdoc */
import log from '../utils/log/logger';
import isEmpty from '../utils/is-empty';

export default class Stats {
  constructor(options) {
    this.prevReport_ = {};
  }

  async getSenderStats(connection) {
    const stats = {
      audio: { bytesSent: 0, packetsSent: 0, audioLevel: 0, totalAudioEnergy: 0 },
      video: {
        bytesSent: 0,
        packetsSent: 0,
        framesEncoded: 0,
        frameWidth: 0,
        frameHeight: 0,
        framesSent: 0
      },
      rtt: 0
    };
    const pc = connection.getPeerConnection();
    if (pc) {
      try {
        const results = await pc.getStats();
        results.forEach(item => {
          // log.debug('uplink item: ' + JSON.stringify(item));
          if (item.type === 'outbound-rtp') {
            // log.debug('outbound-rtp item: ' + JSON.stringify(item));
            if (item.mediaType === 'video') {
              stats.video.bytesSent = item.bytesSent;
              stats.video.packetsSent = item.packetsSent;
              stats.video.framesEncoded = item.framesEncoded;
            } else if (item.mediaType === 'audio') {
              stats.audio.bytesSent = item.bytesSent;
              stats.audio.packetsSent = item.packetsSent;
            }
          } else if (item.type === 'candidate-pair') {
            stats.rtt = item.currentRoundTripTime * 1000;
          } else if (item.type === 'track') {
            if (item.frameWidth !== 'undefined') {
              // this is the trackId for video sender
              stats.video.frameWidth = item.frameWidth;
              stats.video.frameHeight = item.frameHeight;
              stats.video.framesSent = item.framesSent;
            }
            // safari type = 'track'
            if (typeof item.audioLevel !== 'undefined') {
              stats.audio.audioLevel = item.audioLevel || 0;
            }
          } else if (item.type === 'media-source') {
            // chrome 78+ 'media-source'
            if (item.kind === 'audio') {
              stats.audio.audioLevel = item.audioLevel || 0;
              stats.audio.totalAudioEnergy = item.totalAudioEnergy || 0;
            }
          }
        });
      } catch (e) {
        log.warn('failed to getStats on sender connection');
      }
    }
    return stats;
  }

  async getReceiverStats(connection) {
    const stats = {
      tinyId: connection.getTinyId(),
      userId: connection.getUserId(),
      hasAudio: false,
      hasVideo: false,
      audio: {
        bytesReceived: 0,
        packetsReceived: 0,
        packetsLost: 0,
        jitter: 0,
        audioLevel: 0,
        totalAudioEnergy: 0
      },
      video: {
        bytesReceived: 0,
        packetsReceived: 0,
        packetsLost: 0,
        framesDecoded: 0,
        frameWidth: 0,
        frameHeight: 0
      }
    };
    const pc = connection.getPeerConnection();
    if (pc) {
      try {
        const results = await pc.getStats();
        results.forEach(item => {
          // log.debug('downlink item: ' + JSON.stringify(item));
          if (item.type === 'inbound-rtp') {
            // log.debug('inbound-rtp item: ' + JSON.stringify(item));
            if (item.mediaType === 'audio') {
              stats.audio.packetsReceived = item.packetsReceived;
              stats.audio.bytesReceived = item.bytesReceived;
              stats.audio.packetsLost = item.packetsLost;
              stats.audio.jitter = item.jitter;
              stats.hasAudio = true;
            } else if (item.mediaType === 'video') {
              stats.video.packetsReceived = item.packetsReceived;
              stats.video.bytesReceived = item.bytesReceived;
              stats.video.packetsLost = item.packetsLost;
              stats.video.framesDecoded = item.framesDecoded;
              stats.hasVideo = true;
            }
          } else if (item.type === 'track') {
            if (typeof item.frameWidth !== 'undefined') {
              // this is a track for video receiver
              stats.video.frameWidth = item.frameWidth;
              stats.video.frameHeight = item.frameHeight;
            }

            if (item.kind === 'audio') {
              stats.audio.audioLevel = item.audioLevel || 0;
              stats.audio.totalAudioEnergy = item.totalAudioEnergy || 0;
            }
          }
        });
      } catch (e) {
        log.warn('failed to getStats on receiver connection');
      }
    }
    return stats;
  }

  async getStats(uplinkConnection, downlinkConnections) {
    let senderStats = {};
    if (uplinkConnection) {
      senderStats = await this.getSenderStats(uplinkConnection);
    }
    const receiverStats = [];
    for (let [key, value] of downlinkConnections) {
      const st = await this.getReceiverStats(value);
      receiverStats.push(st);
    }
    return { senderStats, receiverStats };
  }

  prepareReport(stats, report) {
    // senders' stats
    if (!isEmpty(stats.senderStats)) {
      report.uint32_delay = stats.senderStats.rtt;

      report.AudioReportState.sentAudioLevel = stats.senderStats.audio.audioLevel;
      report.AudioReportState.sentAudioEnergy = stats.senderStats.audio.totalAudioEnergy;
      report.AudioReportState.uint32_audio_enc_pkg_br = stats.senderStats.audio.bytesSent;
      report.VideoReportState.uint32_video_snd_br = stats.senderStats.video.bytesSent;
      report.VideoReportState.uint32_send_total_pkg = stats.senderStats.video.packetsSent;

      // video encode stats
      report.VideoReportState.VideoEncState[0].uint32_enc_width =
        stats.senderStats.video.frameWidth;
      report.VideoReportState.VideoEncState[0].uint32_enc_height =
        stats.senderStats.video.frameHeight;
      report.VideoReportState.VideoEncState[0].uint32_enc_fps = stats.senderStats.video.framesSent;
    }

    stats.receiverStats.forEach(st => {
      if (st.hasAudio) {
        report.AudioReportState.AudioDecState.push({
          uint32_audio_delay: 0,
          uint32_audio_jitter: st.audio.jitter,
          uint32_audio_real_recv_pkg: st.audio.packetsReceived,
          uint32_audio_flow: st.audio.bytesReceived,
          uint32_audio_real_recv_br: 0,
          uint64_sender_uin: st.tinyId,
          packetsLost: st.audio.packetsLost,
          totalPacketsLost: st.audio.packetsLost,
          audioLevel: st.audio.audioLevel,
          audioEnergy: st.audio.totalAudioEnergy
        });
        report.AudioReportState.uint32_audio_real_recv_pkg += st.audio.packetsReceived;
        report.AudioReportState.uint32_audio_flow += st.audio.bytesReceived;
        report.uint32_real_num += st.audio.packetsReceived;
      }
      if (st.hasVideo) {
        report.VideoReportState.VideoDecState.push({
          uint32_video_recv_fps: st.video.framesDecoded,
          uint32_video_recv_br: st.video.bytesReceived, //单路视频下行码率
          uint32_video_real_recv_pkg: st.video.packetsReceived, //单路下行收包
          uint32_dec_height: st.video.frameHeight,
          uint32_dec_width: st.video.frameWidth,
          uint32_video_jitter: 0,
          uint64_sender_uin: st.tinyId,
          packetsLost: st.video.packetsLost,
          totalPacketsLost: st.video.packetsLost
        });
        report.VideoReportState.uint32_video_total_real_recv_pkg += st.video.packetsReceived;
        report.VideoReportState.uint32_video_rcv_br += st.video.bytesReceived;
      }
    });

    report.uint64_end_utime = new Date().getTime();

    const prevReport = this.prevReport_;
    // it's fine that use JSON.parse/strigify to do a deep copy as
    // all field in the report is JSON stringifyable
    this.prevReport_ = JSON.parse(JSON.stringify(report));

    // code below are copied from old SDK

    if (isEmpty(prevReport)) {
      report.AudioReportState.uint32_audio_enc_pkg_br =
        (report.AudioReportState.uint32_audio_enc_pkg_br * 8) / 2;
      report.VideoReportState.uint32_video_rcv_br =
        (report.VideoReportState.uint32_video_rcv_br * 8) / 2;
      report.VideoReportState.uint32_video_snd_br =
        (report.VideoReportState.uint32_video_snd_br * 8) / 2;
      report.VideoReportState.VideoDecState.forEach(st => {
        st.uint32_video_recv_br = (st.uint32_video_recv_br * 8) / 2;
        report.uint32_total_send_bps =
          report.AudioReportState.uint32_audio_enc_pkg_br +
          report.VideoReportState.uint32_video_snd_br;
      });
    } else {
      report.uint64_begine_utime = prevReport.uint64_end_utime;

      report.uint32_real_num -= prevReport.uint32_real_num;
      if (report.uint32_real_num <= 0) {
        report.uint32_real_num = 0;
      }

      report.AudioReportState.uint32_audio_real_recv_pkg -=
        prevReport.AudioReportState.uint32_audio_real_recv_pkg;
      if (report.AudioReportState.uint32_audio_real_recv_pkg <= 0) {
        report.AudioReportState.uint32_audio_real_recv_pkg = 0;
      }

      report.AudioReportState.uint32_audio_enc_pkg_br -=
        prevReport.AudioReportState.uint32_audio_enc_pkg_br;
      if (report.AudioReportState.uint32_audio_enc_pkg_br <= 0) {
        report.AudioReportState.uint32_audio_enc_pkg_br = 0;
      }
      report.AudioReportState.uint32_audio_enc_pkg_br =
        (report.AudioReportState.uint32_audio_enc_pkg_br * 8) / 2;

      report.VideoReportState.uint32_video_snd_br -=
        prevReport.VideoReportState.uint32_video_snd_br;
      if (report.VideoReportState.uint32_video_snd_br <= 0) {
        report.VideoReportState.uint32_video_snd_br = 0;
      }
      report.VideoReportState.uint32_video_snd_br =
        (report.VideoReportState.uint32_video_snd_br * 8) / 2;

      report.AudioReportState.uint32_audio_flow -= prevReport.AudioReportState.uint32_audio_flow;
      if (report.AudioReportState.uint32_audio_flow <= 0) {
        report.AudioReportState.uint32_audio_flow = 0;
      }

      report.VideoReportState.uint32_send_total_pkg -=
        prevReport.VideoReportState.uint32_send_total_pkg;
      if (report.VideoReportState.uint32_send_total_pkg <= 0) {
        report.VideoReportState.uint32_send_total_pkg = 0;
      }

      report.VideoReportState.uint32_video_rcv_br -=
        prevReport.VideoReportState.uint32_video_rcv_br;
      if (report.VideoReportState.uint32_video_rcv_br <= 0) {
        report.VideoReportState.uint32_video_rcv_br = 0;
      }
      report.VideoReportState.uint32_video_rcv_br =
        (report.VideoReportState.uint32_video_rcv_br * 8) / 2;

      report.VideoReportState.uint32_video_total_real_recv_pkg -=
        prevReport.VideoReportState.uint32_video_total_real_recv_pkg;
      if (report.VideoReportState.uint32_video_total_real_recv_pkg <= 0) {
        report.VideoReportState.uint32_video_total_real_recv_pkg = 0;
      }

      // encode fps calc
      report.VideoReportState.VideoEncState[0].uint32_enc_fps -=
        prevReport.VideoReportState.VideoEncState[0].uint32_enc_fps;
      if (report.VideoReportState.VideoEncState[0].uint32_enc_fps < 0) {
        report.VideoReportState.VideoEncState[0].uint32_enc_fps = 0;
      }
      report.VideoReportState.VideoEncState[0].uint32_enc_fps =
        report.VideoReportState.VideoEncState[0].uint32_enc_fps / 2;

      let len = report.VideoReportState.VideoDecState.length;
      for (let i = 0; i < len; i++) {
        const decVideoItem = report.VideoReportState.VideoDecState[i];
        const tinyid = decVideoItem.uint64_sender_uin;
        let recvPackage = decVideoItem.uint32_video_real_recv_pkg;
        let recvBr = decVideoItem.uint32_video_recv_br;
        let fps = decVideoItem.uint32_video_recv_fps;

        for (let k = 0; k < prevReport.VideoReportState.VideoDecState.length; k++) {
          const tempItem = prevReport.VideoReportState.VideoDecState[k];

          if (tempItem.uint64_sender_uin === tinyid) {
            // packetsLost should be the 2s' delta
            decVideoItem.packetsLost = decVideoItem.totalPacketsLost - tempItem.totalPacketsLost;

            recvPackage -= tempItem.uint32_video_real_recv_pkg;
            if (recvPackage <= 0) {
              recvPackage = 0;
            }

            recvBr -= tempItem.uint32_video_recv_br;
            if (recvBr <= 0) {
              recvBr = 0;
            }
            // decode fps calc
            fps -= tempItem.uint32_video_recv_fps;
            if (fps < 0) fps = 0;
            break;
          }
        }
        report.VideoReportState.VideoDecState[i].uint32_video_real_recv_pkg = recvPackage;
        report.VideoReportState.VideoDecState[i].uint32_video_recv_br = (recvBr * 8) / 2;
        report.VideoReportState.VideoDecState[i].uint32_video_recv_fps = fps / 2;
      }

      len = report.AudioReportState.AudioDecState.length;
      for (let i = 0; i < len; i++) {
        const decAudioItem = report.AudioReportState.AudioDecState[i];
        let audioRecvPack = decAudioItem.uint32_audio_real_recv_pkg;
        let audioRecvFlow = decAudioItem.uint32_audio_flow;
        const tinyid = decAudioItem.uint64_sender_uin;
        for (let h = 0; h < prevReport.AudioReportState.AudioDecState.length; h++) {
          const tempAudioItem = prevReport.AudioReportState.AudioDecState[h];
          if (tempAudioItem.uint64_sender_uin === tinyid) {
            // packetsLost should be the 2s' delta
            decAudioItem.packetsLost =
              decAudioItem.totalPacketsLost - tempAudioItem.totalPacketsLost;

            audioRecvPack -= tempAudioItem.uint32_audio_real_recv_pkg;
            if (audioRecvPack <= 0) {
              audioRecvPack = 0;
            }

            audioRecvFlow -= tempAudioItem.uint32_audio_flow;
            if (audioRecvFlow <= 0) {
              audioRecvFlow = 0;
            }
            break;
          }
        }
        report.AudioReportState.AudioDecState[i].uint32_audio_real_recv_pkg = audioRecvPack;
        report.AudioReportState.AudioDecState[i].uint32_audio_flow = audioRecvFlow;
        report.AudioReportState.AudioDecState[i].uint32_audio_real_recv_br =
          (audioRecvFlow * 8) / 2;
      }

      report.AudioReportState.uint32_audio_real_recv_br =
        (report.AudioReportState.uint32_audio_flow * 8) / 2;
      report.uint32_real_num =
        report.AudioReportState.uint32_audio_real_recv_pkg +
        report.VideoReportState.uint32_video_total_real_recv_pkg;
      report.uint32_total_send_bps =
        report.AudioReportState.uint32_audio_enc_pkg_br +
        report.VideoReportState.uint32_video_snd_br;
      report.uint32_total_recv_bps =
        report.AudioReportState.uint32_audio_real_recv_br +
        report.VideoReportState.uint32_video_rcv_br;
    }
    return report;
  }

  async getStatsReport(uplinkConnection, downlinkConnections) {
    const report = {
      uint64_begine_utime: new Date().getTime(),
      uint64_end_utime: 0,
      uint32_real_num: 0, //音频总下行包
      uint32_delay: 0, // Conn-audio-1-0 googRtt
      uint32_CPU_curfreq: 0,
      uint32_total_send_bps: 0, //总上行码率
      uint32_total_recv_bps: 0,
      AudioReportState: {
        uint32_audio_enc_pkg_br: 0, //音频上行码率
        uint32_audio_real_recv_pkg: 0, //音频下行收包
        uint32_audio_flow: 0, //音频下行流量
        uint32_audio_real_recv_br: 0, //音频下行码率
        uint32_audio_delay: 0, //googCurrentDelayMs
        uint32_audio_jitter: 0, //googJitterBufferMs
        uint32_microphone_status: 1,
        sentAudioLevel: 0,
        sentAudioEnergy: 0,
        AudioDecState: []
      },
      VideoReportState: {
        uint32_video_delay: 0, //googTargetDelayMs
        uint32_video_snd_br: 0, //视频上行码率
        uint32_video_total_real_recv_pkg: 0, //视频下行收包
        uint32_video_rcv_br: 0, //视频下行码率
        uint32_send_total_pkg: 0, //视频上行发包
        VideoEncState: [
          {
            uint32_enc_width: 0,
            uint32_enc_height: 0,
            uint32_capture_fps: 0,
            uint32_enc_fps: 0
          }
        ],
        VideoDecState: []
      }
    };

    const stats = await this.getStats(uplinkConnection, downlinkConnections);
    this.prepareReport(stats, report);
    return report;
  }
}
