/**
 * WebSocket Channel 事件类型
 *
 * @enum {string}
 */
const ChannelEvent = {
  /** WebSocket 连接状态变化通知 */
  CONNECTION_STATE_CHANGED: 'connection-state-changed',
  /** WebSocket 连接错误 */
  ERROR: 'error'
};

/**
 * WebSocket 连接状态
 *
 * @enum {string}
 */
const ConnectionState = {
  /** WebSocket 断开连接 */
  DISCONNECTED: 'DISCONNECTED',
  /** WebSocket 正在连接 */
  CONNECTING: 'CONNECTING',
  /** WebSocket 断开后正在重连 */
  RECONNECTING: 'RECONNECTING',
  /** WebSocket 连接成功 */
  CONNECTED: 'CONNECTED'
};

/**
 * Signal 事件映射表
 */
const SignalEventMap = {
  /** 远端交换 sdp offer/answer */
  NEW_REMOTE_SDP: 0x2,
  /** 远端交换 iceCandidate */
  NEW_ICE_CANDIDATE: 0x4,
  /** 用户被踢出房间 (Relay超时、同名用户登录等) */
  CLINET_BANNDED: 0x08,
  /** Signal Channel 建立成功 */
  CHANNEL_SETUP_SUCCESS: 0x13,
  /** Signal Channel 建立失败 */
  CHANNEL_SETUP_FAILED: 0x50,
  /** Signal Channel 重连结果 */
  CHANNEL_RECONNECT_RESULT: 0x202,
  /** 用户进房返回结果 */
  JOIN_ROOM_RESULT: 0x14,
  /** 远端用户进房 */
  PEER_JOIN: 0x10,
  /** 远端用户退房 */
  PEER_LEAVE: 0x12,
  /** 远端更新 sdp offer，涉及 audio/video track 增减 */
  UPDATE_REMOTE_SDP: 0x30,
  UPDATE_AUDIO_SSRC: 0x32,
  UPDATE_VIDEO_SSRC: 0x34,
  /** 远端用户音视频mute状态更新 */
  UPDATE_REMOTE_MUTE_STAT: 0x17,
  /** Remote server's acknowledge on SignalCommand.CLOSE_PEER */
  CLOSE_PEER_ACK: 0x0a,
  /** acknowledge on subscription */
  SUBSCRIBE_ACK: 0x1a
};

/**
 * Signal 事件，与 SignalEventMap 一一对应
 */
const SignalEvent = {
  /** 远端交换 sdp offer/answer */
  NEW_REMOTE_SDP: 'new-remote-sdp',
  /** 远端交换 iceCandidate */
  NEW_ICE_CANDIDATE: 'new-ice-candidate',
  /** 用户被踢出房间 (Relay超时、同名用户登录等) */
  CLINET_BANNDED: 'client-banned',
  /** Signal Channel 建立成功 */
  CHANNEL_SETUP_SUCCESS: 'channel-setup-success',
  /** Signal Channel 建立失败 */
  CHANNEL_SETUP_FAILED: 'channel-setup-failed',
  /** Signal Channel 重连结果 */
  CHANNEL_RECONNECT_RESULT: 'channel-reconnect-result',
  /** 用户进房返回结果 */
  JOIN_ROOM_RESULT: 'join-room-result',
  /** 远端用户进房 */
  PEER_JOIN: 'peer-join',
  /** 远端用户退房 */
  PEER_LEAVE: 'peer-leave',
  /** 远端更新 sdp offer，涉及 audio/video track 增减 */
  UPDATE_REMOTE_SDP: 'update-remote-sdp',
  UPDATE_AUDIO_SSRC: 'update-audio-ssrc',
  UPDATE_VIDEO_SSRC: 'update-video-ssrc',
  /** 远端用户音视频mute状态更新 */
  UPDATE_REMOTE_MUTE_STAT: 'update-remote-mute-stat',
  /** Remote server's acknowledge on SignalCommand.CLOSE_PEER */
  CLOSE_PEER_ACK: 'close-peer-ack',
  SUBSCRIBE_ACK: 'subscribe-ack',

  // internal event
  REQUEST_REBUILD_SESSION: 'request-rebuild-session'
};

