function WebGMEAPI() {
  WebGMEAPI.callbacks = function(event, result) {};
  WebGMEAPI.Param = {};
  WebGMEAPI.Quality = {};
  WebGMEAPI.RemoteStreams = {};
  WebGMEAPI.fn.event = {};

  //set default upload local stream or not
  WebGMEAPI.Param.defaultMute = true;
  WebGMEAPI.Param.hasRTCStarted = false;

  WebGMEAPI.Param.localStream_ = null;
  WebGMEAPI.Param.remoteStreams_ = [];
  WebGMEAPI.Param.micId_;

  WebGMEAPI.Param.interval_ = 1000;
  WebGMEAPI.Param.isInRoom = false;
  WebGMEAPI.Param.micEnbale = false;

  // 伴奏相关
  WebGMEAPI.AccompanyParam = {};
  WebGMEAPI.AccompanyParam.file = null; //the current file
  WebGMEAPI.AccompanyParam.fileName = null; //the current file name
  WebGMEAPI.AccompanyParam.audioContext = null;
  WebGMEAPI.AccompanyParam.source = null; //the audio source
  WebGMEAPI.AccompanyParam.animationId = null;
  WebGMEAPI.AccompanyParam.forceStop = false;
  WebGMEAPI.AccompanyParam.loopBack = true;
  WebGMEAPI.AccompanyParam.loopCount = 0;
  WebGMEAPI.AccompanyParam.volume = 1.0;
  WebGMEAPI.AccompanyParam.localGainNode = null;
  WebGMEAPI.AccompanyParam.remoteGainNode = null;
  WebGMEAPI.AccompanyParam.micStream = null; //获取伴音的麦克风stream，在退出的时候关闭stream的track

  // 初始化GME的事件系统
  InitGMEEvent();
}
