/**
 * 初始化一个event事件，会在回调的过程中通过eventType通知给回调函数
 */
function InitGMEEvent() {
  WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_NONE = 0;

  //Notification of entering a room, triggered by EnterRoom API.
  WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_ENTER_ROOM = 1;
  //Notification of exiting a room, triggered by ExitRoom API.
  WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_EXIT_ROOM = 2;
  //Notification of room disconnection due to network or other reasons, which will trigger automatically.
  WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_ROOM_DISCONNECT = 3;
  //Notification of the updates of room members, the notification contains detailed information, refer to ITMG_EVENT_ID_USER_UPDATE.
  WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_USER_UPDATE = 4;

  WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_LOCALSTREAM_READY = 5;
  WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_REMOTESTREAM_IN = 6;
  WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_REMOTESTREAM_PAUSED = 7;
  WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_REMOTESTREAM_OUT = 8;
  WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_SPEAKER_ENABLE_RESULT = 9;
  WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_MICROPHONE_ENABLE_RESULT = 10;
  WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_ACCOMPANY_FINISH = 11;
  WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_START_ACCOMPANY = 12;
  WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_SET_ACCOMPANY_VOLUME = 13;
}

/**
 * 通知状态给回调函数，携带事件类型，返回码返回信息
 * @param eventType 事件类型
 * @param errCode 返回码
 * @param msg 返回信息
 */
function notifyEveny(eventType, errCode, msg) {
  var result = {};
  result.errorCode = errCode;
  result.errorMsg = msg;
  try {
    console.log(`notifyEvent eventType:${eventType},errCode:${errCode}, msg:${msg}`);
    WebGMEAPI.callbacks(eventType, result);
  } catch (e) {
    log.error(`Dispatch Event:${eventType},Result:${result} Error!`);
  }
}

/**
 * 通知状态给回调函数，携带事件类型和数据，此函数会携带数据obj返回给对调
 * @param eventType 事件类型
 * @param errObj 数据
 */
function notifyEvenyObj(eventType, errObj) {
  try {
    console.log(`notifyEventObj eventType:${eventType},errObj:${errObj}`);
    WebGMEAPI.callbacks(eventType, errObj);
  } catch (e) {
    log.error(`Dispatch Event:${eventType},errObj:${errObj} Error!`);
  }
}

WebGMEAPI.fn = WebGMEAPI.prototype;

function addView(id) {
  // eslint-disable-next-line no-undef
  if (!$('#' + id)[0]) {
    // eslint-disable-next-line no-undef
    $('<div/>', {
      id,
      class: 'video-view'
    }).appendTo('#gme-audio-wrap');
  }
}

function removeView(id) {
  // eslint-disable-next-line no-undef
  if ($('#' + id)[0]) {
    // eslint-disable-next-line no-undef
    $('#' + id).remove();
  }
}

