/**
 * 接收到远端流更新状态，远端流会携带用户的userId等信息
 * @param data 源断流数据对象
 */
function onRemoteStreamUpdate(data) {
  let userIds = [];
  WebGMEAPI.Param.remoteStreams_.forEach(remoteStream => {
    userIds.push(remoteStream.getUserId());
  });
  data.PeerInfo = userIds;
  console.log('GME:onRemoteStreamUpdate, data:', data);
  notifyEvenyObj(WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_USER_UPDATE, data);
}

/**
 * 房间断开连接函数
 * @param data 远端流数据
 */
function onKickout(data) {
  console.log('GME:onKickout, data:', data);
  notifyEvenyObj(WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_ROOM_DISCONNECT, data);
}

/**
 * 客户端连接服务端房间错误信息调用函数，例如房间退出或者房间失去连接等原因造成连接失败
 * @param data 错误数据
 */
function onErrorNotify(data) {
  console.error('onErrorNotify, data:', data);
  if (
    (data.getCode() === 10015 && data.errorMsg === 'ice close') ||
    (data.getCode() === 10035 && data.errorMsg === 'websocket close')
  ) {
    WebGMEAPI.Param.isInRoom = false;
    notifyEveny(WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_EXIT_ROOM, 0, 'Exit OK');
  } else {
    console.log('start unpublish');
    WebGMEAPI.Param.client_
      .unpublish(WebGMEAPI.Param.localStream_)
      .then(function() {
        console.log('start unpublish succ!');
      })
      .catch(function(e) {
        console.error('unpublish failed:' + e);
      })
      .finally(function() {
        WebGMEAPI.Param.client_
          .leave()
          .then(function() {
            console.log('start client leave succ!');
            WebGMEAPI.Param.isInRoom = false;
          })
          .catch(function(e) {
            console.error('client leave failed:' + e);
          })
          .finally(function() {
            resetAccompany();
            if (WebGMEAPI.Param.localStream_) {
              WebGMEAPI.Param.localStream_.stop();
              WebGMEAPI.Param.localStream_.close();
              WebGMEAPI.Param.localStream_ = null;
            }
            if (WebGMEAPI.AccompanyParam.micStream) {
              stopTracks(WebGMEAPI.AccompanyParam.micStream);
              WebGMEAPI.AccompanyParam.micStream = null;
            }
            WebGMEAPI.Param.remoteStreams_ = [];
            WebGMEAPI.Param.client_ = null;
            WebGMEAPI.Param.isInRoom = false;
            WebGMEAPI.Param.micEnbale = false;
          });

        setTimeout(function() {
          notifyEvenyObj(WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_ROOM_DISCONNECT, data);
        }, 1200);
      });
  }
}

/**
 * 进入房间流程时间监听处理，在进入语音房间后处理时间和监听，同时进行相关log的记录
 * @param rtc trtc数据对象
 */
function handleEvents(rtc) {
  rtc.client_.on('error', err => {
    console.error('GME:error:' + err);
    // alert(err);
    onErrorNotify(err);
  });
  rtc.client_.on('client-banned', err => {
    console.error('GME:client has been banned for ' + err);
    // alert(err);
    onKickout(err);
  });
  // fired when a remote peer is joining the room
  rtc.client_.on('peer-join', evt => {
    const userId = evt.userId;
    // rtc.peers.push(userId);
    // rtc.updatePeers();
    console.log('GME:peer-join ' + userId);
    // onRemoteStreamUpdate(evt);
  });
  // fired when a remote peer is leaving the room
  rtc.client_.on('peer-leave', evt => {
    const userId = evt.userId;
    // var templist = []
    // rtc.peers.forEach(p => {
    //   if(p !== userId) {
    //     templist.push(p);
    //   }
    // });
    // rtc.peers = templist;
    // rtc.updatePeers();
    console.log('GME:peer-leave ' + userId);
    // onRemoteStreamRemove(evt);
  });

  // fired when a remote stream is added
  rtc.client_.on('stream-added', evt => {
    const remoteStream = evt.stream;
    const id = remoteStream.getId();
    const userId = remoteStream.getUserId();
    console.log(`GME:remote stream added: [${userId}] ID: ${id} type: ${remoteStream.getType()}`);
    console.log('GME:subscribe to this remote stream');
    rtc.remoteStreams_.push(remoteStream);
    rtc.client_.subscribe(remoteStream);
  });
  // fired when a remote stream has been subscribed
  rtc.client_.on('stream-subscribed', evt => {
    const remoteStream = evt.stream;
    const id = remoteStream.getId();
    addView(id);
    remoteStream
      .play(id)
      .then(() => {
        log.info(`Start play remote stream:${id} userid:${remoteStream.getUserId()}`);
      })
      .catch(e => {
        //log.warn(
        //  `Start play remote stream:${id} userid:${remoteStream.getUserId()} Error, code:${errorCode}`
        //);
        //if (errorCode === 0x4043) {
        // PLAY_NOT_ALLOWED,引导用户手势操作恢复音视频播放
        // stream.resume()
        log.warn(`Start auto play remote stream:${id} error, \
            need to resume stream after doing some gesture or click on this webpage!`);
        //notifyEveny(WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_REMOTESTREAM_PAUSED, 0, '');
        //}
      });
    console.log('GME:stream-subscribed ID: ' + id);
    notifyEvenyObj(WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_REMOTESTREAM_IN, remoteStream);
  });
  // fired when the remote stream is removed, e.g. the remote user called Client.unpublish()
  rtc.client_.on('stream-removed', evt => {
    const remoteStream = evt.stream;
    const id = remoteStream.getId();
    remoteStream.stop();
    rtc.remoteStreams_ = rtc.remoteStreams_.filter(stream => {
      return stream.getId() !== id;
    });
    removeView(id);
    console.log(`GME:stream-removed ID: ${id}  type: ${remoteStream.getType()}`);
    notifyEvenyObj(WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_REMOTESTREAM_OUT, remoteStream);
  });

  rtc.client_.on('stream-updated', evt => {
    // seiyabai stream update
    const remoteStream = evt.stream;
    console.log(
      'GME:type: ' +
        remoteStream.getType() +
        ' stream-updated hasAudio: ' +
        remoteStream.hasAudio() +
        ' hasVideo: ' +
        remoteStream.hasVideo() +
        'evt:' +
        evt
    );
    onRemoteStreamUpdate(evt);
  });

  rtc.client_.on('mute-audio', evt => {
    console.log('GME:' + evt.userId + ' mute audio');
  });
  rtc.client_.on('unmute-audio', evt => {
    console.log('GME:' + evt.userId + ' unmute audio');
  });
  rtc.client_.on('mute-video', evt => {
    console.log('GME:' + evt.userId + ' mute video');
  });
  rtc.client_.on('unmute-video', evt => {
    console.log('GME:' + evt.userId + ' unmute video');
  });

  rtc.client_.on('connection-state-changed', evt => {
    console.log(`GME:RtcClient state changed to ${evt.state} from ${evt.prevState}`);
  });
}

