/**
 * 初始化本地流对象(主要是音频输入流)，获取本地设备的麦克风设备数据，
 * 创建本地流成功后将流推送到服务端，同时监听stream事件
 * @returns {Promise<boolean>}
 */
async function initLocalStream() {
  let micId;
  const devices = await TRTC.getMicrophones();
  if (devices.length > 0) {
    micId = devices[0].deviceId;
    if (micId.length === 0) {
      micId = 'default';
    }
    console.log(`select microphone id:${micId}, label:${devices[0].label}`);
  } else {
    console.log('NO microphone found!');
  }
  WebGMEAPI.Param.micId_ = micId;
  if (!WebGMEAPI.Param.micId_) {
    return false;
  }
  log.info('start create local stream');
  WebGMEAPI.Param.localStream_ = TRTC.createStream({
    audio: true,
    video: false,
    userId: WebGMEAPI.Param.openId,
    cameraId: 0,
    microphoneId: micId
  });
  ReportSelfVersion(WebGMEAPI.Param.sdkAppId);
  // try {
  console.log('start init local stream!');
  // initialize the local stream and the stream will be populated with audio/video
  await WebGMEAPI.Param.localStream_.initialize();
  console.log('local stream init ok!');

  WebGMEAPI.Param.localStream_.on('player-state-changed', event => {
    log.info(`local stream ${event.type} player is ${event.state}`);
  });

  addView('local-stream');
  // eslint-disable-next-line no-undef
  const container = $('#' + 'local-stream')[0];
  // console.log('container:'+container);

  // play the localStream within 'local-stream' div
  await WebGMEAPI.Param.localStream_.play(container);

  console.log('start localstream play ok');
  console.log('start publish!');
  notifyEveny(WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_LOCALSTREAM_READY, 0, '');
  // publish the local stream
  await WebGMEAPI.Param.client_.publish(WebGMEAPI.Param.localStream_);
  log.info('start publish local stream ok!');

  setTimeout(getStatsLooper, WebGMEAPI.Param.interval_ || 1000);

  // log.error('start public local stream failed:'+e);

  //     notifyEveny(WebGMEAPI.fn.event.ITMG_MAIN_EVNET_TYPE_LOCALSTREAM_READY, 1, "");
  //     console.error('start localstream play error:'+e);
  //   });

  return true;
}

//01--初始化GME
/**
 * 初始化GME的SDK
 * @param document html的document对象
 * @param sdkAppId sdk的appId
 * @param openId sdk的openId
 */
WebGMEAPI.fn.Init = function(document, sdkAppId, openId) {
  WebGMEAPI.Param.document = document;
  WebGMEAPI.Param.sdkAppId = sdkAppId;
  WebGMEAPI.Param.openId = openId;
  log.info('sdkappid:' + sdkAppId);
};

function getStatsLooper() {
  if (WebGMEAPI.Param.client_) {
    let data = {};
    WebGMEAPI.Param.client_
      .getLocalAudioStats()
      .then(stats => {
        for (let userId in stats) {
          data.local_userId = userId;
          data.local_bytesSent = stats[userId].bytesSent;
          data.local_packetsSent = stats[userId].packetsSent;
          data.UploadBRSend = stats[userId].bytesSent;
          data.UploadRTT = 0;
          // console.log('$$$local userId: ' + data.local_userId +
          //             ' bytesSent: ' + data.local_bytesSent +
          //             ' packetsSent: ' + data.local_packetsSent);
        }

        let peerInfo = [];
        WebGMEAPI.Param.client_
          .getRemoteAudioStats()
          .then(stats => {
            for (let userId in stats) {
              let p = {};
              p.userId = userId;
              p.bytesReceived = stats[userId].bytesReceived;
              p.packetsReceived = stats[userId].packetsReceived;
              p.packetsLost = stats[userId].packetsLost;
              p.jitter = stats[userId].jitter;
              // p.delay = 0;
              // p.jitterBufferMs = 0;
              // p.jitterReceived = 0;
              peerInfo.push(p);
              // console.log('remote userId: ' + p.userId +
              //             ' bytesReceived: ' + p.bytesReceived +
              //             ' packetsReceived: ' + p.packetsReceived +
              //             ' packetsLost: ' + p.packetsLost);
            }
            data.PeerInfo = peerInfo;
            notifyEvenyObj(WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_USER_UPDATE, data);
            setTimeout(getStatsLooper, WebGMEAPI.Param.interval_ || 1000);
            // console.log('====local userId: ' + data.local_userId +
            //             ' bytesSent: ' + data.local_bytesSent +
            //             ' packetsSent: ' + data.local_packetsSent);
          })
          .catch(e => {
            console.error('getRemoteAudioStats error:' + e);
          });
      })
      .catch(e => {
        console.error('getLocalAudioStats error:' + e);
      });
  }
}

