/**
 * 用作准备播放背景音乐的audioContext，后续通过audioContext进行混音播放
 * @private
 */
function _prepareAPI() {
  //fix browser vender for AudioContext and requestAnimationFrame
  window.AudioContext =
    window.AudioContext ||
    window.webkitAudioContext ||
    window.mozAudioContext ||
    window.msAudioContext;
  window.requestAnimationFrame =
    window.requestAnimationFrame ||
    window.webkitRequestAnimationFrame ||
    window.mozRequestAnimationFrame ||
    window.msRequestAnimationFrame;
  window.cancelAnimationFrame =
    window.cancelAnimationFrame ||
    window.webkitCancelAnimationFrame ||
    window.mozCancelAnimationFrame ||
    window.msCancelAnimationFrame;
  try {
    WebGMEAPI.AccompanyParam.audioContext = new AudioContext();
  } catch (e) {
    console.log(e);
  }
}

/**
 * 开始伴音播放，此时会播放本地背景音乐，同时采集麦克风录音，同时替换远端的音轨，就可实现将本地播放的音频和麦克风采集的音频混合之后推流给远端，
 * file为选择的本地音频文件，次文件音频会发送给远端房间的接收者。
 */
function _startAccompany() {
  //read and decode the file into audio array buffer
  let param = WebGMEAPI.AccompanyParam;
  let file = param.file;
  if (!file) {
    console.error('file is empty');
    return;
  }
  let fr = new FileReader();
  fr.onload = function(e) {
    let fileResult = e.target.result;
    let audioContext = param.audioContext;
    if (!audioContext) {
      console.error('audioContext is empty');
      return;
    }
    audioContext.decodeAudioData(
      fileResult,
      function(buffer) {
        _visualize(audioContext, buffer);
      },
      function(e) {
        console.error(e);
      }
    );
  };
  fr.onerror = function(e) {
    console.error(e);
  };
  //assign the file to the reader
  fr.readAsArrayBuffer(file);
}

/**
 *
 * @param audioContext
 * @param buffer
 * @private
 */
async function _visualize(audioContext, buffer) {
  if (!audioContext) {
    console.error('audioContext is empty');
    return;
  }
  let gainNode = audioContext.createGain();
  WebGMEAPI.AccompanyParam.localGainNode = gainNode;
  let audioBufferSouceNode = audioContext.createBufferSource();
  let analyser = audioContext.createAnalyser();
  let param = WebGMEAPI.AccompanyParam;
  //connect the analyser to the destination(the speaker), or we won't hear the sound
  analyser.connect(audioContext.destination);
  gainNode.gain.value = WebGMEAPI.AccompanyParam.volume;
  audioBufferSouceNode.connect(gainNode).connect(analyser);
  //then assign the buffer to the buffer source node
  audioBufferSouceNode.buffer = buffer;
  //play the source
  if (!audioBufferSouceNode.start) {
    audioBufferSouceNode.start = audioBufferSouceNode.noteOn; //in old browsers use noteOn method
    audioBufferSouceNode.stop = audioBufferSouceNode.noteOff; //in old browsers use noteOff method
  }

  //stop the previous sound if any
  if (param.source !== null) {
    param.source.stop(0);
  }
  audioBufferSouceNode.start(0);
  param.source = audioBufferSouceNode;
  if (WebGMEAPI.AccompanyParam.loopCount == -1) {
    audioBufferSouceNode.loop = true;
  } else {
    audioBufferSouceNode.addEventListener(
      'ended',
      function(a) {
        if (WebGMEAPI.AccompanyParam.forceStop) {
          WebGMEAPI.AccompanyParam.forceStop = false;
          return;
        }
        onPlayCompleted(audioContext, buffer);
      },
      false
    );
  }
  if (param.loopBack) {
    _startMixStream(audioContext);
  } else {
    notifyEveny(WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_START_ACCOMPANY, 0, 'success');
  }
  audioBufferSouceNode.onended = function() {
    _audioEnd(param);
  };
  console.log('local stream gainNode value::' + gainNode.gain.value);
}

function onPlayCompleted(audioContext, buffer) {
  console.log('onPlayCompleted');
  setTimeout(function() {
    WebGMEAPI.AccompanyParam.loopCount--;
    if (WebGMEAPI.AccompanyParam.loopCount < 0) {
      WebGMEAPI.AccompanyParam.loopCount = 0;
    }
    console.info('on play complete loopCount' + WebGMEAPI.AccompanyParam.loopCount);
    if (WebGMEAPI.AccompanyParam.loopCount > 0) {
      _visualize(audioContext, buffer);
    }
  }, 50);
}

/**
 * 混音流，获取当前设备的输入流，创建音乐播放的MediaStreamAudioDestinationNode，
 * 绑定本地播放流中，创建混合流node，然后将本地设备的输入流（麦克风的采集流）通过connect音频的node绑定到创建的混合流中，
 * 最后获取混合流的音轨，通过replaceTrack替换远端的推送流，从而形成混音流的推送
 * @param audioContext
 * @private
 */
function _startMixStream(audioContext) {
  if (WebGMEAPI.Param.localStream_) {
    let gainNode = audioContext.createGain();
    WebGMEAPI.AccompanyParam.remoteGainNode = gainNode;
    gainNode.gain.value = WebGMEAPI.AccompanyParam.volume;

    navigator.mediaDevices.getUserMedia({ audio: true, video: false }).then(stream => {
      // 创建属于音乐的steam
      let musicDestinationNode = audioContext.createMediaStreamDestination();
      let param = WebGMEAPI.AccompanyParam;
      if (param && param.source) {
        param.source.connect(musicDestinationNode);
        let musicStreamTrack = musicDestinationNode.stream.getAudioTracks()[0];
        if (!musicStreamTrack) {
          console.error('musicStreamTrack is null');
        }
        // 获取原本的localsteam
        // for mixing tracks
        // 创建dest for mix
        let mixDestinationNode = audioContext.createMediaStreamDestination();
        let mixStream = new MediaStream([musicStreamTrack]);
        audioContext
          .createMediaStreamSource(mixStream)
          .connect(gainNode)
          .connect(mixDestinationNode); //绑定music
        WebGMEAPI.AccompanyParam.micStream = stream;
        audioContext.createMediaStreamSource(stream).connect(mixDestinationNode); //绑定mic采集的数据
        let mixSteamTrack = mixDestinationNode.stream.getTracks()[0];
        if (mixSteamTrack && WebGMEAPI.Param.localStream_) {
          //先停掉之前绑定的麦克风的track
          let localSteam = WebGMEAPI.Param.localStream_.getMediaStream();
          if (localSteam) {
            stopTracks(localSteam);
          }
          // 对audioTrack和videoTrack处理之后，
          WebGMEAPI.Param.localStream_.replaceTrack(mixSteamTrack);
          notifyEveny(WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_START_ACCOMPANY, 0, 'success');
          console.log('remote stream gainNode value::' + gainNode.gain.value);
        }
      } else {
        console.error('Accompany Param is empty or source is empty');
      }
    });
  }
}

function stopTracks(stream) {
  if (stream) {
    stream.getTracks().forEach(track => {
      console.log('stopTracks');
      track.stop();
    });
  }
}

/**
 * 背景音乐播放完成，通过事件通知给事件接收者
 * @param instance
 * @private
 */
function _audioEnd(instance) {
  let loopCount = WebGMEAPI.AccompanyParam.loopCount;
  console.log('_audioEnd loopCount=: ' + loopCount);
  let that = WebGMEAPI.AccompanyParam;
  if (that.forceStop) {
    that.forceStop = false;
  }
  if (loopCount > 1) {
    return;
  }
  setTimeout(function() {
    notifyEveny(WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_ACCOMPANY_FINISH, 0, 'Finish Successfully');
  }, 0);
}

/**
 * 开始播放伴奏和背景音乐，同时开始推流
 * @param file 背景音乐的播放文件
 * @param loopBack 是混音并且发送
 * @param loopCount 循环次数
 */
WebGMEAPI.fn.StartAccompany = function StartAccompany(file, loopBack = true, loopCount = 1) {
  console.info(`StartAccompany file:${file}, loopBack:${loopBack}， loopCount: ${loopCount}`);
  if (loopCount < -1 || loopCount == 0) {
    notifyEveny(
      WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_START_ACCOMPANY,
      4,
      'loopCount is not available'
    );
    return;
  }
  if (!file) {
    notifyEveny(WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_START_ACCOMPANY, 1, 'file is null');
    console.error('file is null');
    return;
  }
  if (!WebGMEAPI.Param.isInRoom) {
    notifyEveny(WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_START_ACCOMPANY, 2, 'user not enter room');
    console.error('user not enter room');
    return;
  }

  if (!WebGMEAPI.Param.micEnbale) {
    notifyEveny(
      WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_START_ACCOMPANY,
      3,
      'the mic is not enable'
    );
    console.error('the mic is not enable');
    return;
  }

  if (null === WebGMEAPI.AccompanyParam.audioContext) {
    _prepareAPI();
  }

  WebGMEAPI.AccompanyParam.file = file;
  WebGMEAPI.AccompanyParam.fileName = file.name;
  WebGMEAPI.AccompanyParam.loopBack = loopBack;
  WebGMEAPI.AccompanyParam.loopCount = loopCount;
  _startAccompany();
};

WebGMEAPI.fn.setAccompanyVolume = function setAccompanyVolume(volume) {
  console.log('setAccompanyVolume volume:' + volume);
  WebGMEAPI.AccompanyParam.volume = volume;
  let localGainNode = WebGMEAPI.AccompanyParam.localGainNode;
  let remoteGainNode = WebGMEAPI.AccompanyParam.remoteGainNode;
  if (localGainNode && remoteGainNode) {
    localGainNode.gain.value = volume;
    remoteGainNode.gain.value = volume;
    notifyEveny(
      WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_SET_ACCOMPANY_VOLUME,
      0,
      'set accompany volume success'
    );
  } else {
    notifyEveny(
      WebGMEAPI.fn.event.ITMG_MAIN_EVENT_TYPE_SET_ACCOMPANY_VOLUME,
      1,
      'local or remote accompany volume not init'
    );
  }
};

/**
 * 停止伴奏播放，同时停止推流
 */
WebGMEAPI.fn.StopAccompany = function StopAccompany() {
  console.log('StopAccompany');
  resetAccompany();
};

/**
 * 获取房间内成员下行实时音量,范围为0 - 200
 */
WebGMEAPI.fn.GetRecvStreamLevel = function GetRecvStreamLevel(openId) {
  let volume = -1;
  if (WebGMEAPI.Param.localStream_) {
    if (WebGMEAPI.Param.localStream_.getUserId() === openId) {
      volume = Math.round(WebGMEAPI.Param.localStream_.getAudioLevel() * 200);
    }
  }

  if (WebGMEAPI.Param.remoteStreams_) {
    WebGMEAPI.Param.remoteStreams_.forEach(remoteStream => {
      if (remoteStream.getUserId() === openId) {
        volume = Math.round(remoteStream.getAudioLevel() * 200);
      }
    });
  }

  return volume;
};

function resetAccompany() {
  let param = WebGMEAPI.AccompanyParam;
  if (param) {
    if (param.source) {
      param.forceStop = true;
      WebGMEAPI.AccompanyParam.loopCount = 1;
      param.source.stop(0);
      param.source = null;
      WebGMEAPI.AccompanyParam.loopBack = true;
    }
  }
}

// window.WebGMEAPI = WebGMEAPI;
// module.exports = WebGMEAPI;
if (typeof module !== 'undefined' && typeof module.exports !== 'undefined') {
  module.exports = WebGMEAPI;
} else {
  window.WebGMEAPI = WebGMEAPI;
}
