import SignalChannel from '../signal/signal-channel';
import { SignalEvent, ChannelEvent, ConnectionState, SignalCommand } from '../signal/signal-event';
import * as rtcDetector from './rtc-detection';
import * as env from '../utils/environment';
import EventEmitter from 'eventemitter3';
import RtcError from '../common/rtc-error';
import ErrorCode from '../common/error-code';
import { Mode, Role, mainUrl, mainUrlTest, backupUrlTest, backupUrl } from '../common/constants';
import Connection from './connection';
import { ConnectionEvent, ClientEvent } from './event';
import Stats from './stats';
import * as Util from '../utils/utils';
import { logEvent, setLogEventConfig } from '../utils/event-log';
import IDLogger from '../utils/log/id-logger';
import log from '../utils/log/logger';
import { addEvent, getEvents, EventId } from '../utils/event-hub';
import { getCurrentTime } from '../utils/time';

/**
 * 音视频通话客户端对象 Client 通过 {@link TRTC.createClient createClient()} 创建，代表一次音视频会话。<br>
 * Client 客户端对象提供 TRTC Web SDK 的核心功能，包括:
 * - 进房 {@link Client#join join()}
 * - 退房 {@link Client#leave leave()}
 * - 发布本地流 {@link Client#publish publish()}
 * - 取消发布本地流 {@link Client#unpublish unpublish()}
 * - 订阅远端流 {@link Client#subscribe subscribe()}
 * - 取消订阅远端流 {@link Client#unsubscribe unsubscribe()}
 *
 * @class Client
 */
class Client {
  /**
   * Creates an instance of Client.
   * @package
   * @param {*} options
   */
  constructor(options) {
    this.mode_ = options.mode;
    if (options.sdpSemantics && (options.sdpSemantics.length > 0) ){
      this.sdpSemantics_ = options.sdpSemantics;
    }else{
      this.sdpSemantics_ = 'plan-b';
    }
    if (typeof options.sdpSemantics !== 'undefined') {
      this.sdpSemantics_ = options.sdpSemantics;
    } else if (rtcDetector.isUnifiedPlanDefault()) {
      this.sdpSemantics_ = 'unified-plan';
    }
    this.sdkAppId_ = options.sdkAppId;
    this.userId_ = options.userId;
    this.UseTestEnv = options.UseTestEnv;
    this.log_ = new IDLogger({
      id: 'c|' + this.userId_,
      direction: 'local',
      type: '',
    });
    this.userSig_ = options.userSig;
    this.roomId_ = 0;
    // hidden parameter for special customers to use string roomId
    this.useStringRoomId_ = options.useStringRoomId;
    this.recordId_ = null;
    this.pureAudioPushMode_ = null;
    this.version_ = options.version;
    this.log_.info('using sdpSemantics: ' + this.sdpSemantics_);

    // configure the logger for uploading
    log.setConfig({
      sdkAppId: this.sdkAppId_,
      userId: this.userId_,
      version: this.version_,
    });

    // validate parameters - DEPRECATED
    if (typeof options.recordId !== 'undefined') {
      if (!Number.isInteger(Number(options.recordId))) {
        throw new RtcError({
          code: ErrorCode.INVALID_PARAM,
          message: 'recordId must be an integer number',
        });
      }
      this.recordId_ = options.recordId;
    }
    if (typeof options.pureAudioPushMode !== 'undefined') {
      if (!Number.isInteger(Number(options.pureAudioPushMode))) {
        throw new RtcError({
          code: ErrorCode.INVALID_PARAM,
          message: 'pureAudioPushMode must be an integer number',
        });
      }
      this.pureAudioPushMode_ = options.pureAudioPushMode;
    }

    // business info pass through to remote server.
    this.bussinessInfo_ = options.bussinessInfo;

    // prefer streamId/userDefeinedRecordId to bussinessInfo
    let bzInfo;
    if (typeof options.streamId !== 'undefined') {
      bzInfo = {
        Str_uc_params: {
          userdefine_streamid_main: options.streamId,
        },
      };
    }
    if (typeof options.userDefineRecordId !== 'undefined') {
      if (bzInfo) {
        bzInfo.Str_uc_params.userdefine_record_id = options.userDefineRecordId;
      } else {
        bzInfo = {
          Str_uc_params: {
            userdefine_record_id: options.userDefineRecordId,
          },
        };
      }
    }
    if (bzInfo) {
      // businessInfo must be in JSON format
      this.bussinessInfo_ = JSON.stringify(bzInfo);
    }

    this.disableReceiver_ = false;
    this.signalChannel_ = null;

    this.isScreenShareOnly_ = 0;
    // 隐藏参数，TRTC.createClient({ isScreenShareOnly: true }), 后台会把该用户当成一个推送屏幕分享的“辅流”
    // 一个房间内只能有一个“辅流” - 屏幕分享
    if (typeof options.isScreenShareOnly !== 'undefined') {
      this.isScreenShareOnly_ = options.isScreenShareOnly ? 1 : 0;
    }

    // by default the role is 'host'.
    this.role_ = 'anchor';
    // backup for switchRole
    this.privateMapKey_ = '';
    // userId's representation in remote server
    this.tinyId_ = 0;

    // wss proxy url
    this.proxy_ = null;
    // TURN server setting
    this.turnServer_ = null;

    /*
     * remote connection map, tinyId -> Connection
     * @type {Map<number:Connection>}
     */
    this.connections_ = new Map();
    this.connectionsRefreshCount_ = new Map();
    this.pendingRefresh_ = new Map();

    /*
     * @typedef MutedState
     * @property {boolean} hasVideo
     * @property {boolean} hasAudio
     * @property {boolean} audioMuted
     * @property {boolean} videoMuted
     */
    /*
     * remote user's muted state, tinyId -> state
     * @type {Map<number:MutedState>}
     */
    this.mutedStates_ = new Map();

    // local stuffs
    this.localStream_ = null;
    this.isPublishing_ = false;
    // uplink connection, created on demand once a local Stream is published
    this.uplinkConnection_ = null;

    this.emitter_ = new EventEmitter();

    this.signalInfo_ = {};
    this.isInitialized_ = false;
    this.isJoined_ = false;
    this.heartbeat_ = -1;
    this.stats_ = new Stats();

    this.joinTimeout_ = -1;
    this.publishTimeout_ = -1;
    this.unpublishTimeout_ = -1;
    this.publishRetryCount_ = 0;

    this.startJoinTimestamp_ = new Date();
    this.joinedTimestamp_ = new Date();

    this.downlinkVideoHealthStats_ = {};
    this.uplinkVideoHealthStats_ = {};
  }

  /**
   * 设置代理服务器
   * <br>
   * 该方法适用于企业自己部署代理服务器，如 ngnix+coturn 方案。
   *
   * <br>
   * Note:
   *
   * - 该方法需要在 {@link Client#join join()} 之前调用。
   *
   * @param {string} url websocket 代理服务器地址，格式如：‘wss://proxy.example.com:443’
   * @memberof Client
   * @example
   * client.setProxyServer('wss://proxy.example.com:443');
   */
  setProxyServer(url) {
    if (!url.startsWith('wss://')) {
      throw new RtcError({
        code: ErrorCode.INVALID_PARAMETER,
        message: 'proxy server url shall be started with "wss://"',
      });
    }
    this.proxy_ = url;
  }

  getUrl(url) {
    let finalUrl = Util.getUrl(url);
    if (!Util.getEnv() && this.proxy_) {
      finalUrl = this.proxy_;
    }
    return finalUrl;
    //return this.proxy_ ? this.proxy_ : Util.getUrl(mainUrl);
  }

  getBackupUrl() {
    return this.proxy_ ? this.proxy_ : Util.getUrl(backupUrl);
  }

  getUserId() {
    return this.userId_;
  }

  getTinyId() {
    return this.tinyId_;
  }

  /**
   * 设置 TURN 服务器
   * <br>
   * 该方法配合 {@link Client#setProxyServer setProxyServer()} 使用，适用于企业自己部署代理服务器和 TURN 中转。
   *
   * <br>
   * Note:
   *
   * - 该方法需要在 {@link Client#join join()} 之前调用。
   *
   * @param {object} config TURN 服务器配置项
   * @param {string} config.url TURN 服务器 url
   * @param {string=} config.username TURN 服务器验证用户名
   * @param {string=} config.credential TURN 服务器验证密码
   * @param {string=} config.credentialType TURN 服务器验证密码类型，默认是 ‘password’
   * <br>
   * {@link https://developer.mozilla.org/en-US/docs/Web/API/RTCIceServer/credentialType credentialType参考}
   * @memberof Client
   * @example
   * client.setTurnServer({ url: '192.168.0.110:3478', username: 'bob', credential: 'bobspassword', credentialType: 'password' });
   */
  setTurnServer(config) {
    this.turnServer_ = {};
    // the right turn URL format is: turn:192.168.0.110:3478
    this.turnServer_.urls = 'turn:' + config.url;
    if (typeof config.username !== 'undefined' && typeof config.credential !== 'undefined') {
      this.turnServer_.username = config.username;
      this.turnServer_.credential = config.credential;
      this.turnServer_.credentialType = 'password';
      if (typeof config.credentialType !== 'undefined') {
        this.turnServer_.credentialType = config.credentialType;
      }
    }
  }

  /**
   * setup the signal channel.
   *
   * @private
   * @returns {Promise}
   * @memberof Client
   */
  initialize() {
    let loginMainURL = mainUrl;
    let loginBackupURL = backupUrl;

    return new Promise((resolve, reject) => {
      console.log("InitEnv:" + this.UseTestEnv);
      if (this.UseTestEnv) {
        loginMainURL = mainUrlTest;
        loginBackupURL = backupUrlTest;
      }
      this.log_.info('setup signal channel');
      this.signalChannel_ = new SignalChannel({
        sdkAppId: this.sdkAppId_,
        userId: this.userId_,
        userSig: this.userSig_,
        roomId_: this.roomId_,
        url: this.getUrl(loginMainURL),
        backupUrl: this.getUrl(loginBackupURL),
        version: this.version_,
      });

      // signal channel (WebSocket) connection state changed handler
      this.signalChannel_.on(ChannelEvent.CONNECTION_STATE_CHANGED, (event) => {
        this.log_.info(`SignalChannel state changed from ${event.prevState} to ${event.state}`);
        this.emitter_.emit(ClientEvent.CONNECTION_STATE_CHANGED, event);
      });

      // signal channel Error handler
      this.signalChannel_.on(ChannelEvent.ERROR, (error) => {
        if (!this.isInitialized_) {
          reject(error);
        } else {
          // ensure uplink connection is closed to avoid a/v state in backend run into chaos
          this.closeUplink();
          // also close downlink connections to prevent unexpected ice failed report.
          this.closeConnections();

          this.emitter_.emit(ClientEvent.ERROR, error);
        }
      });

      // signal channel setup failure handler
      this.signalChannel_.on(SignalEvent.CHANNEL_SETUP_FAILED, (error) => {
        this.log_.error('signal channel setup failed');
        reject(error);
      });

      // signal channel setup success handler
      this.signalChannel_.on(SignalEvent.CHANNEL_SETUP_SUCCESS, (event) => {
        this.signalInfo_ = event.signalInfo;
        this.tinyId_ = this.signalInfo_.tinyId;
        this.roomId_ = this.signalInfo_.roomId;
        this.isInitialized_ = true;
        resolve();
      });

      // peer-join event handler
      this.signalChannel_.on(SignalEvent.PEER_JOIN, (event) => {
        // hanadle peer-join only when the receiver is not disabled.
        if (!this.disableReceiver_) {
          this.onPeerJoin(event.data);
        }
      });
      // peer-leave
      this.signalChannel_.on(SignalEvent.PEER_LEAVE, (event) => {
        this.onPeerLeave(event.data);
      });

      this.signalChannel_.on(SignalEvent.UPDATE_REMOTE_MUTE_STAT, (event) => {
        this.onUpdateRemoteMuteStat(event.data);
      });

      this.signalChannel_.on(SignalEvent.CLINET_BANNDED, (event) => {
        const content = event.data.content;

        // close uplink connection to clear a/v state in case app continue to
        // publish stream even the user was banned either by kick or relay timeout
        this.closeUplink();

        // also close downlink connections to prevent unexpected ICE failed report.
        this.closeConnections();

        if (content.type === 'banned') {
          const reason = 'you got banned by account admin';
          this.log_.error('user was banned because of ' + reason);
          this.onClientBanned(reason);
        } else if (content.type === 'kick') {
          const reason = 'duplicated userId joining the room';
          this.log_.error('user was banned because of ' + reason);
          this.onClientBanned(reason);
        } else {
          this.log_.error('Relay server timeout observed');
          this.emitter_.emit(
            ClientEvent.ERROR,
            new RtcError({
              code: ErrorCode.SERVER_TIMEOUT,
              message: 'Relay server timeout observed',
            })
          );
        }
      });

      this.signalChannel_.on(SignalEvent.REQUEST_REBUILD_SESSION, (event) => {
        this.signalInfo_ = event.signalInfo;
        const tinyIds = [];
        if (this.connections_) {
          // add 0 for uplink connection, it indicates that the uplink connection
          // had already been established.
          tinyIds.push(0);
        }
        const remoteSdpOffers = [];
        for (let [key, value] of this.connections_) {
          tinyIds.push(key);
          const desc = value.getPeerConnection().remoteDescription;
          if (desc) {
            remoteSdpOffers.push(desc.sdp);
          }
        }
        const data = {
          socketid: this.signalInfo_.socketId,
          tinyid: this.tinyId_,
          appid: this.sdkAppId_,
          openid: this.userId_,
          sessionid: String(this.roomId_),
          sids: tinyIds,
          relayInfo: this.signalInfo_.relayInnerIp,
          remotesdp: remoteSdpOffers,
        };
        try {
          this.log_.info(`reconnect - rebuild session with data: ${JSON.stringify(data)}`);
          this.signalChannel_.send(SignalCommand.ON_REBUILD_SESSION, data);
        } catch (error) {
          this.log_.error('reconnect failed because rebuild session failed');
        }
      });

      this.signalChannel_.connect();
    });
  }

  /**
   * 加入一个音视频通话房间。<br>
   * 进房代表开始一个音视频通话会话，这时候 SDK 会监听远端用户进房退房情况，若有远端用户进房并且发布流，<br>
   * 本地会收到 'stream-added' 事件。进房后用户可以通过 {@link Client#publish publish()} 发布本地流，本地流发布成功后远端用户就会收到相应
   * 'stream-added' 事件通知从而完成一个双向的音视频通话连接。
   *
   * @param {object} options 进房参数
   * @param {number} options.roomId 房间号，32位整型
   * @param {string=} options.role 用户角色，仅在 ‘live’ 模式下有意义，目前支持两种角色：
   * - ‘anchor’ 主播
   * - ‘audience’ 观众
   * <br>
   * 注意：互动直播 live 模式下的观众角色没有发布本地流的权限，只有收看远端流的权限。如果观众想要连麦跟主播互动，
   * 请先通过 {@link Client#switchRole switchRole()} 切换角色到主播 anchor 后再发布本地流。
   * @param {string=} options.privateMapKey `Deprecated` 进房钥匙，若需要权限控制请携带该参数，因某些局限性目前已不建议使用该参数。
   * @returns {Promise}
   * @throws RtcError
   * @example
   * const client = TRTC.createClient({ mode: 'live', sdkAppId, userId, userSig });
   * client.join({ roomId: 8888, role: 'anchor' }).then(() => {
   *   // join room success
   * }).catch(error => {
   *   console.error('Join room failed: ' + error);
   * });
   */
  async join(options) {
    if (!rtcDetector.isTRTCSupported()) {
      throw new RtcError({
        code: ErrorCode.NOT_SUPPORTED,
        message: 'the browser does NOT support TRTC!',
      });
    }
    if (options.roomId) {
      // if set roomid refresh it, GME need room id to init signalChannel
      this.roomId_ = options.roomId;
    }


    this.startJoinTimestamp_ = new Date();
    // configure logEvent
    let env = Util.getEnv();
    if (!env) {
      env = 'qcloud';
      if (this.proxy_ && this.proxy_.startsWith('wss://trtc.rtc.qq.com')) {
        // 'trtc' is a special env for tencent call center
        env = 'trtc';
      }
    }
    setLogEventConfig({
      env,
      sdkAppId: this.sdkAppId_,
      userId: this.userId_,
      version: this.version_,
    });

    addEvent(this.userId_, {
      eventId: EventId.JOIN,
      eventDesc: 'joining room',
      timestamp: getCurrentTime(),
      userId: this.userId_,
      tinyId: this.tinyId_,
    });

    try {
      await this.initialize();
    } catch (error) {
      const errorCode = error.getCode();
      logEvent({ eventType: 'join', result: 'failed', code: errorCode });
      // rethrow the error to abort the call
      throw error;
    }
    try {
      await this.doJoin(options);
    } catch (error) {
      let extraCode = error.getExtraCode();
      const errorCode = extraCode !== 0 ? extraCode : error.getCode();
      // log the detailed extraCode for join-failure case
      logEvent({ eventType: 'join', result: 'failed', code: errorCode });
      // rethrow the error to abort the call
      throw error;
    }

    this.joinedTimestamp_ = new Date();
    // how many ms it took to join the room
    const delta = this.joinedTimestamp_ - this.startJoinTimestamp_;
    logEvent({ eventType: 'delta-join', result: 'success', delta });
    logEvent({ eventType: 'join', result: 'success' });
  }

  getVersion() {
    const v = this.version_.split('.');
    // the jsSdkVersion requested by backend, sigh!
    return parseInt(v[0]) * 1000 + parseInt(v[1]) * 100 + parseInt(v[2]);
  }

  doJoin(options) {
    return new Promise((resolve, reject) => {
      if (!this.isInitialized_) {
        throw new RtcError({
          code: ErrorCode.INVALID_OPERATION,
          message: 'SignalChannel is not ready yet',
        });
      }

      if (this.isJoined_) {
        throw new RtcError({
          code: ErrorCode.INVALID_OPERATION,
          message: 'duplicate join() called',
        });
      }

      // mandatory parameters
      if (this.roomId_ === undefined) {
        this.roomId_ = options.roomId;
      }

      // optional paramters
      if (typeof options.role !== 'undefined') {
        this.role_ = options.role;
      }
      let privateMapKey = '';
      if (typeof options.privateMapKey !== 'undefined') {
        privateMapKey = options.privateMapKey;
      }
      this.privateMapKey_ = privateMapKey;

      this.log_.info(
        `Join() => joining room: ${options.roomId} mode: ${this.mode_} role: ${this.role_}`
      );
      const signalInfo = this.signalInfo_;
      // join room parameters, it's awkful that plenty of params are not meaningful at all.
      const data = {
        openid: signalInfo.openId,
        tinyid: signalInfo.tinyId,
        // unused
        peerconnectionport: '',
        // WTF, useStrRoomid must be numeric 1 for true.
        useStrRoomid: this.useStringRoomId_ ? 1 : false,
        roomid: String(this.roomId_),
        sdkAppID: String(this.sdkAppId_),
        socketid: signalInfo.socketId,
        userSig: this.userSig_,
        // privateMapKey
        privMapEncrypt: privateMapKey,
        // unused?
        privMap: '',
        relayip: signalInfo.relayInnerIp,
        dataport: signalInfo.dataPort,
        stunport: signalInfo.stunPort,
        checkSigSeq: signalInfo.checkSigSeq,
        // pstn unused
        pstnBizType: 0,
        pstnPhoneNumber: null,
        recordId: this.recordId_,
        pureAudioPushMod: this.pureAudioPushMode_,
        role: 'user', // SpearRole was deprecated so this is unused at all.
        jsSdkVersion: String(this.getVersion()), // SDK Version starting with v4.0.0
        sdpSemantics: this.sdpSemantics_,
        browserVersion: env.BROWSER_VERSION,
        closeLocalMedia: true,
        trtcscene: this.mode_ === 'live' ? Mode.LIVE : Mode.RTC,
        trtcrole: this.role_ === 'anchor' ? Role.HOST : Role.AUDIENCE,
        bussinessInfo: this.bussinessInfo_,
        isAuxUser: this.isScreenShareOnly_,
      };
      // schedule timeout to check if join room could be finished in 2s
      this.joinTimeout_ = setTimeout(() => {
        this.log_.error('join room timeout observed');
        reject(
          new RtcError({
            code: ErrorCode.JOIN_ROOM_FAILED,
            message: 'join room timeout',
          })
        );
      }, 5000);
      this.signalChannel_.sendWithReport(SignalCommand.JOIN_ROOM, data, Util.getSysInfo());
      this.signalChannel_.once(SignalEvent.JOIN_ROOM_RESULT, (evt) => {
        clearTimeout(this.joinTimeout_);
        this.joinTimeout_ = -1;
        const result = evt.data.content.ret;
        if (!result) {
          this.isJoined_ = true;
          this.log_.info('Join room success, start heartbeat');
          this.startHeartbeat();
          resolve();
        } else {
          this.log_.error(
            'Join room failed result: ' + result + ' error: ' + evt.data.content.error
          );
          // TODO: join room failure error code
          reject(
            new RtcError({
              code: ErrorCode.JOIN_ROOM_FAILED,
              extraCode: result,
              message: 'Failed to join room - ' + evt.data.content.error,
            })
          );
        }
      });
    });
  }

  /**
   * 退出当前音视频通话房间，结束一次音视频通话会话。<br>
   * 退房前请确保已经通过 {@link Client#unpublish unpublish()} 取消发布本地流，若未取消发布本地流，SDK 内部会自动取消发布本地流。
   * 同时，退房会关闭所有远端流。
   *
   * @returns {Promise}
   * @memberof Client
   * @example
   * client.leave().then(() => {
   *   // leaving room success
   * }).catch(error => {
   *   console.error('leaving room failed: ' + error);
   * });
   */
  async leave() {
    addEvent(this.userId_, {
      eventId: EventId.LEAVE,
      eventDesc: 'leaving room',
      timestamp: getCurrentTime(),
      userId: this.userId_,
      tinyId: this.tinyId_,
    });
    try {
      // ensure all of events are uploaded
      await this.doHeartbeat();
    } catch (e) {
      // ignore
    }

    let found = false;
    for (let [key, value] of this.connections_) {
      if (!found) {
        let health = await value.getVideoHealthStats();
        if (health.valid) {
          found = true;
          this.downlinkVideoHealthStats_ = health;
        }
      }
    }

    // 视频通话健康度上报
    const healthStats = {
      HealthStats: {
        userAgent: navigator.userAgent,
        uplink: this.uplinkVideoHealthStats_,
        downlink: this.downlinkVideoHealthStats_
      }
    };
    this.log_.info(JSON.stringify(healthStats));

    try {
      await this.doLeave();
    } catch (e) {
      // ignore
    }
    this.isJoined_ = false;
    this.destroy();
    logEvent({ eventType: 'leave', result: 'success' });

    // how many seconds the client spent in the room
    const delta = Math.floor((new Date() - this.joinedTimestamp_) / 1000);
    logEvent({ eventType: 'delta-leave', result: 'success', delta });
  }

  doLeave() {
    return new Promise((resolve, reject) => {
      if (!this.isJoined_) return resolve();

      this.log_.info(`leave() => leaving room`);
      this.stopHeartbeat();
      this.closeConnections();
      this.mutedStates_.clear();

      // cleanup the timeouts
      this.clearPublishTimeout();
      this.clearUnpublishTimeout();

      // clean up pending refresh if any
      this.pendingRefresh_.clear();

      // terminate uplink w/o notifying remote server
      this.closeUplink();

      return resolve();
    });
  }

  clearPublishTimeout() {
    if (this.publishTimeout_ !== -1) {
      clearTimeout(this.publishTimeout_);
      this.publishTimeout_ = -1;
    }
  }

  clearUnpublishTimeout() {
    if (this.unpublishTimeout_) {
      clearTimeout(this.unpublishTimeout_);
      this.unpublishTimeout_ = -1;
    }
  }

  // close all downlink connections
  closeConnections() {
    this.connections_.forEach((connection) => {
      connection.close();
    });
    this.connections_.clear();
    this.connectionsRefreshCount_.clear();
  }

  /**
   * counterpart to initialize(), this will destroy the SignalChannel
   * and close the WebSocket underlying. Remote server requires that
   * closing the websocket after leaving the room and reopen a new
   * websocket before re-join the room. That is, a websocket session
   * is only valid for the timeline beween a join and a leave.
   *
   * @private
   * @memberof Client
   */
  destroy() {
    if (this.isJoined_) {
      this.log_.warn('please call leave() before destroy() client');
      throw new RtcError({
        code: ErrorCode.INVALID_OPERATION,
        message: 'Please call leave() before destory() the client',
      });
    }
    this.log_.info('destroying SignalChannel');
    if (this.signalChannel_) {
      this.signalChannel_.close();
      this.signalChannel_ = null;
    }
  }

  /**
   * 发布本地音视频流。<br>
   * 该方法需要在 {@link Client#join join()} 进房后调用，一次音视频会话中只能发布一个本地流。若想发布另外一个本地流，可先通过
   * {@link Client#unpublish unpublish()} 取消发布当前本地流后再发布新的本地流。<br>
   * 在发布本地流后，可通过 {@link LocalStream#removeTrack removeTrack()}、{@link LocalStream#addTrack addTrack()}、
   * {@link LocalStream#replaceTrack replaceTrack()} 来更新本地流中的某个音频或视频流。<br>
   * 发布本地流后远端会收到 ‘stream-added’ 事件通知。
   *
   * **NOTE**
   *
   * - 在 Client `mode` 为 'live' 及 `role` 为 'audience' 的场景下，即互动直播观众角色场景下，用户没有发布本地流权限。
   * 如果观众想要连麦互动，需要先调用 {@link Client#switchRole switchRole('anchor')} 切换到主播角色后再调用该接口发布本地流。
   *
   * @param {Stream} stream 本地流，通过 {@link TRTC.createStream createStream()} 创建出来。
   * @returns {Promise}
   * @throws RtcError
   * @memberof Client
   * @example
   * localStream.initialize().then(() => {
   *   // 本地流初始化成功，发布本地流
   *   client.publish(localStream).then(() => {
   *     // 本地流发布成功
   *   });
   * });
   */
  publish(stream) {
    return new Promise((resolve, reject) => {
      if (!rtcDetector.isTRTCSupported()) {
        throw new RtcError({
          code: ErrorCode.NOT_SUPPORTED,
          message: 'the browser does NOT support TRTC!',
        });
      }
      if (!this.isJoined_) {
        throw new RtcError({
          code: ErrorCode.INVALID_OPERATION,
          message: 'please call join() before publish()',
        });
      }

      if (this.mode_ === 'live' && this.role_ === 'audience') {
        throw new RtcError({
          code: ErrorCode.INVALID_OPERATION,
          message:
            'no permission to publish() under live/audience, please call swithRole("anchor") firstly before publish()',
        });
      }

      if (this.localStream_) {
        throw new RtcError({
          code: ErrorCode.INVALID_OPERATION,
          message: 'duplicate publishing, please unpublish and then re-publish',
        });
      }

      if (this.isPublishing_) {
        throw new RtcError({
          code: ErrorCode.INVALID_OPERATION,
          message: 'previous publishing is ongoing, please avod re-publishing',
        });
      }
      this.isPublishing_ = true;

      const startTime = new Date();

      this.log_.info(`publish() => publishing local stream`);
      const connection = new Connection({
        userId: this.userId_,
        tinyId: this.tinyId_,
        client: this,
        isUplink: true,
        signalChannel: this.signalChannel_,
      });
      connection.initialize();
      // schedule timeout to check if publish could be finished in 2s
      this.publishTimeout_ = setTimeout(() => {
        this.isPublishing_ = false;
        connection.close();

        this.log_.error('failed to publish because of timeout');
        logEvent({ eventType: 'publish', result: 'failed' });
        reject(new RtcError({ code: ErrorCode.UNKNOWN, message: 'publish timeout' }));
      }, 10000);
      connection.publish(stream, (error) => {
        this.clearPublishTimeout();
        this.isPublishing_ = false;
        connection.close();
        this.log_.error('failed to publish stream');
        logEvent({ eventType: 'publish', result: 'failed' });
        reject(error);
      });
      connection.on(ConnectionEvent.STREAM_PUBLISHED, (event) => {
        this.clearPublishTimeout();
        this.isPublishing_ = false;
        this.localStream_ = event.stream;
        this.localStream_.setConnection(connection);
        this.uplinkConnection_ = connection;

        logEvent({ eventType: 'publish', result: 'success' });
        const endTime = new Date();
        const delta = endTime - startTime;
        logEvent({ eventType: 'delta-publish', result: 'success', delta });

        if (stream.hasAudio()) {
          addEvent(this.userId_, {
            eventId: EventId.PUBLISH_AUDIO,
            eventDesc: 'publish audio track',
            timestamp: getCurrentTime(),
            userId: this.userId_,
            tinyId: this.tinyId_,
          });
        }
        if (stream.hasVideo()) {
          addEvent(this.userId_, {
            eventId: EventId.PUBLISH_VIDEO,
            eventDesc: 'publish video track',
            timestamp: getCurrentTime(),
            userId: this.userId_,
            tinyId: this.tinyId_,
          });
        }

        resolve();
      });
      connection.on(ConnectionEvent.CONNECTION_STATE_CHANGED, (event) => {
        if (event.state === 'connected') {
          if (this.publishRetryCount_) {
            // clear the auto re-publish flag if any.
            this.publishRetryCount_ = 0;
            this.log_.info('auto re-publish success');
          }
        }
      });

      connection.on(ConnectionEvent.ERROR, (error) => {
        const errorCode = error.getCode();
        if (
          errorCode === ErrorCode.ICE_TRANSPORT_ERROR &&
          this.publishRetryCount_ < 3 &&
          // try re-publish only when it's already published
          this.localStream_ === stream
        ) {
          // try to re-publish the stream in case of ICE failure
          this.publishRetryCount_ += 1;
          this.log_.warn('try to re-plublish the stream due to ice failure observed');
          this.unpublish(stream)
            .then(() => {
              this.log_.warn('trying re-publish');
              this.publish(stream)
                .then(() => {
                  this.log_.info('auto re-publish is ongoing');
                })
                .catch((e) => {
                  this.log_.warn('auto re-publish failure');
                  this.publishRetryCount_ = 0;
                  // rethrow the ICE_TRANSPORT_ERROR
                  this.emitter_.emit(ClientEvent.ERROR, error);
                });
            })
            .catch((e) => {
              this.log_.error('re-plublish/unpublish failure observed');
              this.publishRetryCount_ = 0;
              this.emitter_.emit(ClientEvent.ERROR, error);
            });
          return;
        }
        this.emitter_.emit(ClientEvent.ERROR, error);
      });
    });
  }

  /**
   * 取消发布本地流。<br>
   * 取消发布本地流后远端会收到 'stream-removed' 事件通知。<br>
   * 请在 {@link Client#leave leave()} 退房前取消已经发布的本地流。
   *
   * @param {Stream} stream 本地流
   * @returns {Promise}
   * @throws RtcError
   * @memberof Client
   * @example
   * // 取消发布本地流
   * client.unpublish(localStream).then(() => {
   *   // 取消发布本地流成功
   * });
   */
  unpublish(stream) {
    return new Promise((resolve, reject) => {
      if (this.isPublishing_) {
        throw new RtcError({
          code: ErrorCode.INVALID_OPERATION,
          message: 'unpublish() is being called during publish() is ongoing',
        });
      }

      if (stream !== this.localStream_) {
        throw new RtcError({
          code: ErrorCode.INVALID_PARAMETER,
          message: 'stream has not been published yet',
        });
      }
      this.log_.info(`unpublish() => unpublishing local stream`);

      this.doUnpublish()
        .then(() => {
          logEvent({ eventType: 'unpublish', result: 'success' });
          resolve();
        })
        .catch((e) => {
          logEvent({ eventType: 'unpublish', result: 'failed' });
          reject();
        });
    });
  }

  /**
   * remote server runs into exception if we close the uplink connection directly
   * WA: we send to remote server to request a peer close and close the connection
   * once we receive the ack from remote.
   *
   * @private
   * @returns {Promise}
   * @memberof Client
   */
  doUnpublish() {
    return new Promise((resolve, reject) => {
      this.signalChannel_.send(SignalCommand.CLOSE_PEER);

      // schedule timeout to check if remote server can response in 2s
      this.unpublishTimeout_ = setTimeout(() => {
        this.log_.warn('unpublish() timeout observed');
        // force to close uplink
        this.closeUplink();

        return reject();
      }, 5000);

      this.signalChannel_.once(SignalEvent.CLOSE_PEER_ACK, (data) => {
        this.clearUnpublishTimeout();
        this.log_.info('received CLOSE_PEER_ACK, close uplink connection');
        this.closeUplink();
        return resolve();
      });
    });
  }

  async closeUplink() {
    if (this.uplinkConnection_) {
      this.uplinkVideoHealthStats_ = await this.uplinkConnection_.getVideoHealthStats();

      this.uplinkConnection_.close();
      this.uplinkConnection_ = null;

      if (this.localStream_.hasAudio()) {
        addEvent(this.userId_, {
          eventId: EventId.UNPUBLISH_AUDIO,
          eventDesc: 'unpublish audio track',
          timestamp: getCurrentTime(),
          userId: this.userId_,
          tinyId: this.tinyId_,
        });
      }
      if (this.localStream_.hasVideo()) {
        addEvent(this.userId_, {
          eventId: EventId.UNPUBLISH_VIDEO,
          eventDesc: 'unpublish video track',
          timestamp: getCurrentTime(),
          userId: this.userId_,
          tinyId: this.tinyId_,
        });
      }

      // set localStream null to indicate that no local stream is published at all.
      this.localStream_.setConnection(null);
      this.localStream_ = null;
    }
  }

  /**
   * 订阅远端流<br>
   * 默认情况下，当收到远端流增加事件 'stream-added' 后，SDK 会立刻接收并解码该远端流所包含的音视频数据。<br>
   * 开发者可通过该订阅接口指明需要订阅音频、视频或者音视频流。若不想接收该远端流所包含的任何音视频数据，请通过
   * {@link Client#unsubscribe unsubscribe()} 取消订阅。
   *
   * @param {Stream} stream 远端流，通过监听 'stream-added' 事件获得。
   * @param {object=} options 订阅选项
   * @param {boolean=} options.audio 是否订阅音频
   * @param {boolean=} options.video 是否订阅视频
   * @memberof Client
   * @throws RtcError
   * @fires Event.STREAM_SUBSCRIBED 订阅成功事件通知
   * @example
   * // 监听远端流订阅成功事件
   * client.on('stream-subscribed', event => {
   *   const remoteStream = event.stream;
   *   // 远端流订阅成功，播放远端音视频流
   *   remoteStream.play('remote_stream');
   * });
   * // 监听远端流增加事件
   * client.on('stream-added', event => {
   *   const remoteStream = event.stream;
   *   // 订阅远端音频和视频流
   *   client.subscribe(remoteStream, { audio: true, video: true }).catch(e => {
   *     console.error('failed to subscribe remoteStream');
   *   });
   *
   *   // 仅订阅音频数据
   *   // client.subscribe(remoteStream, { audio: true, video: false }).catch(e => {
   *   //  console.error('failed to subscribe remoteStream');
   *   // });
   * });
   *
   */
  async subscribe(stream, options) {
    if (!stream.isRemote()) {
      throw new RtcError({
        code: ErrorCode.INVALID_PARAMETER,
        message: 'try to subscribe a local stream',
      });
    }

    this.log_.info(
      `subscribe() => subscribe to [${stream.getUserId()}] ${stream.getType()} stream with options: ${JSON.stringify(
        options
      )}`
    );
    const connection = stream.getConnection();
    await connection.subscribe(stream, options);
  }

  /**
   * 取消订阅远端流<br>
   * 当收到远端流增加事件 'stream-added' 通知后，SDK 默认会立刻接收并解码该远端流所包含的音视频数据，如果你想拒绝接收
   * 该远端流的任何音视频数据，请在 'stream-added' 事件处理回调中调用此接口。<br>
   * 如果你想默认拒绝接收所有的远端流，请在 {@link Client#join join()} 进房前调用 {@link Client#setDefaultMuteRemoteStreams setDefaultMuteRemoteStreams(true)}
   *
   * @param {Stream} stream 远端流，通过监听 ‘stream-added’ 事件获得。
   * @memberof Client
   * @throws RtcError
   * @example
   * client.on('stream-added', event => {
   *   const remoteStream = event.stream;
   *   // 拒绝接收该远端流所包含的任何音视频数据
   *   client.unsubscribe(remoteStream).catch(e => {
   *     console.error('failed to unsubscribe remoteStream');
   *   });
   * });
   */
  async unsubscribe(stream) {
    if (!stream.isRemote()) {
      throw new RtcError({
        code: ErrorCode.INVALID_PARAMETER,
        message: 'try to unsubscribe a local stream',
      });
    }
    this.log_.info(
      `unsubscribe() => unsubscribe to [${stream.getUserId()}] ${stream.getType()} stream`
    );
    const connection = stream.getConnection();
    await connection.unsubscribe(stream);
  }

  /**
   * 切换用户角色，仅在 ‘live’ 互动直播模式下生效。<br>
   * 该方法主要用于互动直播模式下观众用户连麦推流场景，在观众用户连麦推流之前，请先将用户角色转换为 ‘anchor’ 主播角色然后再发布本地流。<br>
   *
   * @param {string} role 用户角色
   * - ‘anchor’ 主播
   * - ‘audience’ 观众
   * @returns {Promise}
   * @throws RtcError
   * @memberof Client
   * @example
   * // live互动直播模式下，观众连麦推流
   * client.switchRole('anchor').then(() => {
   *   // 连麦观众角色切换为主播，开始推流
   *   client.publish(localStream);
   * });
   */
  async switchRole(role) {
    if (this.mode_ !== 'live') {
      // role is only valid in live mode.
      return;
    }
    if (this.role_ !== role) {
      this.role_ = role;
      this.log_.info('switchRole() => switch role to: ' + role);

      if (!this.isJoined_) return;

      await this.leave();
      await this.join({
        role,
        roomId: this.roomId_,
        privateMapKey: this.privateMapKey_,
      });
    }
  }

  /**
   * 监听客户端对象事件<br>
   * 详细事件列表请参见：{@link module:Event Event}
   *
   * @param {string} eventName 事件名称
   * @param {function} handler 事件处理方法
   * @memberof Client
   * @example
   * client.on('stream-added', event => {
   *   // stream-added event handler
   * });
   */
  on(eventName, handler) {
    this.emitter_.on(eventName, handler);
  }

  /**
   * 取消事件绑定<br>
   * 用于移除通过 {@link Client#on on()} 绑定的事件。
   *
   * @param {string} eventName 事件名称，传入通配符 '*' 会解除所有事件绑定。
   * @param {function} handler 事件处理方法
   * @memberof Client
   * @example
   * client.on('peer-join', function peerJoinHandler(event) {
   *   // peer-join event handler
   *   console.log('peer joined');
   *
   *   client.off('peer-join', peerJoinHandler);
   * });
   *
   * // 解除所有事件绑定
   * client.off('*');
   */
  off(eventName, handler) {
    if (eventName === '*') {
      this.emitter_.removeAllListeners();
    } else {
      this.emitter_.off(eventName, handler);
    }
  }

  /**
   * 远端用户音视频 mute 状态
   * @typedef RemoteMutedState
   * @property {string} userId 远端用户ID
   * @property {boolean} hasVideo 是否有视频
   * @property {boolean} hasAudio 是否有音频
   * @property {boolean} audioMuted 是否静音
   * @property {boolean} videoMuted 是否关闭摄像头
   */
  /**
   * 获取当前房间内远端用户音视频 mute 状态列表。
   *
   * @returns {RemoteMutedState[]} 远端用户音视频 mute 状态数组
   * @memberof Client
   */
  getRemoteMutedState() {
    const states = [];
    this.mutedStates_.forEach((value, key, map) => {
      const connection = this.connections_.get(key);
      if (connection) {
        states.push({ userId: connection.getUserId(), ...value });
      }
    });
    return states;
  }

  /**
   * 设置是否默认接收远端流。该方法可在 {@link Client#join join()} 调用前使用，若在进房后调用，会接收不到后续进房的远端用户音视频流。
   *
   * @param {boolean} muted 是否默认不接收远端流
   * - true 默认不接收任何远端流。
   * - false 默认接收所有远端流。（默认）
   * @memberof Client
   * @example
   * // 设置默认不接收任何远端流
   * client.setDefaultMuteRemoteStreams(true);
   * client.join({ roomId: 8888 }).then(() => {
   *   // 进房成功，此后不会接收到任何远端流，只能发布本地流
   * });
   */
  setDefaultMuteRemoteStreams(muted) {
    this.log_.info('setDefaultMuteRemoteStreams muted: ' + muted);
    this.disableReceiver_ = muted;
  }

  /**
   * 传输状态统计
   * @typedef {object} TransportStats
   * @property {number} rtt SDK 到腾讯视频云的 RTT (Round-Trip Time)，单位 ms
   */

  /**
   * 获取当前网络传输状况统计数据
   * <br>
   * 该方法需要在 {@link Client#publish publish()} 后调用。
   *
   * @returns {Promise<TransportStats>} Promise成功返回 `TransportStats`
   * @memberof Client
   * @example
   * client.getTransportStats().then(stats => {
   *     console.log('RTT: ' + stats.rtt);
   *   }
   * });
   */
  async getTransportStats() {
    let ts = { rtt: 0 };
    if (this.uplinkConnection_) {
      const stats = await this.stats_.getSenderStats(this.uplinkConnection_);
      ts.rtt = stats.rtt;
    }
    return ts;
  }

  /**
   * 本地流音频统计数据
   * @typedef {object} LocalAudioStats
   * @property {number} bytesSent 已发送字节数
   * @property {number} packetsSent 已发送包数
   */
  /**
   * 本地流音频统计数据Map对象，一个 `userId` 对应一个 `LocalAudioStats`。
   * @typedef {object} LocalAudioStatsMap
   */

  /**
   * 获取当前已发布本地流的音频统计数据
   * <br>
   * 该方法需要在 {@link Client#publish publish()} 后调用。
   *
   * @returns {Promise<LocalAudioStatsMap>} Promise成功返回 `LocalAudioStatsMap`
   * @memberof Client
   * @example
   * client.getLocalAudioStats().then(stats => {
   *   for (let userId in stats) {
   *     console.log('userId: ' + userId +
   *                 ' bytesSent: ' + stats[userId].bytesSent +
   *                 ' packetsSent: ' + stats[userId].packetsSent);
   *   }
   * });
   */
  async getLocalAudioStats() {
    const statsMap = {};
    statsMap[this.userId_] = { bytesSent: 0, packetsSent: 0 };
    if (this.uplinkConnection_) {
      const stats = await this.stats_.getSenderStats(this.uplinkConnection_);
      statsMap[this.userId_] = {
        bytesSent: stats.audio.bytesSent,
        packetsSent: stats.audio.packetsSent,
      };
    }
    return statsMap;
  }

  /**
   * 本地流视频统计数据
   * @typedef {object} LocalVideoStats
   * @property {number} bytesSent 已发送字节数
   * @property {number} packetsSent 已发送包数
   * @property {number} framesEncoded 已编码帧数
   * @property {number} framesSent 已发送帧数
   * @property {number} frameWidth 视频宽度
   * @property {number} frameHeight 视频高度
   */
  /**
   * 本地流视频统计数据Map对象，一个 `userId` 对应一个 `LocalVideoStats`。
   * @typedef {object} LocalVideoStatsMap
   */

  /**
   * 获取当前已发布本地流的视频统计数据
   * <br>
   * 该方法需要在 {@link Client#publish publish()} 后调用。
   *
   * @returns {Promise<LocalVideoStatsMap>} Promise成功返回 `LocalVideoStatsMap`
   * @memberof Client
   * @example
   * client.getLocalVideoStats().then(stats => {
   *   for (let userId in stats) {
   *     console.log('userId: ' + userId +
   *                 'bytesSent: ' + stats[userId].bytesSent +
   *                 'packetsSent: ' + stats[userId].packetsSent +
   *                 'framesEncoded: ' + stats[userId].framesEncoded +
   *                 'framesSent: ' + stats[userId].framesSent +
   *                 'frameWidth: ' + stats[userId].frameWidth +
   *                 'frameHeight: ' + stats[userId].frameHeight);
   *   }
   * });
   */
  async getLocalVideoStats() {
    const statsMap = {};
    statsMap[this.userId_] = {
      bytesSent: 0,
      packetsSent: 0,
      framesEncoded: 0,
      framesSent: 0,
      frameWidth: 0,
      frameHeight: 0,
    };
    if (this.uplinkConnection_) {
      const stats = await this.stats_.getSenderStats(this.uplinkConnection_);
      statsMap[this.userId_] = {
        bytesSent: stats.video.bytesSent,
        packetsSent: stats.video.packetsSent,
        framesEncoded: stats.video.framesEncoded,
        framesSent: stats.video.framesSent,
        frameWidth: stats.video.frameWidth,
        frameHeight: stats.video.frameHeight,
      };
    }
    return statsMap;
  }

  /**
   * 远端流音频统计数据
   * @typedef {object} RemoteAudioStats
   * @property {number} bytesReceived 已接收字节数
   * @property {number} packetsReceived 已接收包数
   * @property {number} packetsLost 丢包数
   */
  /**
   * 远端流音频统计数据Map对象，一个 `userId` 对应一个 `RemoteAudioStats`。
   * @typedef {object} RemoteAudioStatsMap
   */
  /**
   * 获取当前所有远端流的音频统计数据
   * <br>
   *
   * @returns {Promise<RemoteAudioStatsMap>} Promise成功返回 `RemoteAudioStatsMap`
   * @memberof Client
   * @example
   * client.getRemoteAudioStats().then(stats => {
   *   for (let userId in stats) {
   *     console.log('userId: ' + userId +
   *                 ' bytesReceived: ' + stats[userId].bytesReceived +
   *                 ' packetsReceived: ' + stats[userId].packetsReceived +
   *                 ' packetsLost: ' + stats[userId].packetsLost);
   *   }
   * });
   */
  async getRemoteAudioStats() {
    const statsMap = {};
    for (let [key, value] of this.connections_) {
      const stats = await this.stats_.getReceiverStats(value);
      if (stats.hasAudio) {
        statsMap[stats.userId] = {
          bytesReceived: stats.audio.bytesReceived,
          packetsReceived: stats.audio.packetsReceived,
          packetsLost: stats.audio.packetsLost,
          jitter: stats.audio.jitter,
        };
      }
    }
    return statsMap;
  }

  /**
   * 远端视频统计数据
   * @typedef {object} RemoteVideoStats
   * @property {number} bytesReceived 已接收字节数
   * @property {number} packetsReceived 已接收包数
   * @property {number} packetsLost 丢包数
   * @property {number} framesDecoded 已解码帧数
   * @property {number} frameWidth 视频宽度
   * @property {number} frameHeight 视频高度
   */
  /**
   * 远端流视频统计数据Map对象，一个 `userId` 对应一个 `RemoteVideoStats`。
   * @typedef {object} RemoteVideoStatsMap
   */

  /**
   * 获取当前所有远端流的视频统计数据
   *
   * @returns {Promise<RemoteVideoStatsMap>} Promise成功返回 `RemoteVideoStatsMap`
   * @memberof Client
   * @example
   * client.getRemoteVideoStats().then(stats => {
   *   for (let userId in stats) {
   *     console.log('userId: ' + userId +
   *                 ' bytesReceived: ' + stats[userId].bytesReceived +
   *                 ' packetsReceived: ' + stats[userId].packetsReceived +
   *                 ' packetsLost: ' + stats[userId].packetsLost +
   *                 ' framesDecoded: ' + stats[userId].framesDecoded +
   *                 ' frameWidth: ' + stats[userId].frameWidth +
   *                 ' frameHeight: ' + stats[userId].frameHeight);
   *
   *   }
   * });
   */
  async getRemoteVideoStats() {
    const statsMap = {};
    for (let [key, value] of this.connections_) {
      const stats = await this.stats_.getReceiverStats(value);
      if (stats.hasVideo) {
        statsMap[stats.userId] = {
          bytesReceived: stats.video.bytesReceived,
          packetsReceived: stats.video.packetsReceived,
          packetsLost: stats.video.packetsLost,
          framesDecoded: stats.video.framesDecoded,
          frameWidth: stats.video.frameWidth,
          frameHeight: stats.video.frameHeight,
        };
      }
    }
    return statsMap;
  }

  /**
   *
   * @package
   * @returns {string} sdpSemantics
   * @memberof Client
   */
  getSdpSemantics() {
    return this.sdpSemantics_;
  }

  /**
   *
   * @package
   * @returns {Array.<string>} Ice STUN/TURN servers
   * @memberof Client
   */
  getIceServers() {
    const iceServers = [];
    // disable STUN as we exchange iceCandidate through remote sdp offer/answer immediately
    // iceServers.push({ urls: this.signalInfo_.stunServers });
    if (this.turnServer_) {
      iceServers.push(this.turnServer_);
    }
    return iceServers;
  }

  /**
   *
   * @private
   * @memberof Client
   */
  startHeartbeat() {
    if (this.heartbeat_ === -1) {
      const hearbeatInterval = 2000;
      this.log_.info('startHeartbeat...');
      this.heartbeat_ = setInterval(this.doHeartbeat.bind(this), hearbeatInterval);
    }
  }

  /**
   *
   * @private
   * @memberof Client
   */
  stopHeartbeat() {
    if (this.heartbeat_ !== -1) {
      this.log_.info('stopHeartbeat');
      clearInterval(this.heartbeat_);
      this.heartbeat_ = -1;
    }
  }

  /**
   *
   * @private
   * @memberof Client
   */
  async doHeartbeat() {
    const stats = await this.stats_.getStatsReport(this.uplinkConnection_, this.connections_);
    // send event only when the signal channel is connected.
    const eventList = this.signalChannel_.isConnected() ? getEvents(this.userId_) : [];
    const report = {
      WebRTCQualityReq: stats,
      eventList,
      sdkAppId: this.sdkAppId_,
      tinyid: this.tinyId_,
      roomid: this.roomId_,
      socketid: this.signalInfo_.socketId,
      clientip: this.signalInfo_.localIp,
      serverip: this.signalInfo_.relayIp,
      cpunumber: navigator.hardwareConcurrency || 0,
      cpudevice: navigator.platform,
      devicename: navigator.platform,
      ostype: navigator.platform,
    };
    let micStatus = 0;
    if (this.localStream_ && this.localStream_.getMediaStream()) {
      const audioTracks = this.localStream_.getMediaStream().getAudioTracks();
      if (audioTracks.length > 0 && audioTracks[0].muted) {
        micStatus = 3;
      } else {
        micStatus = 1;
      }
    }
    report.WebRTCQualityReq.AudioReportState.uint32_microphone_status = micStatus;
    this.signalChannel_.send(SignalCommand.ON_QUALITY_REPORT, report);
  }

  /**
   *
   * @private
   * @param {object} data
   * @memberof Client
   */
  onPeerJoin(data) {
    const content = data.content;
    const userId = content.srcopenid;
    const tinyId = content.srctinyid;
    const sdp = content.remoteoffer;

    // remote may send duplicated peer-join if the previous connection is on-going and
    // a SignalChannel reconnection was requested.
    const prev = this.connections_.get(tinyId);
    if (prev) {
      this.log_.warn('duplicated peer-join observed, rebuild the connection');
      this.log_.info(`remote peer [${userId}] is leaving`);
      this.emitter_.emit(ClientEvent.PEER_LEAVE, { userId });
      prev.close();
      this.connections_.delete(tinyId);
    }

    // clean up the pending timeout if any
    const timeout = this.pendingRefresh_.get(tinyId);
    if (timeout) {
      clearTimeout(timeout);
      this.pendingRefresh_.delete(tinyId);
    }

    this.log_.info(`remote peer [${userId}] is online`);
    // fire the peer-join event to let app determine whether it wants to block this user or not
    this.emitter_.emit(ClientEvent.PEER_JOIN, { userId });

    const connection = new Connection({
      userId,
      tinyId,
      client: this,
      isUplink: false,
      signalChannel: this.signalChannel_,
    });
    this.connections_.set(tinyId, connection);
    connection.initialize();
    connection.setRemoteOffer(sdp);
    connection.on(ConnectionEvent.STREAM_ADDED, (event) => {
      this.emitter_.emit(ClientEvent.STREAM_ADDED, { stream: event.stream });
    });
    connection.on(ConnectionEvent.STREAM_REMOVED, (event) => {
      this.emitter_.emit(ClientEvent.STREAM_REMOVED, { stream: event.stream });
    });
    connection.on(ConnectionEvent.STREAM_UPDATED, (event) => {
      this.emitter_.emit(ClientEvent.STREAM_UPDATED, { stream: event.stream });
    });
    connection.on(ConnectionEvent.STREAM_SUBSCRIBED, (event) => {
      this.emitter_.emit(ClientEvent.STREAM_SUBSCRIBED, {
        stream: event.stream,
      });
    });
    connection.on(ConnectionEvent.ERROR, (error) => {
      const errorCode = error.getCode();
      let refreshCount = this.connectionsRefreshCount_.get(tinyId);
      if (errorCode === ErrorCode.ICE_TRANSPORT_ERROR && refreshCount < 5) {
        refreshCount += 1;
        this.connectionsRefreshCount_.set(tinyId, refreshCount);
        this.log_.warn(`try to refresh remote peer [${userId}]`);
        const data = {
          srctinyid: tinyId,
        };
        // try to refresh peer so that remote will send a new CreatePeerConnection
        // request for this remote user to re-establish the connection.
        this.signalChannel_.send(SignalCommand.REFRESH_PEER, data);
        // schedule a timeout to check if remote refresh can be completed in 10s
        var timeout = this.pendingRefresh_.get(tinyId);
        if (timeout) {
          clearTimeout(timeout);
        }
        const that = this;
        timeout = setTimeout(() => {
          this.log_.warn(`refresh remote peer timeout observed`);
          that.emitter_.emit(ClientEvent.ERROR, error);
        }, 10000);
        this.pendingRefresh_.set(tinyId, timeout);
        return;
      }
      this.emitter_.emit(ClientEvent.ERROR, error);
    });
    connection.on(ConnectionEvent.CONNECTION_STATE_CHANGED, (event) => {
      if (event.state === 'connected') {
        // reset the refresh count in case this connection is re-established for the refresh
        this.connectionsRefreshCount_.set(tinyId, 0);
      }
    });
  }

  /**
   *
   * @private
   * @param {object} data
   * @memberof Client
   */
  onPeerLeave(data) {
    const content = data.content;
    const tinyId = content.srctinyid;
    let userId = content.srcopenid;

    const connection = this.connections_.get(tinyId);
    if (connection) {
      // WA: remote server doesn't send the srcopenid
      if (userId === undefined) {
        userId = connection.getUserId();
      }
      this.log_.info(`remote peer [${userId}] is leaving`);
      this.emitter_.emit(ClientEvent.PEER_LEAVE, { userId });
      connection.close();
      this.connections_.delete(tinyId);
      this.connectionsRefreshCount_.delete(tinyId);
    }
  }

  /**
   *
   * @private
   * @memberof Client
   */
  onUpdateRemoteMuteStat(data) {
    const content = data.content;
    // TODO
    // data = {
    //     userlist:[
    //         {
    //             srctinyid,
    //             userid,
    //             // bit0: 大视频
    //             // bit1: 小视频 unused
    //             // bit2: 辅路，屏幕分享
    //             // bit3: 音频
    //             // bit4: 大视频mute状态
    //             // bit5: 辅路mute状态
    //             // bit6: 音频mute状态
    //             flag,
    //         },
    //         {},
    //     ]
    // }

    const VIDEO_STAT_BIT = 0x01;
    const AUDIO_STAT_BIT = 0x08;
    const AUDIO_MUTE_BIT = 0x40;
    const VIDEO_MUTE_BIT = 0x10;
    content.userlist.forEach((item) => {
      const tinyId = item.srctinyid;
      const userId = item.userid;
      // skip my stats
      if (tinyId === 0 || tinyId === this.tinyId_) return;
      // the remote user had been left the room, skip it.
      if (!this.connections_.get(tinyId)) {
        // also make sure remove its stats
        this.mutedStates_.delete(tinyId);
        return;
      }

      const hasVideo = !!(item.flag & VIDEO_STAT_BIT);
      const hasAudio = !!(item.flag & AUDIO_STAT_BIT);
      const audioMuted = !!(item.flag & AUDIO_MUTE_BIT);
      const videoMuted = !!(item.flag & VIDEO_MUTE_BIT);

      const state = this.mutedStates_.get(tinyId);
      if (state === undefined) {
        // hit the user at the first time
        this.mutedStates_.set(tinyId, {
          hasAudio,
          hasVideo,
          audioMuted,
          videoMuted,
        });
        if (hasVideo) {
          if (videoMuted) {
            this.emitter_.emit(ClientEvent.MUTE_VIDEO, { userId });
          } else {
            this.emitter_.emit(ClientEvent.UNMUTE_VIDEO, { userId });
          }
        }
        if (hasAudio) {
          if (audioMuted) {
            this.emitter_.emit(ClientEvent.MUTE_AUDIO, { userId });
          } else {
            this.emitter_.emit(ClientEvent.UNMUTE_AUDIO, { userId });
          }
        }
        return;
      } else {
        if (hasAudio && state.audioMuted !== audioMuted) {
          if (audioMuted) {
            this.emitter_.emit(ClientEvent.MUTE_AUDIO, { userId });
          } else {
            this.emitter_.emit(ClientEvent.UNMUTE_AUDIO, { userId });
          }
        }
        const prevVideoAvailable = !state.videoMuted && state.hasVideo;
        const videoAvailable = !videoMuted && hasVideo;
        if (prevVideoAvailable !== videoAvailable) {
          if (!videoAvailable) {
            this.emitter_.emit(ClientEvent.MUTE_VIDEO, { userId });
          } else {
            this.emitter_.emit(ClientEvent.UNMUTE_VIDEO, { userId });
          }
        }

        // update the state
        this.mutedStates_.set(tinyId, {
          hasAudio,
          hasVideo,
          audioMuted,
          videoMuted,
        });
      }
    });
  }

  /**
   *
   * @private
   * @memberof Client
   */
  onClientBanned(reason) {
    this.emitter_.emit(
      ClientEvent.CLIENT_BANNED,
      new RtcError({
        code: ErrorCode.CLIENT_BANNED,
        message: 'client was banned because of ' + reason,
      })
    );
  }

  triggerReconnectForTest(code) {
    const data = {
      exitcode: code,
    };
    this.log_.warn('triggerReconnectForTest exitcode: ' + code);
    this.signalChannel_.send(SignalCommand.TEST_WEBSOCKET, data);
  }

  setBandwidthForTest(bandwidth) {
    if (this.uplinkConnection_) {
      this.uplinkConnection_.setBandwidth(bandwidth);
    }
  }
}

export default Client;
