// import adapter to ensure webrtc-shim is applied
import adapter from 'webrtc-adapter';
import { version } from '../package.json';
import log from './utils/log/logger';
import Client from './rtc/client';
import LocalStream from './rtc/local-stream';
import {
  isScreenCaptureApiAvailable,
  checkSystemRequirementsInternal
} from './rtc/rtc-detection.js';
import RtcError from './common/rtc-error';
import ErrorCode from './common/error-code';
import MTA from './utils/mta/index';
import logger from './utils/log/loglevel.js';
import * as env from './utils/environment';

console.log(`WebRTC-adapter ${adapter.browserDetails.browser}/${adapter.browserDetails.version}`);

// MTA 上报相关。目前暂时写在此处，后续得讨论优化。
const mta = new MTA();

/**
 * TRTC 是 TRTC Web SDK 的主入口，通过 TRTC 方法可以创建一个实时音视频通信的客户端对象 (Client)
 * 和本地音视频流对象 (Stream)。
 * TRTC 方法还可以检测浏览器的兼容性，是否支持屏幕分享，以及设置日志级别及日志上传。
 *
 * @namespace TRTC
 */
const TRTC = {
  /**
   * TRTC Web SDK 版本号
   * @type {string}
   */
  VERSION: version,

  /**
   * 提供日志设置方法，包括设置日志输出等级、打开或关闭日志上传。
   *
   * @memberof TRTC
   * @namespace TRTC.Logger
   */
  Logger: {
    // Loglevel definitions, please align with loglevel definition in utils/log/loglevel.js
    // self.levels = { TRACE: 0, DEBUG: 1, INFO: 2, WARN: 3, ERROR: 4, SILENT: 5 }
    /**
     * 日志输出等级
     * @readonly
     * @enum {number}
     */
    LogLevel: {
      /**
       * 输出所有日志
       */
      TRACE: 0,
      /**
       * 输出 DEBUG、INFO、WARN、ERROR 等级日志
       */
      DEBUG: 1,
      /**
       * 输出 INFO、WARN、ERROR 等级日志
       */
      INFO: 2,
      /**
       * 输出 WARN、ERROR 等级日志
       */
      WARN: 3,
      /**
       * 输出 ERROR 等级日志
       */
      ERROR: 4,
      /**
       * 不输出任何日志
       * **注意：**
       * **关闭日之后我们将无法帮助您定位线上问题，建议至少保留 WARN 日志等级！**
       */
      NONE: 5
    },

    /**
     * 设置日志输出等级
     * <br>
     * 默认输出 INFO 日志等级，该日志等级包含 SDK 关键路径信息。
     *
     * @param {LogLevel} level 日志输出等级 {@link TRTC.Logger.LogLevel LogLevel}
     * @example
     * // 输出INFO以上日志等级
     * TRTC.Logger.setLogLevel(TRTC.Logger.LogLevel.INFO);
     */
    setLogLevel(level) {
      log.setLogLevel(level);
    },

    /**
     * 打开日志上传
     * <br>
     * 默认打开日志上传。如无特别原因，请保持打开日志上传，以便我们帮助您定位线上问题。
     *
     */
    enableUploadLog() {
      log.enableUploadLog();
    },

    /**
     * 关闭日志上传
     * <br>
     * **注意：**
     * **如果您关闭了日志上传，我们将无法协助您定位线上问题！**
     */
    disableUploadLog() {
      log.disableUploadLog();
    }
  },

  /**
   * 检测浏览器是否兼容 TRTC Web SDK <br>
   * 若当前浏览器不兼容 TRTC Web SDK，建议引导用户去下载最新版本的 Chrome 浏览器。
   *
   * @example
   * TRTC.checkSystemRequirements().then((result) => {
   *   if(!result) {
   *     alert('Your browser is not compatible with TRTC');
   *   }
   * });
   *
   * @returns {Promise.<boolean>} Promise 返回检测结果
   */
  async checkSystemRequirements() {
    return await checkSystemRequirementsInternal();
  },

  /**
   * 检测浏览器是否支持屏幕分享<br>
   * 在创建屏幕分享流之前请调用该方法检查当前浏览器是否支持屏幕分享。
   *
   * @returns {boolean}
   */
  isScreenShareSupported() {
    return isScreenCaptureApiAvailable();
  },

  /**
   * 返回媒体输入输出设备列表
   * <br>
   * 出于安全的考虑，在用户未授权摄像头或麦克风访问权限前，label 及 deviceId 字段可能都是空的。因此建议在用户授权访问后
   * 再调用该接口获取设备详情，比如在 {@link LocalStream#initialize initialize()} 后再调用此接口获取设备详情。
   *
   * @returns {Promise.<MediaDeviceInfo[]>} Promise 返回 {@link https://developer.mozilla.org/en-US/docs/Web/API/MediaDeviceInfo|MediaDeviceInfo} 数组
   */
  async getDevices() {
    if (!navigator.mediaDevices) {
      log.error('navigator.mediaDevices is undefined');
      return [];
    }

    const devices = await navigator.mediaDevices.enumerateDevices();
    return devices.map((item, index) => {
      let label = item.label;
      if (!item.label) {
        label = item.kind + '_' + index;
      }
      return {
        label,
        deviceId: item.deviceId,
        kind: item.kind
      };
    });
  },

  /**
   * 返回摄像头设备列表
   * <br>
   * 出于安全的考虑，在用户未授权摄像头或麦克风访问权限前，label 及 deviceId 字段可能都是空的。因此建议在用户授权访问后
   * 再调用该接口获取设备详情，比如在 {@link LocalStream#initialize initialize()} 后再调用此接口获取设备详情。
   *
   * @returns {Promise.<MediaDeviceInfo[]>} Promise 返回 {@link https://developer.mozilla.org/en-US/docs/Web/API/MediaDeviceInfo|MediaDeviceInfo} 数组
   */
  async getCameras() {
    if (!navigator.mediaDevices) {
      log.error('navigator.mediaDevices is undefined');
      return [];
    }
    const devices = await navigator.mediaDevices.enumerateDevices();
    return devices
      .filter(item => item.kind === 'videoinput')
      .map((item, index) => {
        // For security reasons, the label field is always blank unless an active media stream
        // exists or the user has granted persistent permission for media device access.
        let label = item.label;
        if (!item.label) {
          label = 'camera_' + index;
        }
        return {
          label,
          deviceId: item.deviceId,
          kind: item.kind
        };
      });
  },

  /**
   * 返回麦克风设备列表
   * <br>
   * 出于安全的考虑，在用户未授权摄像头或麦克风访问权限前，label 及 deviceId 字段可能都是空的。因此建议在用户授权访问后
   * 再调用该接口获取设备详情，比如在 {@link LocalStream#initialize initialize()} 后再调用此接口获取设备详情。
   *
   * @returns {Promise.<MediaDeviceInfo[]>} Promise 返回 {@link https://developer.mozilla.org/en-US/docs/Web/API/MediaDeviceInfo|MediaDeviceInfo} 数组
   */
  async getMicrophones() {
    if (!navigator.mediaDevices) {
      log.error('navigator.mediaDevices is undefined');
      return [];
    }
    // eslint-disable-next-line no-undef
    const devices = await this.getLocalDevices();
    return devices
      .filter(item => item.kind === 'audioinput')
      .map((item, index) => {
        let label = item.label;
        if (!item.label) {
          label = 'microphone_' + index;
        }
        return {
          label,
          deviceId: item.deviceId,
          kind: item.kind
        };
      });
  },

  /**
   * 浏览器兼容，safari 下
   */
  // eslint-disable-next-line no-dupe-keys
  async getLocalDevices() {
    const constraints = {
      audio: { deviceId: undefined }
    };
    // eslint-disable-next-line no-undef
    if (env.IS_ANY_SAFARI) {
      return navigator.mediaDevices
        .getUserMedia(constraints)
        .then(() => {
          return navigator.mediaDevices.enumerateDevices();
        })
        .catch(() => {
          log.error('navigator.mediaDevices.getUserMedia failed');
        });
    } else {
      return navigator.mediaDevices.enumerateDevices();
    }
  },

  /**
   * 返回扬声器设备列表
   * <br>
   * 出于安全的考虑，在用户未授权摄像头或麦克风访问权限前，label 及 deviceId 字段可能都是空的。因此建议在用户授权访问后
   * 再调用该接口获取设备详情，比如在 {@link LocalStream#initialize initialize()} 后再调用此接口获取设备详情。
   *
   * @returns {Promise.<MediaDeviceInfo[]>} Promise 返回 {@link https://developer.mozilla.org/en-US/docs/Web/API/MediaDeviceInfo|MediaDeviceInfo} 数组
   */
  async getSpeakers() {
    const devices = await navigator.mediaDevices.enumerateDevices();
    return devices
      .filter(item => item.kind === 'audiooutput')
      .map((item, index) => {
        let label = item.label;
        if (!item.label) {
          label = 'speaker_' + index;
        }
        return {
          label,
          deviceId: item.deviceId,
          kind: item.kind
        };
      });
  },

  /**
   * 创建一个实时音视频通话的客户端对象，在每次会话中仅需要调用一次。
   * 通常一个客户端对象跟一个用户 ID(userId) 绑定，同一个页面中可以有多个不同的客户端对象，
   * 每个客户端对象跟不同的用户ID绑定，比如，你可以使用一个客户端对象负责推送本地音视频流和接收
   * 远端流，同时使用另外一个客户端对象负责推送屏幕分享流，但是不接收远端流。
   *
   * @param {object} clientConfig 客户端配置项
   * @param {number} clientConfig.sdkAppId sdkAppId
   * @param {string} clientConfig.userId 用户ID
   * @param {string} clientConfig.userSig userSig 签名
   * @param {string} clientConfig.mode 应用场景，目前支持以下两种场景：
   * - 'videoCall' 实时通话模式
   * - 'live' 互动直播模式
   * @param {string=} clientConfig.streamId 绑定腾讯云直播 CDN 流 ID，设置之后，您就可以在腾讯云直播 CDN 上通过标准直播方案（FLV|HLS）播放该用户的音视频流。
   * 限制长度为64字节，可以不填写，一种推荐的方案是使用 “sdkappid_roomid_userid_main” 作为 streamid，这样比较好辨认且不会在您的多个应用中发生冲突。
   * - 【特殊说明】要使用腾讯云直播 CDN，您需要先在[控制台](https://console.cloud.tencent.com/trtc/) 中的功能配置页开启“启动自动旁路直播”开关。
   * - 【参考文档】[CDN 旁路直播](https://cloud.tencent.com/document/product/647/16826)。
   * @param {string=} clientConfig.userDefineRecordId 设置云端录制完成后的回调消息中的 "userdefinerecordid"  字段内容，便于您更方便的识别录制回调。
   * - 【推荐取值】限制长度为64字节，只允许包含大小写英文字母（a-zA-Z）、数字（0-9）及下划线和连词符。
   * - 【参考文档】[云端录制](https://cloud.tencent.com/document/product/647/16823)。
   * @returns {Client} 客户端对象
   * @param {number=} clientConfig.pureAudioPushMode 纯音频推流模式，需要旁路直播和录制时需要带上此参数:
   * - 1 表示本次是纯音频推流，不需要录制 MP3 文件
   * - 2 表示本次是纯音频推流，录制文件为 MP3
   *
   */
  createClient(clientConfig) {
    // 上报 sdkAppID
    mta.report('sdkAppID', { value: clientConfig.sdkAppId });
    // 上报 version
    mta.report('version', { value: this.VERSION });

    const opt = { version: this.VERSION };
    return new Client({ ...opt, ...clientConfig });
  },

  /**
   * 创建一个本地流 Stream 对象，本地流 Stream 对象通过 {@link Client#publish publish()} 方法发布本地音视频流。<br>
   *
   * 注意：一个音视频流 Stream 中最多只能包含一个音频 track 和一个视频 track。<br>
   *
   * 本地音视频流可以
   * - 从摄像头和麦克风采集获得，适用于一般的音视频通话。
   * - 从屏幕分享源采集获得，适用于进行屏幕分享。
   * - 从开发者通过 audioSource/videoSource 指定的
   * {@link https://developer.mozilla.org/en-US/docs/Web/API/MediaStreamTrack|MediaStreamTrack} 获得，
   * 使用这种方式业务层可先对音视频进行前处理，比如，对音频进行混音，或者对视频进行美颜处理，亦或者通过这种方式向远端用户推送一个正在播放的音视频文件。
   * <br>
   *
   * @param {object} streamConfig
   * @param {boolean} streamConfig.audio 是否从麦克风采集音频
   * @param {string=} streamConfig.microphoneId 麦克风设备 deviceId，通过 {@link TRTC.getMicrophones getMicrophones()} 获取
   * @param {boolean} streamConfig.video 是否从摄像头采集视频
   * @param {string=} streamConfig.cameraId 摄像头设备 deviceId，通过 {@link TRTC.getCameras getCameras()} 获取
   * @param {string=} streamConfig.facingMode 指定使用前置或后置摄像头来采集视频。<br>在移动设备上，可通过该参数选择使用前置或后置摄像头：
   * - 'user'：前置摄像头
   * - 'environment'：后置摄像头
   *
   * **注意**
   * - 请勿同时使用 cameraId 和 facingMode 参数。
   * @param {boolean=} streamConfig.screen 是否采集屏幕分享流
   * @param {MediaStreamTrack} streamConfig.audioSource 音频源
   * @param {MediaStreamTrack} streamConfig.videoSource 视频源
   * @param {boolean=} streamConfig.mirror 视频显示是否为镜像，默认为 `true`。建议在使用前置摄像头时开启，使用后置摄像头时关闭。
   * <br>
   * 镜像设置不适用于屏幕分享。
   * @throws {RtcError}
   * @returns {Stream} 本地音视频流对象
   * @example
   * // 从麦克风和摄像头采集本地音视频流
   * const localStream = TRTC.createStream({ audio: true, video: true });
   * localStream.initialize().catch(error => {
   *   console.error('failed initialize localStream ' + error);
   * }).then(() => {
   *   console.log('initialize localStream success');
   *   // 本地流初始化成功，可通过Client.publish(localStream)发布本地音视频流
   * });
   *
   * @example
   * // 从屏幕分享源采集
   * // 仅采集屏幕分享视频流
   * const localStream = TRTC.createStream({ audio: false, screen: true });
   *
   * // 采集麦克风及屏幕分享视频流
   * // const localStream = TRTC.createStream({ audio: true, screen: true });
   *
   * localStream.initialize().catch(error => {
   *   console.error('failed initialize localStream ' + error);
   * }).then(() => {
   *   console.log('initialize localStream success');
   *   // 本地流初始化成功，可通过Client.publish(localStream)发布本地音视频流
   * });
   *
   * @example
   * // 从外部App指定的音视频源创建本地音视频流
   * navigator.mediaDevices.getUserMedia({ audio: true, video: true }).then(stream => {
   *   const audioTrack = stream.getAudioTracks()[0];
   *   const videoTrack = stream.getVideoTracks()[0];
   *
   *   // 对audioTrack和videoTrack处理之后，
   *
   *   const localStream = TRTC.createStream({ audioSource: audioTrack, videoSource: videoTrack });
   *   localStream.initialize().catch(error => {
   *     console.error('failed initialize localStream ' + error);
   *   }).then(() => {
   *     console.log('initialize localStream success');
   *     // 本地流初始化成功，可通过Client.publish(localStream)发布本地音视频流
   *   });
   * }
   * );
   */
  createStream(streamConfig) {
    if (
      (typeof streamConfig.audio !== 'undefined' || typeof streamConfig.video !== 'undefined') &&
      (typeof streamConfig.audioSource !== 'undefined' ||
        typeof streamConfig.videoSource !== 'undefined')
    ) {
      throw new RtcError({
        code: ErrorCode.INVALID_PARAMETER,
        message:
          'LocalStream must be created by createStream() with either audio/video or audioSource/videoSource' +
          'but can not be mixed with audio/video and audioSource/videoSource'
      });
    }
    if (
      typeof streamConfig.screen !== 'undefined' &&
      streamConfig.screen === true &&
      streamConfig.video === true
    ) {
      throw new RtcError({
        code: ErrorCode.INVALID_PARAMETER,
        message: 'screen/video options are mutually exclusive, they can not be both true'
      });
    }

    if (
      typeof streamConfig.screen !== 'undefined' &&
      streamConfig.screen === true &&
      !this.isScreenShareSupported()
    ) {
      throw new RtcError({
        code: ErrorCode.INVALID_OPERATION,
        message: 'screen capture is not supported, please use the latest chrome browser'
      });
    }
    return new LocalStream(streamConfig);
  }
};

console.info('******************************************************************************');
console.info('*   欢迎使用 GME Web SDK - 腾讯云游戏多媒体引擎');
// console.info('*   API 文档：' + 'https://trtc-1252463788.file.myqcloud.com/web/docs/index.html');
// console.info('*   版本更新日志：' + 'https://cloud.tencent.com/document/product/647/38958');
// console.info('*   反馈问题：' + 'https://github.com/tencentyun/TRTCSDK/issues');
console.info('******************************************************************************');
log.info('GME Web SDK Version: ' + TRTC.VERSION);
log.info('UserAgent: ' + navigator.userAgent);
log.info('URL of current page: ' + location.href);

export default TRTC;
