import log from '../utils/log/logger';
import isEmpty from '../utils/is-empty';
import EventEmitter from 'eventemitter3';
import { ChannelEvent, SignalEventMap, SignalEvent, ConnectionState } from './signal-event';
import RtcError from '../common/rtc-error';
import ErrorCode from '../common/error-code';
import { logEvent } from '../utils/event-log';
import { setBaseTime, getCurrentTime } from '../utils/time';
import { addEvent, getEvents, EventId } from '../utils/event-hub';

// 重连尝试次数
const RECONNECT_TRY_COUNT = 0;
// 重连间隔事件
const RECONNECT_TIMEOUT = 50;

/**
 *
 *
 * @class SignalChannel
 */
export default class SignalChannel {
  constructor(options) {
    this.sdkAppId_ = options.sdkAppId;
    this.userId_ = options.userId;
    this.userSig_ = options.userSig;
    this.roomId_ = options.roomId_;
    this.url_ = options.url;
    this.backupUrl_ = options.backupUrl;
    this.version_ = options.version;
    // this.urlWithParam_ = `${this.url_}?sdkAppid=${this.sdkAppId_}&identifier=${this.userId_}&userSig=${this.userSig_}`;
    this.urlWithParam_ = `${this.url_}?sdkAppid=${this.sdkAppId_}&roomId=${this.roomId_}&identifier=${this.userId_}&tinyid=${this.userId_}&privMapEncrypt=${this.userSig_}`;
    // this.backupUrlWithParam_ = `${this.backupUrl_}?sdkAppid=${this.sdkAppId_}&identifier=${this.userId_}&userSig=${this.userSig_}`;
    this.backupUrlWithParam_ = `${this.backupUrl_}?sdkAppid=${this.sdkAppId_}&roomId=${this.roomId_}&identifier=${this.userId_}&tinyid=${this.userId_}&privMapEncrypt=${this.userSig_}`;
    this.isConnected_ = false;
    this.isConnecting_ = false;
    this.tryCount_ = RECONNECT_TRY_COUNT;
    this.socketInUse_ = null;
    this.socket_ = null;
    this.backupSocket_ = null;
    this.backupTimer_ = -1;
    this.reconnectTimer_ = -1;
    this.signalInfo_ = {};
    this.currentState_ = ConnectionState.DISCONNECTED;
    this.emitter_ = new EventEmitter();
  }

  /**
   * Connect to Signal Server via WebSocket
   *
   * @memberof SignalChannel
   */
  connect() {
    log.info(`connect to url: ${this.urlWithParam_}`);

    this.emitter_.emit(ChannelEvent.CONNECTION_STATE_CHANGED, {
      prevState: this.currentState_,
      state: ConnectionState.CONNECTING
    });
    this.currentState_ = ConnectionState.CONNECTING;

    this.socket_ = new WebSocket(this.urlWithParam_);
    this.bindSocket(this.socket_);
    // try connecting to backupUrl in 1s
    this.backupTimer_ = setTimeout(() => {
      if (this.isConnected_) return;
      log.info('trying to connect to backupUrl');
      this.tryConnectBackup();
    }, 1000);
  }

  /**
   * try to connect to backup domain
   *
   * @private
   * @memberof SignalChannel
   */
  tryConnectBackup() {
    if (this.backupSocket_) return;
    log.debug(`try to connect to url: ${this.backupUrlWithParam_}`);
    this.backupSocket_ = new WebSocket(this.backupUrlWithParam_);
    this.bindSocket(this.backupSocket_);
  }

  /**
   * bind WebSocket callbacks
   *
   * @private
   * @param {WebSocket} socket
   * @memberof SignalChannel
   */
  bindSocket(socket) {
    socket.onopen = this.onopen.bind(this);
    socket.onclose = this.onclose.bind(this);
    socket.onerror = this.onerror.bind(this);
    socket.onmessage = this.onmessage.bind(this);
  }

  unbindSocket(socket) {
    socket.onopen = () => {};
    socket.onclose = () => {};
    socket.onerror = () => {};
    socket.onmessage = () => {};
  }

  unbindAndCloseSocket(type) {
    if (type === 'main') {
      if (this.socket_) {
        this.unbindSocket(this.socket_);
        try {
          this.socket_.close(1000);
        } catch (e) {
          // ignore
        }
        this.socket_ = null;
      }
    } else {
      if (this.backupSocket_) {
        this.unbindSocket(this.backupSocket_);
        try {
          this.backupSocket_.close(1000);
        } catch (e) {
          // ignore
        }
        this.backupSocket_ = null;
      }
    }
  }

  clearBackupTimer() {
    if (this.backupTimer_ !== -1) {
      clearTimeout(this.backupTimer_);
      this.backupTimer_ = -1;
    }
  }
  /**
   * WebSocket.onopen callback
   *
   * @private
   * @param {Event} event
   * @memberof SignalChannel
   */
  onopen(event) {
    if (this.isConnected_) return;

    if (this.isConnecting_) {
      logEvent({ eventType: 'websocketReconnectionState', result: 'success' });
    } else {
      logEvent({ eventType: 'websocketConnectionState', result: 'success' });
    }
    this.isConnected_ = true;
    this.isConnecting_ = false;

    // ensure backup timer is cleared
    this.clearBackupTimer();

    if (event.target === this.socket_) {
      // ensure backup is closed
      this.unbindAndCloseSocket('backup');
      this.socketInUse_ = this.socket_;
    } else {
      // ensure main is closed
      this.unbindAndCloseSocket('main');
      this.socketInUse_ = this.backupSocket_;
    }

    const url = event.target.url;
    log.info(`[${this.userId_}] websocket[${url}] is connected`);

    this.emitter_.emit(ChannelEvent.CONNECTION_STATE_CHANGED, {
      prevState: this.currentState_,
      state: ConnectionState.CONNECTED
    });
    this.currentState_ = ConnectionState.CONNECTED;
  }

  /**
   * WebSocket.onclose callback
   *
   * @private
   * @param {CloseEvent} event
   * @memberof SignalChannel
   */
  onclose(event) {
    const url = event.target.url;
    const isInUse = event.target === this.socketInUse_;
    log.info(
      `[${this.userId_}] websocket[${url} InUse: ${isInUse}] is closed with code: ${event.code}`
    );

    // only handle the close event for the socket in use
    if (event.target === this.socketInUse_) {
      // mark the SignalChannel is disconnected
      this.isConnected_ = false;

      this.addSignalEvent('disconnected');

      // 1000 is considered as normal close
      if (event.wasClean && event.code === 1000) {
        this.emitter_.emit(ChannelEvent.CONNECTION_STATE_CHANGED, {
          prevState: this.currentState_,
          state: ConnectionState.DISCONNECTED
        });
        this.currentState_ = ConnectionState.DISCONNECTED;
      } else {
        log.warn(`[${this.userId_}] onclose code:${event.code} reason:${event.reason}`);
        if (this.isConnecting_ && this.tryCount_ <= 0) {
          this.isConnecting_ = false;
          this.tryCount_ = RECONNECT_TRY_COUNT;
          log.error(`[${this.userId_}] reconnect failed`);
          this.emitter_.emit(ChannelEvent.CONNECTION_STATE_CHANGED, {
            prevState: this.currentState_,
            state: ConnectionState.DISCONNECTED
          });
          this.currentState_ = ConnectionState.DISCONNECTED;

          logEvent({
            eventType: 'websocketReconnectionState',
            result: 'failed'
          });

          // reconnection failure should be considered as Error
          this.emitter_.emit(
            ChannelEvent.ERROR,
            new RtcError({
              code: ErrorCode.SIGNAL_CHANNEL_ERROR,
              message: 'WebSocket reconnection failed'
            })
          );
        } else {
          log.warn('close current websocket and schedule a reconnect timeout');
          this.socketInUse_.onclose = () => {};
          // 4011 indicates that we want reconnect with new WebSocket
          this.socketInUse_.close(4011);

          this.socket_ = this.backupSocket_ = this.socketInUse_ = null;

          this.emitter_.emit(
            ChannelEvent.ERROR,
            new RtcError({
              code: ErrorCode.ICE_TRANSPORT_BROKE,
              message: 'WebSocket reconnection failed'
            })
          );

          // try reconnect
          // this.reconnect(url);
        }
      }
    }
  }

  /**
   * WebSocket.onerror callback
   *
   * @private
   * @param {Event} event
   * @memberof SignalChannel
   */
  onerror(event) {
    const url = event.target.url;
    log.error(`[${this.userId_}] websocket[${url}] error observed`);
    if (!this.isConnected_) {
      if (event.target == this.socket_) {
        // ensure main is closed
        this.unbindAndCloseSocket('main');

        // try backup only when it's not reconnecting.
        if (!this.isConnecting_) {
          // try to connect to backup domain immediately
          log.warn('main socket error observed, try connecting to backup domain');
          this.tryConnectBackup();
        }
      } else {
        // ensure backup is closed
        this.unbindAndCloseSocket('backup');
      }

      if (this.socket_ === null && this.backupSocket_ === null) {
        if (this.isConnecting_) {
          logEvent({
            eventType: 'websocketReconnectionState',
            result: 'failed'
          });
        } else {
          logEvent({ eventType: 'websocketConnectionState', result: 'failed' });
        }
        log.error('SignalChannel setup failed');
        this.emitter_.emit(
          ChannelEvent.ERROR,
          new RtcError({
            code: ErrorCode.SIGNAL_CHANNEL_ERROR,
            message: 'failed to connect to remote server via websocket'
          })
        );
      }
    } else if (event.target === this.socketInUse_) {
      this.isConnected_ = false;

      this.unbindAndCloseSocket('main');
      this.unbindAndCloseSocket('backup');
      this.socketInUse_ = null;

      this.addSignalEvent('disconnected');

      this.emitter_.emit(
        ChannelEvent.ERROR,
        new RtcError({
          code: ErrorCode.SIGNAL_CHANNEL_ERROR,
          message: 'WebSocket error observed'
        })
      );
    }
  }

  /**
   * WebSocket.onmessage callback
   *
   * @private
   * @param {MessageEvent} event the MessageEvent received from WebSocket
   * @memberof SignalChannel
   */
  onmessage(event) {
    if (!this.isConnected_) return; // close was requested.

    const data = JSON.parse(event.data);
    const eventCode = data.cmd;
    const content = data.content;
    const signalEventValues = Object.values(SignalEventMap);
    const signalEventKeys = Object.keys(SignalEventMap);
    const index = signalEventKeys[signalEventValues.indexOf(eventCode)];
    const eventName = SignalEvent[index];
    // filter out the annoying message
    if (eventCode !== SignalEventMap.UPDATE_REMOTE_MUTE_STAT) {
      const url = event.target == this.socket_ ? this.url_ : this.backupUrl_;
      log.debug(`[${this.userId_}] websocket[${url}] received message: ${event.data}`);
      log.info(`[${this.userId_}] Received event: [ ${eventName} ]`);
    }

    switch (eventCode) {
      case SignalEventMap.CHANNEL_SETUP_SUCCESS: {
        this.signalInfo_.relayChanged = false;
        if (this.signalInfo_.relayIp && this.signalInfo_.relayIp.localeCompare(content.relayip) == 0) {
          this.signalInfo_.relayChanged = true;
        }
        this.signalInfo_.relayIp = content.relayip;
        this.signalInfo_.relayInnerIp = content.innerip;
        this.signalInfo_.signalIp = content.signalip;
        this.signalInfo_.localIp = content.localip;
        this.signalInfo_.dataPort = content.dataport;
        this.signalInfo_.stunPort = content.stunport;
        this.signalInfo_.checkSigSeq = content.checkSigSeq;
        this.signalInfo_.socketId = content.socketid;
        this.signalInfo_.tinyId = content.tinyid;
        this.signalInfo_.openId = content.openid;
        this.signalInfo_.roomId = content.roomid;
        this.signalInfo_.stunPortList = content.stunportList;


        if (!content.stunportList || content.stunportList.length <= 0) {
          this.signalInfo_.stunServers = 'stun:' + content.relayip + ':' + content.stunport;
        } else {
          this.signalInfo_.stunServers = [];
          content.stunportList.forEach(port => {
            const server = 'stun:' + content.relayip + ':' + port;
            this.signalInfo_.stunServers.push(server);
          });
        }

        if (content.cgiurl) {
          this.signalInfo_.logCgiUrl = content.cgiurl;
        }

        if (content.svrTime) {
          setBaseTime(content.svrTime);
        }

        log.info(
          `ChannelSetup Success: signalIp:${content.signalip} relayIp:${content.relayip} checkSigSeq:${content.checkSigSeq}`
        );

        const isReconnect = content.rc || 0;
        if (isReconnect === 1) {
          this.emitter_.emit(SignalEvent.REQUEST_REBUILD_SESSION, {
            signalInfo: this.signalInfo_
          });
        } else {
          this.emitter_.emit(eventName, { signalInfo: this.signalInfo_ });
        }

        this.addSignalEvent('connected');

        break;
      }
      case SignalEventMap.CHANNEL_SETUP_FAILED: {
        let errorCode = 'sdkAppId invalid';
        let errorMsg = '';
        if (typeof content.errorCode !== 'undefined') {
          errorCode = content.errorCode;
          errorMsg = content.errorMsg;
        }
        this.emitter_.emit(
          ChannelEvent.ERROR,
          new RtcError({
            code: ErrorCode.SIGNAL_CAHNNEL_SETUP_FAILED,
            message: `SignalChannel setup failure: ('errorCode': ${errorCode}, 'errorMsg': ${errorMsg} })`
          })
        );
        break;
      }
      case SignalEventMap.CHANNEL_RECONNECT_RESULT: {
        if (content.result === -1) {
          log.error(
            'SignalChannel reconnect failed for the user may have left the room from the perspective of remote relay server'
          );

          logEvent({
            eventType: 'websocketReconnectionState',
            result: 'failed'
          });

          this.addSignalEvent('disconnected');

          this.emitter_.emit(
            ChannelEvent.ERROR,
            new RtcError({
              code: ErrorCode.SIGNAL_CAHNNEL_ERROR,
              message: `SignalChannel reconnect failed`
            })
          );
        }
        break;
      }
      default:
        // 0x12 退出
        this.emitter_.emit(eventName, {
          /** @type {MessageEvent.data} */ data
        });
        break;
    }
  }

  addSignalEvent(state) {
    if (state === 'connected') {
      addEvent(this.userId_, {
        eventId: EventId.SIGNAL_CONNECTED,
        eventDesc: 'signal channel is connected',
        timestamp: getCurrentTime(),
        userId: this.userId_,
        tinyId: this.signalInfo_.tinyId
      });
    } else {
      addEvent(this.userId_, {
        eventId: EventId.SIGNAL_DISCONNECTED,
        eventDesc: 'signal channel is disconnected',
        timestamp: getCurrentTime(),
        userId: this.userId_,
        tinyId: this.signalInfo_.tinyId
      });
    }
  }

  /**
   * reconnect to signal server
   *
   * @private
   * @param {string} url wss url to which connect
   * @memberof SignalChannel
   */
  reconnect(url) {
    const timeout = RECONNECT_TIMEOUT;
    this.reconnectTimer_ = setTimeout(() => {
      this.isConnecting_ = true;
      this.tryCount_ -= 1;
      let finalUrl = url;
      // avoid inserting dup extra parameters after reconnect retry.
      // if (!isEmpty(this.signalInfo_) && url.indexOf('&rc=1') === -1) {
      //   finalUrl =
      //     url +
      //     '&iip=' +
      //     this.signalInfo_.relayInnerIp +
      //     '&dp=' +
      //     this.signalInfo_.dataPort +
      //     '&oip=' +
      //     this.signalInfo_.relayIp +
      //     '&sp=' +
      //     this.signalInfo_.stunPort +
      //     '&rc=1';
      // }
      if (url.indexOf('&rc=1') === -1) {
        finalUrl = url + '&rc=1';
      }

      this.emitter_.emit(ChannelEvent.CONNECTION_STATE_CHANGED, {
        prevState: this.currentState_,
        state: ConnectionState.RECONNECTING
      });
      this.currentState_ = ConnectionState.RECONNECTING;

      this.socket_ = new WebSocket(finalUrl);
      this.bindSocket(this.socket_);
    }, timeout);
  }

  isConnected() {
    return this.isConnected_;
  }

  /**
   * Send command to signal server
   *
   * @see {@link signal-event.SignalCommand}
   * @param {string} command the signal command sent to signal server
   * @param {object} data the data of the command
   * @memberof SignalChannel
   */
  send(command, data = '', target = undefined) {
    if (this.isConnected_) {
      const sendMessage = this.createSendMessage(command);
      sendMessage.data = data;
      if (target !== undefined) {
        sendMessage.srctinyid = target;
      }
      this.socketInUse_.send(JSON.stringify(sendMessage));
    }
  }

  // ugly message format requested by backend, sigh!
  sendWithReport(command, data = '', report) {
    if (this.isConnected_) {
      const sendMessage = this.createSendMessage(command);
      sendMessage.data = data;
      sendMessage.report = report;
      this.socketInUse_.send(JSON.stringify(sendMessage));
    }
  }

  /**
   * create send message header
   *
   * @private
   * @param {string} command
   * @returns
   * @memberof SignalChannel
   */
  createSendMessage(command) {
    return {
      tag_key: command,
      data: '',
      openid: this.userId_,
      tinyid: this.signalInfo_.tinyId,
      version: this.version_,
      ua: navigator.userAgent
    };
  }

  /**
   * close the SignalChannel
   *
   * @memberof SignalChannel
   */
  close() {
    log.info('close SignalChannel');
    if (this.reconnectTimer_ !== -1) {
      clearTimeout(this.reconnectTimer_);
      this.reconnectTimer_ = -1;
    }

    this.clearBackupTimer();

    this.isConnected_ = false;
    this.socketInUse_ = null;

    this.unbindAndCloseSocket('main');
    this.unbindAndCloseSocket('backup');
  }

  /**
   * add event listeners onto SignalChannel
   *
   * @param {String} event
   * @param {function} handler
   * @param {*} context
   * @memberof SignalChannel
   */
  on(event, handler, context) {
    this.emitter_.on(event, handler, context);
  }

  removeListener(event, handler, context) {
    this.emitter_.removeListener(event, handler, context);
  }

  /**
   * add event listeners onto SignalChannel, the handler will be called once and then unregistered.
   *
   * @param {string} event
   * @param {function} handler
   * @memberof SignalChannel
   */
  once(event, handler) {
    this.emitter_.once(event, handler);
  }
}
