import * as env from '../utils/environment';
import * as screenCaptureExt from '../third_party/Screen-Capturing';
import log from '../../src/utils/log/logger';

export const isWebRTCSupported = function() {
  let isSupported = false;
  ['RTCPeerConnection', 'webkitRTCPeerConnection', 'RTCIceGatherer'].forEach(api => {
    if (api in window) {
      isSupported = true;
    }
  });
  return isSupported;
};

export const isH264Supported = async function() {
  const pc = new RTCPeerConnection();
  let offer = '';
  let result = false;
  try {
    offer = await pc.createOffer({
      offerToReceiveAudio: 1,
      offerToReceiveVideo: 1
    });
    if (offer.sdp.toLowerCase().indexOf('h264') !== -1) {
      result = true;
    }
  } catch (e) {
    // ignore
  }
  pc.close();
  return result;
};

export const isUnifiedPlanDefault = function() {
  // old browser doesn't support this interface and it's in plan-b
  if (typeof RTCRtpTransceiver === 'undefined') {
    return false;
  }
  // Safari supports addTransceiver() but not Unified Plan when
  // currentDirection is not defined.
  // eslint-disable-next-line no-undef
  if (!('currentDirection' in RTCRtpTransceiver.prototype)) return false;

  // If Unified Plan is supported, addTransceiver() should not throw.
  const peer = new RTCPeerConnection();
  let canAddTransceiver = false;
  try {
    peer.addTransceiver('audio');
    canAddTransceiver = true;
  } catch (e) {
    // ignore
  }
  peer.close();
  return canAddTransceiver;
};

export const isScreenCaptureApiAvailable = function() {
  let isSupported = false;
  if (navigator.mediaDevices && navigator.mediaDevices.getDisplayMedia) {
    isSupported = true;
  }
  return isSupported;
};

export const checkScreenCaptureExtensionAvailable = function(extensionId, callback) {
  screenCaptureExt.getChromeExtensionStatus(extensionId, result => {
    if (result === 'installed-enabled') {
      screenCaptureExt.isChromeExtensionAvailable(available => {
        callback(available);
      });
    } else {
      callback(false, { reason: 'Chrome ScrenCaptureExtension is not installed/enabled' });
    }
  });
};

let isSystemReqChecked = false;
let cachedSystemReqCheckResult = true;

export const checkSystemRequirementsInternal = async function() {
  if (isSystemReqChecked) return cachedSystemReqCheckResult;

  // check blacklists
  let result = true;
    // !env.IS_FIREFOX &&
    // !env.IS_MQQB &&
    // !env.IS_UCBROWSER &&
    // !env.IS_MIUIBROWSER &&
    // !env.IS_HUAWEIBROWSER;

  if (result) {
    // check WebRTC Api
    const isRTCSupported = isWebRTCSupported();
    let isAVCSupported = true;
    // if (isRTCSupported) {
    //   // check H264 encoding capability
    //   isAVCSupported = await isH264Supported();
    // }
    result = isRTCSupported && isAVCSupported;
    if (!result) {
      log.error(
        `${navigator.userAgent} isWebRTCSupported: ${isRTCSupported} isH264Supported: ${isAVCSupported}`
      );
    }
  } else {
    log.error(`blacklist: ${navigator.userAgent}`);
  }
  isSystemReqChecked = true;
  cachedSystemReqCheckResult = result;
  return result;
};

export const isTRTCSupported = function() {
  return isSystemReqChecked ? cachedSystemReqCheckResult : true;
};

// invoke this during page load to ensure system requirements is checked.
checkSystemRequirementsInternal();
