import EventEmitter from 'eventemitter3';
import { PlayerEvent } from './event';
import RtcError from '../common/rtc-error';
import ErrorCode from '../common/error-code';

class VideoPlayer {
  constructor(options) {
    // Stream this player belong to
    this.stream_ = options.stream;
    this.userId_ = options.stream.getUserId();
    // share the same IDLogger with stream
    this.log_ = this.stream_.getIDLogger();
    // video track to be played
    this.track_ = options.track;
    // the div container for appending <video> element into
    this.div_ = options.div;
    this.muted_ = options.muted;
    this.objectFit_ = options.objectFit;
    this.mirror_ = options.mirror;

    this.element_ = null;
    this.emitter_ = new EventEmitter();
    this.state_ = 'NONE';
  }

  async play() {
    const mediaStream = new MediaStream();
    mediaStream.addTrack(this.track_);
    const element = document.createElement('video');
    element.srcObject = mediaStream;
    // set to true to ensure video autoplay is always successful
    element.muted = true;

    let rotateY = 0;
    if (this.mirror_) {
      rotateY = '180deg';
    }
    element.setAttribute('id', `video_${this.stream_.getId()}`);
    element.setAttribute(
      'style',
      `width: 100%; height: 100%; position: absolute; transform: rotateY(${rotateY}); object-fit: ${this.objectFit_}`
    );
    element.setAttribute('autoplay', 'autoplay');
    element.setAttribute('playsinline', 'playsinline');
    this.div_.appendChild(element);
    this.element_ = element;
    this.handleEvents();

    try {
      await element.play();
    } catch (e) {
      this.log_.warn('<video> play() error: ' + e);
      const errorMessage = e.toString() + ' <video>';
      if (errorMessage.startsWith('NotAllowedError')) {
        throw new RtcError({ code: ErrorCode.PLAY_NOT_ALLOWED, message: errorMessage });
      }
    }
  }

  handleEvents() {
    this.element_.addEventListener('playing', event => {
      this.log_.info('stream - video player is starting playing');
      this.state_ = 'PLAYING';
      this.emitter_.emit(PlayerEvent.PLAYER_STATE_CHANGED, {
        state: this.state_,
        reason: 'playing'
      });
    });
    this.element_.addEventListener('ended', event => {
      this.log_.info('stream - video player is ended');
      if (this.state_ !== 'STOPPED') {
        this.state_ = 'STOPPED';
        this.emitter_.emit(PlayerEvent.PLAYER_STATE_CHANGED, {
          state: this.state_,
          reason: 'ended'
        });
      }
    });
    this.element_.addEventListener('pause', event => {
      this.log_.info('stream - video player is paused');
      this.state_ = 'PAUSED';
      this.emitter_.emit(PlayerEvent.PLAYER_STATE_CHANGED, { state: this.state_, reason: 'pause' });
    });

    // listen for track's ended event
    this.track_.addEventListener('ended', event => {
      this.log_.info('stream - video player track is ended');
      if (this.state_ !== 'STOPPED') {
        this.state_ = 'STOPPED';
        this.emitter_.emit(PlayerEvent.PLAYER_STATE_CHANGED, {
          state: this.state_,
          reason: 'ended'
        });
      }
    });

    this.track_.addEventListener('mute', event => {
      this.log_.info('stream - video track is muted');
      if (this.state_ !== 'PAUSED') {
        this.state_ = 'PAUSED';
        this.emitter_.emit(PlayerEvent.PLAYER_STATE_CHANGED, {
          state: this.state_,
          reason: 'mute'
        });
      }
    });
    this.track_.addEventListener('unmute', event => {
      this.log_.info('stream - video track is unmuted');
      // reset the state altered by mute
      if (this.state_ === 'PAUSED') {
        this.state_ = 'PLAYING';
        this.emitter_.emit(PlayerEvent.PLAYER_STATE_CHANGED, {
          state: this.state_,
          reason: 'unmute'
        });
      }
    });
  }

  stop() {
    this.div_.removeChild(this.element_);
    this.element_.srcObject = null;
  }

  async resume() {
    try {
      await this.element_.play();
    } catch (e) {
      this.log_.warn('<video> play() error: ' + e);
      const errorMessage = e.toString() + ' <video>';
      if (errorMessage.startsWith('NotAllowedError')) {
        throw new RtcError({ code: ErrorCode.PLAY_NOT_ALLOWED, message: errorMessage });
      }
    }
  }

  getVideoFrame() {
    const canvas = document.createElement('canvas');
    canvas.width = this.element_.videoWidth;
    canvas.height = this.element_.videoHeight;
    const context = canvas.getContext('2d');
    context.drawImage(this.element_, 0, 0);
    return canvas.toDataURL('image/png');
  }

  on(event, handler) {
    this.emitter_.on(event, handler);
  }
}

export default VideoPlayer;
