import ErrorCode from './error-code';

const getErrorName = function(code) {
  if (code === ErrorCode.INVALID_PARAMETER) return 'INVALID_PARAMETER';
  else if (code === ErrorCode.INVALID_OPERATION) return 'INVALID_OPERATION';
  else if (code === ErrorCode.CLIENT_BANNED) return 'CLIENT_BANNED';
  else if (code === ErrorCode.CREATE_OFFER_FAILED) return 'CREATE_OFFER_FAILED';
  else if (code === ErrorCode.JOIN_ROOM_FAILED) return 'JOIN_ROOM_FAILED';
  else if (code === ErrorCode.ICE_TRANSPORT_ERROR) return 'ICE_TRANSPORT_ERROR';
  else if (code === ErrorCode.PLAY_NOT_ALLOWED) return 'PLAY_NOT_ALLOWED';
  else if (code === ErrorCode.NOT_SUPPORTED) return 'NOT_SUPPORTED';
  else if (code === ErrorCode.SIGNAL_CAHNNEL_SETUP_FAILED) return 'SIGNAL_CHANNEL_SETUP_FAILED';
  else if (code === ErrorCode.SIGNAL_CHANNEL_ERROR) return 'SIGNAL_CAHNNEL_ERROR';
  else if (code === ErrorCode.SERVER_TIMEOUT) return 'SERVER_TIMEOUT';
  else if (code === ErrorCode.SUBSCRIPTION_TIMEOUT) return 'SUBSCRIPTION_TIMEOUT';
  return 'UNKNOWN';
};

/**
 * RtcError 错误对象
 *
 * @extends Error
 * @class RtcError
 */
class RtcError extends Error {
  constructor({ message, code = ErrorCode.UNKOWN, extraCode = 0 }) {
    super(
      message +
        ` <${getErrorName(code)} 0x${code.toString(
          16
        )}> https://trtc-1252463788.file.myqcloud.com/web/docs/module-ErrorCode.html`
    );
    this.code_ = code;
    // extra code for internal use only
    this.extraCode_ = extraCode;
    this.name = 'RtcError';
    this.message_ = message;
  }

  /**
   * 获取错误码
   * <br>
   * 详细错误码列表参见 {@link module:ErrorCode ErrorCode}。
   *
   * @memberof RtcError
   */
  getCode() {
    return this.code_;
  }

  // private
  getExtraCode() {
    return this.extraCode_;
  }
}
export default RtcError;
